import React from 'react';
import autobind from 'autobind-decorator';
import CircularProgress from '@material-ui/core/CircularProgress';
import RichEditorEditor from '../controls/RichEditorEditor';
import DOMPurify from 'dompurify';
import { Activity } from '../../ppp';
import moment from 'moment';

export default class ActivityCommentsArea extends React.PureComponent<ActivityCommentsAreaProps> {
  private waitForDebounce = 1500;
  @autobind
  handleCommentsChange(rawText: string) {
    if (!this.props.allowActions && !this.props.expanded) {
      return;
    }
    this.props.onCommentsChange(rawText, true);
  }

  render() {
    const { classes, isUpdatingComments, allowActions, comments, expanded, activity } = this.props;
    return (
      <div className={classes.parentOverlay}>
        <div>
          <span className="MuiTypography-root MuiTypography-caption">Activity Comments/Notes</span>
          {isUpdatingComments && expanded && <CircularProgress variant="indeterminate" disableShrink className={classes.overlay} size={24} thickness={4} />}
          {allowActions && expanded && (
            <RichEditorEditor
              rawText={comments}
              onChange={this.handleCommentsChange}
              placeholder={allowActions ? 'Enter any comments or notes associated with the activity' : 'No comments/notes entered'}
              waitForDebounce={this.waitForDebounce}
            />
          )}
          {!allowActions && expanded && (
            <div style={{ width: '100%', border: '1px solid #ddd', padding: '5px', minHeight: '45px' }} dangerouslySetInnerHTML={{ __html: DOMPurify.sanitize(comments) }} />
          )}
        </div>
        {activity.ActivityNotRequiredInfoSet && (
          <div className={classes.parentOverlay} style={{ marginTop: '10px' }}>
            <span className="MuiTypography-root MuiTypography-caption">Activity Not Required Notes</span>
            <div style={{ width: '100%', border: '1px solid #ddd', padding: '5px', minHeight: '45px' }}>
              <ul style={{ marginLeft: '-12px' }}>
                {activity.notRequiredSet && (
                  <li style={{ marginBottom: '6px' }}>
                    (No Comments)
                    <i>
                      by {activity.notRequiredSet.byUser && activity.notRequiredSet.byUser.displayName} on {moment(activity.notRequiredSet.onDate).format('lll')}
                    </i>
                  </li>
                )}
                {activity.ActivityNotRequiredInfoSet.map((elem, ii) => (
                  <li key={ii} style={{ marginBottom: '6px' }}>
                    {elem.comments}{' '}
                    <i>
                      by {elem.byUser && elem.byUser.displayName} on {moment(elem.onDate).format('lll')}
                    </i>
                  </li>
                ))}
              </ul>
            </div>
          </div>
        )}
      </div>
    );
  }
}

interface ActivityCommentsAreaProps {
  classes: {
    progressSpinner: string;
    activityCommentsInput: string;
    activityCommentsTextField: string;
    overlay: string;
    parentOverlay: string;
  };
  allowActions: boolean;
  isUpdatingComments: boolean;
  comments: string;
  expanded: boolean;
  activity: Activity;
  onCommentsChange: (newComment: string, now?: boolean) => void;
}
