import axios, { AxiosRequestConfig, AxiosInstance, AxiosResponse } from 'axios';

export class BaristaService {
  private _webFullUrl: string | undefined;
  private _relativePath: string | undefined;
  private _axios: AxiosInstance;
  private _isProd = process.env.NODE_ENV === 'production';

  constructor(webFullUrl?: string, relativePath?: string) {
    if (webFullUrl == null) {
      webFullUrl = window.location.href;
    }

    this._webFullUrl = webFullUrl;

    if (relativePath && !relativePath.endsWith('/')) {
      relativePath = `${relativePath}/`;
    }

    this._relativePath = relativePath;

    this._axios = axios.create({
      baseURL: `${this._webFullUrl}_vti_bin/barista/v1/barista.svc/eval`
    });
  }

  get webFullUrl(): string | undefined {
    return this._webFullUrl;
  }

  public getRelativeScriptUrl(scriptOrFilePath: string) {
    if (this._relativePath) {
      return `${this._relativePath}${scriptOrFilePath}`;
    } else {
      return scriptOrFilePath;
    }
  }

  get(scriptOrFilePath: string, config?: AxiosRequestConfig): Promise<AxiosResponse> {
    config = {
      ...config,
      method: 'GET',
      params: {
        ...(config && config.params),
        c: this.getRelativeScriptUrl(scriptOrFilePath),
        t: this._isProd
          ? /* istanbul ignore next */
            new Date().getTime()
          : undefined
      }
    };

    return this._axios.request(config);
  }

  post(scriptOrFilePath: string, config?: AxiosRequestConfig) {
    config = {
      ...config,
      method: 'POST',
      params: {
        ...(config && config.params),
        c: this.getRelativeScriptUrl(scriptOrFilePath)
      }
    };

    return this._axios.request(config);
  }

  upload(scriptOrFilePath: string, fileName: string, file: Blob, queryString?: string) {
    var form = new FormData();
    form.append('files[0]', file, fileName);

    return fetch(`${this._webFullUrl}_vti_bin/barista/v1/barista.svc/eval?c=${this.getRelativeScriptUrl(scriptOrFilePath)}`, {
      headers: {
        'Content-Type': 'multipart/form-data; boundary=----------------------------800859741242946661928004'
      },
      method: 'POST',
      body: form
    });
  }

  put(scriptOrFilePath: string, config?: AxiosRequestConfig) {
    config = {
      ...config,
      method: 'POST',
      params: {
        ...(config && config.params),
        c: this.getRelativeScriptUrl(scriptOrFilePath)
      },
      headers: {
        ...(config && config.headers),
        'X-HTTP-Method': 'PUT'
      }
    };
    return this._axios.request(config);
  }

  delete(scriptOrFilePath: string, config?: AxiosRequestConfig) {
    config = {
      ...config,
      method: 'POST',
      params: {
        ...(config && config.params),
        c: this.getRelativeScriptUrl(scriptOrFilePath)
      },
      headers: {
        ...(config && config.headers),
        'X-HTTP-Method': 'DELETE'
      }
    };
    return this._axios.request(config);
  }

  request(config: AxiosRequestConfig) {
    return this._axios.request(config);
  }
}
