import { ProcessTemplate, ProcessTemplateResponse } from './ProcessTemplate';
import { ProcessTemplateVersion } from './ProcessTemplateVersion';
import { BaristaService } from '../util/BaristaService';
import axios, { CancelTokenSource } from 'axios';
import pick from 'lodash/pick';
import { Attachment } from './Attachment';
/**
 * Represents a service that interacts with Process Templates.
 */
export class ProcessTemplateStore {
  private _baristaService: BaristaService;
  private _partyList: { value: string; text: string }[] | undefined = undefined;

  /**
   * Creates a new ProcessTemplateService
   */
  constructor(baristaService: BaristaService) {
    this._baristaService = baristaService;
  }

  /** * Get list of Users.   */
  public async getPartiesList(): Promise<{ value: string; text: string }[]> {
    if (this._partyList && this._partyList.length > 0) {
      return this._partyList || [];
    } else {
      const response = await this._baristaService.get('ProcessTemplate/getAllResponsibleParties.js');
      if (response && response.data) {
        this._partyList = response.data;
        return response.data;
      }
    }
    return [];
  }

  /**
   * Deletes the specified Process Template. Returns false if the Process Template cannot be deleted.
   * @param processTemplateId The guid of the process template to delete.
   */
  public async deleteProcessTemplateById(processTemplateId: string): Promise<boolean> {
    const response = await this._baristaService.delete('ProcessTemplate/deleteProcessTemplateById.js', { data: { processTemplateId } });
    return response.data;
  }

  /**
   * Deletes the specified Process Template Container at the specified path. Returns false if the container cannot be deleted.
   * @param path The guid of the process template to delete.
   */
  public async deleteProcessTemplateContainer(path: string): Promise<boolean> {
    const response = await this._baristaService.put('ProcessTemplate/deleteProcessTemplateContainer.js', { data: { path } });
    return response.data;
  }

  /**
   * Gets a process template by the specified id.
   * @param processTemplateId The guid of the process template to retrieve.
   */
  public async getProcessTemplateById(processTemplateId: string, version?: string): Promise<ProcessTemplateResponse> {
    const response = await this._baristaService.get('ProcessTemplate/getProcessTemplateById.js', { params: { processTemplateId, version } });
    return response.data;
  }

  /**
   * Gets a process template by the specified id.
   * @param processTemplateId The guid of the process template to retrieve.
   */
  public async getProcessTemplateVersionHistoryById(processTemplateId: string): Promise<ProcessTemplateVersion[]> {
    const response = await this._baristaService.get('ProcessTemplate/getProcessTemplateVersionHistoryById.js', { params: { processTemplateId } });
    return response.data;
  }

  /**
   * Gets the path that the specified process template is contained in.
   * @param processTemplateId The guid of the process template's container name to retrieve.
   */
  public async getProcessTemplateContainerById(processTemplateId: string): Promise<string> {
    const response = await this._baristaService.get('ProcessTemplate/getProcessTemplateContainerById.js', { params: { processTemplateId } });
    return response.data;
  }

  /**
   * Creates or updates a Process Template. If the Process Template's id property is empty, a new Process Template will be created.
   * @param path The path of the container that will contain the Process Template.
   * @param processTemplate The Process Template object that will be stored.
   */
  public async saveProcessTemplateAsDraft(path: string, processTemplate: ProcessTemplate): Promise<ProcessTemplateResponse> {
    const processTemplateRequest = pick(processTemplate, ['id', 'title', 'description', 'fields', 'activities', 'jobAidAttachments']);
    const response = await this._baristaService.put('ProcessTemplate/saveProcessTemplateAsDraft.js', {
      data: {
        path,
        processTemplate: processTemplateRequest
      }
    });
    return response.data;
  }

  public async publishProcessTemplateById(processTemplateId: string, versionComment?: string): Promise<ProcessTemplateResponse> {
    const response = await this._baristaService.post('ProcessTemplate/publishProcessTemplateById.js', {
      data: {
        processTemplateId,
        versionComment
      }
    });
    return response.data;
  }

  /**
   * Creates a Process Template Container with the specified path.
   * @param path The name of the container. Segments are seperated by '/'
   */
  public async putProcessTemplateContainer(path: string) {
    const response = await this._baristaService.put('ProcessTemplate/putProcessTemplateContainer.js', {
      data: {
        path
      }
    });
    return response.data;
  }

  /**
   * Returns a string array of all Process Template Containers currently defined.
   */
  public async listAllProcessTemplateContainers(): Promise<string[]> {
    const response = await this._baristaService.get('ProcessTemplate/listAllProcessTemplateContainers.js');
    return response.data;
  }

  /**
   * Returns an hash array of all process templates.
   */
  public async listAllProcessTemplates(path?: string, cancelTokenCallback?: (canceler: CancelTokenSource) => void): Promise<{ [path: string]: ProcessTemplate[] }> {
    const source = axios.CancelToken.source();
    const response = this._baristaService.get('ProcessTemplate/listAllProcessTemplates.js', {
      params: {
        path
      },
      cancelToken: source.token
    });

    if (cancelTokenCallback) {
      cancelTokenCallback(source);
    }

    return (await response).data;
  }

  /**
   * Lists all process templates that are contained on the specified path.
   * @param path
   */
  public async listProcessTemplatesInContainer(path: string): Promise<ProcessTemplate[]> {
    const response = await this._baristaService.get('ProcessTemplate/listProcessTemplatesInContainer.js', { params: { path } });
    return response.data;
  }

  /**
   * Moves the process template with the specified id to the specified path
   * @param path The name of the container. Segments are seperated by '/'
   */
  public async moveProcessTemplate(processTemplateId: string, path: string): Promise<ProcessTemplate> {
    const response = await this._baristaService.post('ProcessTemplate/moveProcessTemplate.js', {
      data: {
        processTemplateId,
        path
      }
    });
    return response.data;
  }

  /**
   * Upload a process activity attachment
   * @param processId the id of the process
   * @param activityId the id of the activity
   * @param fileName the filename of the attachment
   * @param file the file blob
   */
  public async uploadProcessTemplateAttachment(processId: string, fileName: string, file: Blob, activityId?: string): Promise<Attachment> {
    var fd = new FormData();
    fd.append('files[0]', file, fileName);
    var params = {};
    if (activityId && activityId.length > 0) {
      params = { processId, fileName, activityId };
    } else {
      params = { processId, fileName };
    }

    const response = await this._baristaService.post('ProcessTemplate/uploadProcessTemplateAttachment.js', {
      headers: {
        'Content-Type': 'multipart/form-data'
      },
      params: params,
      data: fd
    });
    return response.data;
  }

  /**
   * Remove the specified process template activity attachment.
   * @param processId the id of the process
   * @param activityId the id of the activity
   * @param fileName the filename of the attachment to remove.
   */
  public async removeProcessActivityAttachment(processId: string, fileName: string, activityId?: string): Promise<{ id: string; fileName: string }> {
    if (activityId && activityId.length > 0) {
      const response = await this._baristaService.delete('ProcessTemplate/removeProcessTemplateAttachment.js', { data: { processId, fileName, activityId } });
      return response.data;
    } else {
      const response = await this._baristaService.delete('ProcessTemplate/removeProcessTemplateAttachment.js', { data: { processId, fileName } });
      return response.data;
    }
  }

  /**
   * Returns an hash array of all process templates structure .
   */
  public async getProcessTemplateTreeStructure(path?: string, cancelTokenCallback?: (canceler: CancelTokenSource) => void): Promise<{ [path: string]: ProcessTemplate[] }> {
    const source = axios.CancelToken.source();
    const response = this._baristaService.get('ProcessTemplate/getProcessTemplateTreeStructure.js', {
      params: {
        path
      },
      cancelToken: source.token
    });

    if (cancelTokenCallback) {
      cancelTokenCallback(source);
    }

    return (await response).data;
  }

  public async listProcessTemplatesForPackaging(path: string): Promise<any[]> {
    const response = await this._baristaService.get('ProcessTemplate/getProcessTemplateTreeStructure.js', { params: path });
    return response.data;
  }
}
