import React from 'react';
import { withStyles, WithStyles } from '@material-ui/core/styles';
import autobind from 'autobind-decorator';
import CircularProgress from '@material-ui/core/CircularProgress';
import TextField from '@material-ui/core/TextField';
import Dialog from '@material-ui/core/Dialog';
import DialogTitle from '@material-ui/core/DialogTitle';
import DialogContent from '@material-ui/core/DialogContent';
import DialogActions from '@material-ui/core/DialogActions';
import Button from '@material-ui/core/Button';

import { styles } from './styles';
import { AppContext } from '../../AppContext';
import { ProcessTemplateContainer } from '../../ppp/ProcessTemplateContainer';
import Grid from '@material-ui/core/Grid';

class CreateProcessTemplateContainerDialog extends React.Component<CreateProcessTemplateContainerDialogProps, CreateProcessTemplateContainerDialogState> {
  static contextType = AppContext;
  context!: React.ContextType<typeof AppContext>;

  constructor(props: CreateProcessTemplateContainerDialogProps) {
    super(props);

    this.state = {
      isCreating: false,
      processTemplateContainer: props.processTemplateContainer,
      newContainerName: ''
    };
  }

  @autobind
  handleOnClose() {
    if (this.props.onClose) {
      this.props.onClose(undefined);
    }
  }

  @autobind
  handleNewContainerNameChange(event: React.ChangeEvent<HTMLInputElement>) {
    this.setState({
      newContainerName: event.currentTarget.value
    });
  }

  @autobind
  async createProcessTemplateContainer() {
    const { onClose } = this.props;
    const { processTemplateContainer, newContainerName } = this.state;
    const { processTemplateStore } = this.context;

    if (!processTemplateContainer.path) {
      if (onClose) {
        onClose(undefined);
      }
      return;
    }

    this.setState({
      isCreating: true
    });

    let newPath;
    if (processTemplateContainer.depth === 0) {
      newPath = await processTemplateStore.putProcessTemplateContainer(`/${newContainerName}`);
    } else {
      newPath = await processTemplateStore.putProcessTemplateContainer(`${processTemplateContainer.path}/${newContainerName}`);
    }

    this.setState({
      isCreating: false
    });

    if (onClose) {
      onClose(newPath);
    }
  }

  render() {
    const { classes } = this.props;
    const { isCreating } = this.state;

    return (
      <Dialog open={this.props.open} onClose={this.handleOnClose} aria-labelledby="create-process-template-container-dialog-title" fullWidth={true} maxWidth="xs">
        <DialogTitle id="create-process-template-container-dialog-title">Create Subfolder</DialogTitle>
        <DialogContent>
          <TextField label="New Folder Name" style={{ width: 300 }} value={this.state.newContainerName} onChange={this.handleNewContainerNameChange} margin="normal" />
        </DialogContent>
        <DialogActions>
          {isCreating && (
            <Grid container style={{ margin: '15px' }}>
              <CircularProgress variant="indeterminate" disableShrink className={classes.facebook2} size={24} thickness={4} />
            </Grid>
          )}
          {!isCreating && (
            <>
              <Button onClick={this.handleOnClose} color="primary">
                Cancel
              </Button>
              <Button onClick={this.createProcessTemplateContainer} color="primary">
                Create
              </Button>
            </>
          )}
        </DialogActions>
      </Dialog>
    );
  }
}

export default withStyles(styles)(CreateProcessTemplateContainerDialog);

interface CreateProcessTemplateContainerDialogProps extends WithStyles<typeof styles> {
  open: boolean;
  onClose?: (newPath: string | undefined) => void;
  processTemplateContainer: ProcessTemplateContainer;
}

interface CreateProcessTemplateContainerDialogState {
  isCreating: boolean;
  processTemplateContainer: ProcessTemplateContainer;
  newContainerName: string;
}
