export enum ActivityStatus {
  /**
   * Indicates that the activity has not been completed.
   */
  Incomplete = 'Incomplete',
  /**
   * Indicates that the activity has been marked as completed.
   */
  Complete = 'Complete',
  /**
   * Indicates that the activity has been marked as completed, but requires secondary review.
   */
  CompleteRequiresReview = 'CompleteRequiresReview',
  /**
   * Indicates that the activity has been marked as not required.
   */
  NotRequired = 'NotRequired'
}
