import React from 'react';
import { observer } from 'mobx-react';
import autobind from 'autobind-decorator';
import cloneDeep from 'lodash/cloneDeep';
import find from 'lodash/find';
import { RouteComponentProps, withRouter } from 'react-router-dom';
import queryString from 'query-string';
import { DragDropContext, DragUpdate, Droppable, DroppableProvided, DroppableStateSnapshot } from 'react-beautiful-dnd';
import { withStyles, WithStyles } from '@material-ui/core/styles';
import cn from 'classnames';
import Paper from '@material-ui/core/Paper';
import Typography from '@material-ui/core/Typography';
import CircularProgress from '@material-ui/core/CircularProgress';
import { ProcessTemplateContainer } from '../../ppp/ProcessTemplateContainer';
import ProcessTemplateContainerNode from './ProcessTemplateContainerNode';
import styles from './style';
import { AppContext } from '../../AppContext';
import { ProcessTemplate, Process } from '../../ppp';
import ProcessTemplateMenu from './ProcessTemplateMenu';
import InitiateProcessDialog from '../dialogs/InitiateProcessDialog';
import CopyProcessTemplateDialog from '../dialogs/CopyProcessTemplateDialog';
import MoveProcessTemplateDialog from '../dialogs/MoveProcessTemplateDialog';
import DeleteProcessTemplateDialog from '../dialogs/DeleteProcessTemplateDialog';
import ProcessTemplateContainerMenu from './ProcessTemplateContainerMenu';
import CreateProcessTemplateDialog from '../dialogs/CreateProcessTemplateDialog';
import DeleteProcessTemplateContainerDialog from '../dialogs/DeleteProcessTemplateContainerDialog';
import CreateProcessTemplateContainerDialog from '../dialogs/CreateProcessTemplateContainerDialog';
import ProcessTemplateVersionHistoryDialog from '../dialogs/ProcessTemplateVersionHistoryDialog';

import Button from '@material-ui/core/Button';
import ExportProcessIcon from '@material-ui/icons/SaveAlt';
import { TemplateExporter } from '../../services/TemplateExporter';
import JSZip from 'jszip';

const guidRegex = new RegExp(/^[0-9a-fA-F]{8}-([0-9a-fA-F]{4}-){3}[0-9a-fA-F]{12}$/);

const defaultProcessTemplateTreeData: Partial<ProcessTemplateTreeState> = {};

export const ProcessTemplateTreeContext = React.createContext(defaultProcessTemplateTreeData);

@observer
export class ProcessTemplateTree extends React.Component<ProcessTemplateTreeProps, ProcessTemplateTreeState> {
  static contextType = AppContext;
  context!: React.ContextType<typeof AppContext>;

  static defaultProps = {
    className: '',
    title: '',
    initialVisibleDepth: 3
  };

  constructor(props: ProcessTemplateTreeProps, context?: React.ContextType<typeof AppContext>) {
    super(props, context);
    this.state = { export: { mode: '', isValid: false }, templatesToExport: [] };
  }

  @autobind
  onDragEnd(result: DragUpdate) {
    if (!result.destination) {
      return;
    }

    const { processTemplateContainerStore } = this.context;

    // If the source folder is the same as the target folder.
    const processTemplateId = result.draggableId.replace(/pt-/, '');
    let sourceContainer: ProcessTemplateContainer | undefined;
    let processTemplate: ProcessTemplate | undefined;
    for (const container of processTemplateContainerStore.flatContainers) {
      processTemplate = find(container.processTemplates, { id: processTemplateId });
      if (processTemplate) {
        sourceContainer = container;
        break;
      }
    }

    const destinationContainerIndex = result.destination.droppableId.replace(/^r?ptc-/, '');
    const destinationContainer = processTemplateContainerStore.flatContainers[parseInt(destinationContainerIndex, 10)];

    if (!sourceContainer || !destinationContainer || !processTemplate) {
      return;
    }

    if (sourceContainer === destinationContainer) {
      return;
    }

    this.setState({
      currentCommand: {
        kind: CommandKind.MoveProcessTemplate,
        sourceContainer,
        container: destinationContainer,
        processTemplate: processTemplate,
        menuAnchor: undefined
      }
    });
  }

  @autobind
  async listAllProcessTemplates() {
    const { processTemplateContainerStore } = this.context;
    processTemplateContainerStore.initialVisibleDepth = this.props.initialVisibleDepth;
    const loader = processTemplateContainerStore.listAllProcessTemplates('', c => {
      this.setState({ cancelLoading: c.cancel });
    });

    return await loader;
  }

  @autobind
  beginCopyProcessTemplate(container: ProcessTemplateContainer, processTemplateToCopy: ProcessTemplate) {
    const processTemplate = cloneDeep(processTemplateToCopy);
    this.setState({
      currentCommand: {
        kind: CommandKind.CopyProcessTemplate,
        container: container,
        processTemplate
      }
    });
  }

  @autobind
  beginCreateProcessTemplate(container: ProcessTemplateContainer) {
    this.setState({
      currentCommand: {
        kind: CommandKind.CreateProcessTemplate,
        container,
        depth: -1
      }
    });
  }

  @autobind
  beginCreateProcessTemplateContainer(container: ProcessTemplateContainer) {
    this.setState({
      currentCommand: {
        kind: CommandKind.CreateContainer,
        container,
        depth: -1
      }
    });
  }

  @autobind
  beginDeleteProcessTemplate(container: ProcessTemplateContainer, processTemplateToDelete: ProcessTemplate) {
    this.setState({
      currentCommand: {
        kind: CommandKind.DeleteProcessTemplate,
        container: container,
        processTemplate: processTemplateToDelete
      }
    });
  }

  @autobind
  beginDeleteProcessTemplateContainer(container: ProcessTemplateContainer) {
    this.setState({
      currentCommand: {
        kind: CommandKind.DeleteContainer,
        container,
        depth: -1
      }
    });
  }

  @autobind
  beginInitiateProcess(container: ProcessTemplateContainer, processTemplateToInitiate: ProcessTemplate) {
    const processTemplate = cloneDeep(processTemplateToInitiate);
    this.setState({
      currentCommand: {
        kind: CommandKind.InitiateProcess,
        container: container,
        processTemplate
      }
    });
  }

  @autobind
  beginShowVersionHistory(container: ProcessTemplateContainer, processTemplateToInitiate: ProcessTemplate) {
    const processTemplate = cloneDeep(processTemplateToInitiate);
    this.setState({
      currentCommand: {
        kind: CommandKind.ShowProcessTemplateVersionHistory,
        container: container,
        processTemplate
      }
    });
  }

  @autobind
  clearCurrentCommand() {
    this.setState({
      currentCommand: undefined
    });
  }

  @autobind
  async handleCopyProcessTemplateClosed(processTemplate?: ProcessTemplate) {
    this.clearCurrentCommand();
    if (processTemplate && processTemplate.id) {
      this.navigateToProcessTemplate(processTemplate.id);
    }
  }

  @autobind
  async handleMoveProcessTemplateClosed(processTemplate?: ProcessTemplate) {
    if (processTemplate) {
      this.selectProcessTemplate(processTemplate.id);
    } else {
      this.selectProcessTemplate();
    }

    const { processTemplateContainerStore } = this.context;
    const { currentCommand } = this.state;
    this.clearCurrentCommand();
    if (currentCommand && processTemplate && processTemplate.id) {
      await Promise.all([
        processTemplateContainerStore.listProcessTemplatesInContainer(currentCommand.container),
        processTemplateContainerStore.listProcessTemplatesInContainer(currentCommand.sourceContainer!)
      ]);
    }
  }

  @autobind
  async handleProcessTemplateCreated(processTemplate?: ProcessTemplate) {
    this.clearCurrentCommand();
    if (processTemplate && processTemplate.id) {
      this.navigateToProcessTemplate(processTemplate.id);
    }
  }

  @autobind
  async handleProcessTemplateContainerCreated() {
    const { processTemplateContainerStore } = this.context;
    this.clearCurrentCommand();
    await processTemplateContainerStore.listAllProcessTemplates();
  }

  @autobind
  async handleProcessTemplateDeleted(wasDeleted: boolean) {
    const { processTemplateContainerStore } = this.context;
    const { currentCommand } = this.state;
    this.clearCurrentCommand();
    if (wasDeleted) {
      await processTemplateContainerStore.listProcessTemplatesInContainer(currentCommand!.container);
    }
  }

  @autobind
  async handleProcessTemplateContainerDeleted(wasDeleted: boolean) {
    this.clearCurrentCommand();
    if (wasDeleted) {
      await this.listAllProcessTemplates();
    }
  }

  @autobind
  async handleProcessInitiated(newProcess: Process | undefined) {
    this.clearCurrentCommand();
    if (newProcess) {
      this.selectProcessTemplate(newProcess.template.id);
      this.props.history.push(`/process/${newProcess.id}`);
    }
  }

  @autobind
  selectProcessTemplate(processTemplateId?: string) {
    const values = queryString.parse(this.props.location.search);
    if (values.pt !== processTemplateId) {
      values.pt = processTemplateId;
      this.props.history.push(`${this.props.location.pathname}?${queryString.stringify(values)}`);
    }

    this.context.processTemplateContainerStore.selectProcessTemplate(processTemplateId);
  }

  @autobind
  handleOnSelectProcessTemplateForExport(processTemplateId?: string, version?: string) {
    if (processTemplateId) {
      var index = this.state.templatesToExport.findIndex(x => x.id === processTemplateId);
      if (index >= 0) {
        if (version && version.length > 0) {
          // update existing
          this.setState(state => {
            state.templatesToExport[index] = { id: processTemplateId || '', version: version || '' };
            return { templatesToExport: [...state.templatesToExport], export: { isValid: true, mode: state.export.mode } };
          });
        } else {
          const templatesToExportClone = [...this.state.templatesToExport];
          templatesToExportClone.splice(index, 1);
          this.setState(state => {
            return { templatesToExport: templatesToExportClone, export: { isValid: templatesToExportClone.length > 0, mode: state.export.mode } };
          });
        }
      } else {
        // add new
        this.setState(state => {
          return { templatesToExport: [...state.templatesToExport, { id: processTemplateId || '', version: version || '' }], export: { isValid: true, mode: state.export.mode } };
        });
      }
    }
  }

  @autobind
  navigateToProcessTemplate(processTemplateId: string) {
    if (!guidRegex.test(processTemplateId)) {
      return;
    }

    this.selectProcessTemplate(processTemplateId);
    this.props.history.push(`/template/${processTemplateId}`);
  }

  @autobind
  showProcessTemplateMenu(event: React.MouseEvent<HTMLElement>, container: ProcessTemplateContainer, processTemplate: ProcessTemplate) {
    this.setState({
      currentCommand: {
        kind: CommandKind.ShowProcessTemplateMenu,
        container: container,
        processTemplate,
        menuAnchor: event.currentTarget
      }
    });
  }

  @autobind
  showProcessTemplateContainerMenu(event: React.MouseEvent<HTMLElement>, container: ProcessTemplateContainer) {
    event.stopPropagation();

    this.setState({
      currentCommand: {
        kind: CommandKind.ShowProcessTemplateContainerMenu,
        container: container,
        menuAnchor: event.currentTarget
      }
    });
  }

  async componentWillMount() {
    const values = queryString.parse(this.props.location.search);
    if (values.pt && guidRegex.test(values.pt.toString())) {
      this.context.processTemplateContainerStore.selectProcessTemplate(values.pt.toString());
      await this.listAllProcessTemplates();
      if (values && values.start) {
        // lookup container and processTemplate.
        let sourceContainer: ProcessTemplateContainer | undefined;
        let processTemplate: ProcessTemplate | undefined;
        for (const container of this.context.processTemplateContainerStore.flatContainers) {
          processTemplate = find(container.processTemplates, { id: values.pt.toString() });
          if (processTemplate) {
            sourceContainer = container;
            break;
          }
        }
        if (sourceContainer && processTemplate) {
          const id = values.start.toString();
          const name = (values.name && values.name.toString()) || '';
          this.setState({
            currentCommand: {
              kind: CommandKind.InitiateProcess,
              container: sourceContainer,
              processTemplate: processTemplate
            },
            relatedRejectedProcessInstance: { id: id, name: name }
          });
          // remove "start" querystring that triggers popup
          this.props.history.replace(`${this.props.location.pathname}`);
        }
      }
    } else {
      return this.listAllProcessTemplates();
    }
  }

  async componentDidMount() {
    let scrollToProcessTemplate = () => {
      const { processTemplateContainerStore } = this.context;
      const { selectedProcessTemplateId } = processTemplateContainerStore;
      if (!selectedProcessTemplateId) {
        return;
      }

      const ele = document.getElementById(`process-template-${selectedProcessTemplateId}`);
      if (ele) {
        const clientRects = ele.getClientRects();
        /* istanbul ignore next */
        const top = clientRects[0] ? clientRects[0].top : 0;

        /* istanbul ignore next */
        if (top > window.scrollY + window.innerHeight || top < window.scrollY) {
          window.scroll({
            top: top,
            behavior: 'smooth'
          });
        }
      } else {
        setTimeout(() => {
          scrollToProcessTemplate();
        }, 500);
      }
    };

    scrollToProcessTemplate();
  }

  componentWillUnmount() {
    if (this.state.cancelLoading) {
      this.state.cancelLoading('Operation canceled by user');
    }
    this.context.processTemplateContainerStore.selectProcessTemplate();
  }

  @autobind
  async getSharePointOrigin() {
    /* istanbul ignore next */
    if (process.env.NODE_ENV !== 'production') {
      return 'http://localhost:3001';
    }

    const ctx = (await this.context.spContext.getContextInfo()) as any;
    const a = document.createElement('a');
    a.setAttribute('href', ctx.WebFullUrl);

    const { hostname, port, protocol } = a;
    const origin = `${protocol}//${hostname}${port.length ? `:${port}` : ''}`;
    return origin;
  }

  @autobind
  async handleCompleteExport() {
    this.setState({ export: { mode: 'Active', isValid: false } }); // to disable complete button while creating zip file
    const processTemplates = await this.context.processTemplateStore.listAllProcessTemplates();
    const keys = this.state.templatesToExport.map(i => i.id);
    let selected: { [path: string]: ProcessTemplate[] } = {};
    for (const functionProcess of Object.keys(processTemplates)) {
      const included = processTemplates[functionProcess].filter(i => keys.indexOf(i.id || '') >= 0);
      selected[functionProcess] = included;
    }
    let bigZip = new JSZip();
    const indexHtml = TemplateExporter.exportIndex(selected);
    bigZip.file(`index.html`, indexHtml);
    for (const toExport of this.state.templatesToExport) {
      const template = await this.context.processTemplateStore.getProcessTemplateById(toExport.id, toExport.version);
      if (template.title) {
        const tempHtml = TemplateExporter.exportTemplate(template);
        const filename = `${template.title.toLocaleLowerCase().replace(/[/\\?%*:|"<>]/g, '-')}-${template.id}`;
        bigZip.file(`templates/${filename}.html`, tempHtml);
      }
    }
    const result = await bigZip.generateAsync({ type: 'blob' });
    saveAs(result, 'processTemps-export.zip');
    this.setState({ export: { mode: '', isValid: false } });
  }

  @autobind
  async handleBeginExport() {
    this.setState({ export: { mode: 'Active', isValid: this.state.templatesToExport && this.state.templatesToExport.length > 0 } });
  }

  customKebabCase(str: string) {
    return str.toLowerCase();
  }

  render() {
    const { className, initialVisibleDepth, classes, title } = this.props;

    const { currentCommand } = this.state;
    const { processTemplateContainerStore } = this.context;

    return (
      <div>
        <div style={{ float: 'right' }}>
          {this.state.export.mode === '' && (
            <Button variant="contained" color="primary" title="Begin Export Process Templates" className={classes.exportButton} onClick={this.handleBeginExport}>
              <ExportProcessIcon style={{ fontSize: '24px', marginRight: '8px' }} />
              SELECT TEMPLATES FOR EXPORT
            </Button>
          )}
          {this.state.export.mode === 'Active' && (
            <>
              <Button variant="contained" color="primary" title="Cancel Export Process" className={classes.exportButton} onClick={() => this.setState({ export: { mode: '', isValid: false } })}>
                <ExportProcessIcon style={{ fontSize: '24px', marginRight: '8px' }} />
                Cancel
              </Button>
              <Button variant="contained" color="primary" title="Export Process Templates" className={classes.exportButton} disabled={!this.state.export.isValid} onClick={this.handleCompleteExport}>
                <ExportProcessIcon style={{ fontSize: '24px', marginRight: '8px' }} />
                Complete Export
              </Button>
            </>
          )}
        </div>

        <Paper className={cn(classes.paper, className)}>
          {title && (
            <Typography variant="caption" className={classes.caption}>
              {title}
            </Typography>
          )}
          {processTemplateContainerStore.isLoading && <CircularProgress className={classes.progress} size={30} thickness={5} />}
          {!processTemplateContainerStore.isLoading && (
            <>
              <ProcessTemplateTreeContext.Provider value={this.state}>
                <DragDropContext onDragEnd={this.onDragEnd}>
                  <Droppable droppableId="rptc-0" isDropDisabled={true}>
                    {(provided: DroppableProvided, snapshot: DroppableStateSnapshot) => (
                      <div ref={provided.innerRef}>
                        <ProcessTemplateContainerNode
                          initialVisibleDepth={initialVisibleDepth}
                          container={processTemplateContainerStore.flatContainers[0]}
                          showProcessTemplateMenu={this.showProcessTemplateMenu}
                          showProcessTemplateContainerMenu={this.showProcessTemplateContainerMenu}
                          navigateToProcessTemplate={this.navigateToProcessTemplate}
                          initiateProcess={this.beginInitiateProcess}
                          selectedProcessTemplateId={processTemplateContainerStore.selectedProcessTemplateId}
                          selectProcessTemplate={this.selectProcessTemplate}
                          onSelectProcessTemplateForExport={this.handleOnSelectProcessTemplateForExport}
                          toggleContainerShowChildren={processTemplateContainerStore.toggleContainerShowChildren}
                          exportMode={this.state.export.mode}
                          templatesToExport={this.state.templatesToExport}
                        />
                        {provided.placeholder}
                      </div>
                    )}
                  </Droppable>
                </DragDropContext>
              </ProcessTemplateTreeContext.Provider>
              {currentCommand &&
                (() => {
                  switch (currentCommand.kind) {
                    case CommandKind.CopyProcessTemplate:
                      return (
                        <CopyProcessTemplateDialog
                          open={true}
                          onClose={this.handleCopyProcessTemplateClosed}
                          processTemplateContainer={currentCommand.container}
                          processTemplateId={currentCommand.processTemplate!.id!}
                        />
                      );
                    case CommandKind.CreateContainer:
                      return <CreateProcessTemplateContainerDialog open={true} onClose={this.handleProcessTemplateContainerCreated} processTemplateContainer={currentCommand.container} />;
                    case CommandKind.CreateProcessTemplate:
                      return <CreateProcessTemplateDialog open={true} onClose={this.handleProcessTemplateCreated} processTemplateContainer={currentCommand.container} />;
                    case CommandKind.DeleteContainer:
                      return <DeleteProcessTemplateContainerDialog open={true} onClose={this.handleProcessTemplateContainerDeleted} processTemplateContainer={currentCommand.container} />;
                    case CommandKind.DeleteProcessTemplate:
                      return (
                        <DeleteProcessTemplateDialog
                          open={true}
                          onClose={this.handleProcessTemplateDeleted}
                          processTemplateContainer={currentCommand.container}
                          processTemplate={currentCommand.processTemplate!}
                        />
                      );
                    case CommandKind.InitiateProcess:
                      return (
                        <InitiateProcessDialog
                          open={true}
                          onClose={this.handleProcessInitiated}
                          path={currentCommand.container.path}
                          processTemplate={currentCommand.processTemplate!}
                          relatedRejectedProcessInstance={this.state.relatedRejectedProcessInstance}
                        />
                      );
                    case CommandKind.MoveProcessTemplate:
                      return (
                        <MoveProcessTemplateDialog
                          open={true}
                          onClose={this.handleMoveProcessTemplateClosed}
                          processTemplateContainer={currentCommand.container}
                          processTemplate={currentCommand.processTemplate!}
                        />
                      );
                    case CommandKind.ShowProcessTemplateContainerMenu:
                      return (
                        <ProcessTemplateContainerMenu
                          createProcessTemplate={this.beginCreateProcessTemplate}
                          createProcessTemplateContainer={this.beginCreateProcessTemplateContainer}
                          deleteProcessTemplateContainer={this.beginDeleteProcessTemplateContainer}
                          hideProcessTemplateContainerMenu={this.clearCurrentCommand}
                          container={currentCommand.container}
                          menuAnchor={currentCommand.menuAnchor}
                        />
                      );
                    case CommandKind.ShowProcessTemplateMenu:
                      return (
                        <ProcessTemplateMenu
                          initiateProcess={this.beginInitiateProcess}
                          showVersionHistory={this.beginShowVersionHistory}
                          copyProcessTemplate={this.beginCopyProcessTemplate}
                          deleteProcessTemplate={this.beginDeleteProcessTemplate}
                          navigateToProcessTemplate={this.navigateToProcessTemplate}
                          hideProcessTemplateMenu={this.clearCurrentCommand}
                          container={currentCommand.container}
                          menuAnchor={currentCommand.menuAnchor}
                          processTemplate={currentCommand.processTemplate!}
                        />
                      );
                    case CommandKind.ShowProcessTemplateVersionHistory:
                      return <ProcessTemplateVersionHistoryDialog open={true} onClose={this.clearCurrentCommand} processTemplate={currentCommand.processTemplate!} />;
                    default:
                      return <div>Unknown Command</div>;
                  }
                })()}
            </>
          )}
        </Paper>
      </div>
    );
  }
}

export default withRouter(withStyles(styles, { withTheme: true })(ProcessTemplateTree));

interface ProcessTemplateTreeProps extends WithStyles<typeof styles>, RouteComponentProps {
  className?: string;
  title?: string;
  initialVisibleDepth: number;
}

interface ProcessTemplateTreeState {
  cancelLoading?: (message?: string) => void;
  export: {
    mode: string;
    isValid: boolean;
  };
  currentCommand?: {
    kind: CommandKind;
    container: ProcessTemplateContainer;
    sourceContainer?: ProcessTemplateContainer;
    processTemplate?: ProcessTemplate;
    menuAnchor?: HTMLElement;
  };
  relatedRejectedProcessInstance?: { id: string; name: string };
  templatesToExport: Array<{ id: string; version: string }>;
}

export enum CommandKind {
  ShowProcessTemplateMenu = 'ShowProcessTemplateMenu',
  ShowProcessTemplateContainerMenu = 'ShowProcessTemplateContainerMenu',
  CreateContainer = 'CreateContainer',
  DeleteContainer = 'DeleteContainer',
  CreateProcessTemplate = 'CreateProcessTemplate',
  CopyProcessTemplate = 'CopyProcessTemplate',
  DeleteProcessTemplate = 'DeleteProcessTemplate',
  MoveProcessTemplate = 'MoveProcessTemplate',
  ShowProcessTemplateVersionHistory = 'ShowProcessTemplateVersionHistory',
  InitiateProcess = 'InitiateProcess'
}
