import React from 'react';
import { withStyles } from '@material-ui/core/styles';
import autobind from 'autobind-decorator';
import moment from 'moment';
import CircularProgress from '@material-ui/core/CircularProgress';
import Dialog from '@material-ui/core/Dialog';
import DialogTitle from '@material-ui/core/DialogTitle';
import DialogContent from '@material-ui/core/DialogContent';
import DialogActions from '@material-ui/core/DialogActions';
import Button from '@material-ui/core/Button';

import { AppContext } from '../../AppContext';
import { ProcessTemplate, ProcessTemplateVersion } from '../../ppp';
import List from '@material-ui/core/List';
import ListItem from '@material-ui/core/ListItem';
import ListItemText from '@material-ui/core/ListItemText';

const styles = {};

class ProcessTemplateVersionHistory extends React.Component<ProcessTemplateVersionHistoryProps, ProcessTemplateVersionHistoryState> {
  static contextType = AppContext;
  context!: React.ContextType<typeof AppContext>;

  constructor(props: ProcessTemplateVersionHistoryProps) {
    super(props);

    this.state = {
      isLoading: true,
      versionHistory: []
    };
  }

  @autobind
  handleClose(e: React.MouseEvent<HTMLElement>) {
    if (!this.props.onClose) {
      return;
    }

    this.props.onClose();
  }

  @autobind
  async getProcessTemplateVersionHistory() {
    const { processTemplate } = this.props;
    if (!processTemplate.id) {
      return;
    }

    this.setState({
      isLoading: true
    });

    const { processTemplateStore } = this.context;
    const versionHistory = await processTemplateStore.getProcessTemplateVersionHistoryById(processTemplate.id);
    this.setState({
      isLoading: false,
      versionHistory
    });
  }

  componentWillMount() {
    this.getProcessTemplateVersionHistory();
  }

  renderVersion(version: ProcessTemplateVersion): JSX.Element {
    return (
      <span>
        {version.version} {moment(version.createdOn).format('MM/DD/YY h:mm a')} {version.createdBy.displayName}
      </span>
    );
  }

  render() {
    const { open, processTemplate } = this.props;
    const { isLoading, versionHistory } = this.state;

    return (
      <Dialog open={open} onClose={this.handleClose} aria-labelledby="initiate-process-dialog-title" fullWidth={true} maxWidth="sm">
        <DialogTitle id="initiate-process-dialog-title">
          Version History for <i>{processTemplate.title}</i>
        </DialogTitle>
        <DialogContent>
          {isLoading && <CircularProgress size={20} thickness={5} />}
          {!isLoading && (
            <List dense style={{ paddingLeft: 32 }}>
              {versionHistory.map((version, ix) => (
                <ListItem key={ix}>
                  <ListItemText>
                    <>
                      {version.isCurrentVersion && <b>{this.renderVersion(version)}</b>}
                      {!version.isCurrentVersion && this.renderVersion(version)}
                    </>
                  </ListItemText>
                </ListItem>
              ))}
            </List>
          )}
        </DialogContent>
        <DialogActions>
          <Button onClick={this.handleClose} color="primary">
            OK
          </Button>
        </DialogActions>
      </Dialog>
    );
  }
}

export default withStyles(styles)(ProcessTemplateVersionHistory);

interface ProcessTemplateVersionHistoryProps {
  open: boolean;
  onClose?: () => void;
  processTemplate: ProcessTemplate;
}

interface ProcessTemplateVersionHistoryState {
  isLoading: boolean;
  versionHistory: ProcessTemplateVersion[];
}
