import React from 'react';
import { renderToStaticMarkup } from 'react-dom/server';
import moment from 'moment';
import DOMPurify from 'dompurify';

export class TemplateExporter {
  private static style = ``;

  private static printStyle = `
      body {zoom: 82%;
        orientation:landscape;
    `;

  // Insert path into directory tree structure:
  static insert(children: any[] = [], s: string[], templates: any[], fullPath?: string) {
    const [head, ...tail] = s;
    const fullPathKey = (fullPath ? fullPath : '') + '/' + head;
    let child = children.find((a: any) => a.text === head);
    const filesKeys = Object.keys(templates);
    const values = Object.keys(templates).map((key: any) => templates[key]);
    let files = [];
    for (let z = 0; z <= filesKeys.length; z++) {
      if (filesKeys[z] === fullPathKey + '/') {
        files = values[z];
        break;
      }
    }
    if (!child) {
      children.push((child = { text: head, children: [], files: files }));
    }
    if (tail.length > 0) {
      this.insert(child.children, tail, templates, fullPathKey);
    }
    return children;
  }

  // tslint:disable-next-line:align
  static renderIndex(templates: any[]) {
    const paths = Object.keys(templates);
    let objectArray = paths
      .map(path => {
        path = path.replace(/\/$/, '');
        return path.split('/').slice(1);
      })
      .reduce((children, path) => {
        return this.insert(children, path, templates);
      });

    // Massage the data further to accomodate JSTree expectations.
    let newObject: any[] = [];
    const buildWeirdTree = (i: any, obj: any[]) => {
      const lastChildren = i.children;
      i.state = {
        opened: true
      };
      if (i.files) {
        let children: any[] = [];
        i.files.forEach((f: any) => {
          const filename = `${f.title.toLowerCase().replace(/[/\\?%*:|"<>]/g, '-')}-${f.id}`;
          let attr = 'templates/' + filename + '.html';
          children.push({
            text: f.title,
            a_attr: attr,
            icon:
              'data:image/png;base64,'
          });
        });
        i.children = [...i.children, ...children];
      }
      obj.push(i);
      if (lastChildren && lastChildren.length > 0) {
        lastChildren.map((ii: any) => {
          buildWeirdTree(ii, lastChildren);
        });
      }
    };

    objectArray.map((i: any) => {
      buildWeirdTree(i, newObject);
    });
    const json = JSON.stringify(newObject);

    var script = `
      $(document).ready(function () {
        $('#jstree_div').jstree({ 'core' : {
          'data' : ${json}
      } })
        .bind("select_node.jstree", function(e, data){
          if(data.node.original.a_attr){
            var href = data.node.original.a_attr;
            window.location.href = href;		
          }
        });
      });`;

    return (
      <html lang="en">
        <head>
          <meta charSet="utf-8" />
          <meta name="viewport" content="width=device-width, initial-scale=1, shrink-to-fit=no" />
          <meta httpEquiv="X-UA-Compatible" content="IE=edge" />
          <link rel="stylesheet" href="https://maxcdn.bootstrapcdn.com/bootstrap/3.3.7/css/bootstrap.min.css" />
          <link rel="stylesheet" href="https://cdnjs.cloudflare.com/ajax/libs/jstree/3.2.1/themes/default/style.min.css" />
          <script src="https://cdnjs.cloudflare.com/ajax/libs/jquery/1.12.1/jquery.min.js" />
          <script src="https://cdnjs.cloudflare.com/ajax/libs/jstree/3.2.1/jstree.min.js" />

          <style type="text/css" dangerouslySetInnerHTML={{ __html: this.style }} />
          <style type="text/css" media="print" dangerouslySetInnerHTML={{ __html: this.printStyle }} />
        </head>
        <body style={{ margin: '10px' }}>
          <div className="row">
            <div className="col-xs-12">
              <h1 className="onlyprint">Process Templates</h1>
            </div>
          </div>
          <p />
          <div id="jstree_div" />

          <script type="text/javascript" dangerouslySetInnerHTML={{ __html: script }} />
        </body>
      </html>
    );
  }

  static renderProcessTemplate(template: any) {
    return (
      <html lang="en">
        <head>
          <meta charSet="utf-8" />
          <meta name="viewport" content="width=device-width, initial-scale=1, shrink-to-fit=no" />
          <meta httpEquiv="X-UA-Compatible" content="IE=edge" />
          <link rel="stylesheet" href="https://maxcdn.bootstrapcdn.com/bootstrap/3.3.7/css/bootstrap.min.css" />
          <style type="text/css" dangerouslySetInnerHTML={{ __html: this.style }} />
          <style type="text/css" media="print" dangerouslySetInnerHTML={{ __html: this.printStyle }} />
        </head>
        <body style={{ margin: '10px' }}>
          <div className="container">
            <div className="row">
              <div className="col-xs-12">
                <h1 className="onlyprint">Template Name: {template.title}</h1>
              </div>
            </div>
            <div className="row">
              <div className="col-xs-12">
                <footer className="onlyprint" />
              </div>
            </div>
            <div className="row">
              <div className="col-xs-12">
                <div className="panel panel-default">
                  <div className="panel-body">
                    <div className="row">
                      <div className="col-xs-6">
                        <div className="form-group">
                          <label className="control-label">path</label>{' '}
                          <label style={{ borderTop: 'none', boxShadow: 'none', borderLeft: 'none', borderRight: 'none', borderRadius: 'unset' }} className="form-control">
                            {template.path}
                          </label>
                        </div>
                      </div>
                      <div className="col-xs-6">
                        <div className="form-group">
                          <label className="control-label">Version </label>{' '}
                          <label style={{ borderTop: 'none', boxShadow: 'none', borderLeft: 'none', borderRight: 'none', borderRadius: 'unset' }} className="form-control">
                            {template.version}
                          </label>
                        </div>
                      </div>
                    </div>
                    <div className="row">
                      <div className="col-xs-6">
                        <div className="form-group">
                          <label className="control-label">Created By</label>{' '}
                          <label style={{ borderTop: 'none', boxShadow: 'none', borderLeft: 'none', borderRight: 'none', borderRadius: 'unset' }} className="form-control">
                            {template.createdBy.displayName}
                          </label>
                        </div>
                      </div>
                      <div className="col-xs-6">
                        <div className="form-group">
                          <label className="control-label">Created Date</label>{' '}
                          <label style={{ borderTop: 'none', boxShadow: 'none', borderLeft: 'none', borderRight: 'none', borderRadius: 'unset' }} className="form-control">
                            {template.created ? moment(template!.created).format('lll') : ''}
                          </label>
                        </div>
                      </div>
                    </div>
                  </div>
                </div>
              </div>
            </div>

            <div className="row">
              <div className="col-xs-12">
                <h3>
                  <b>Activities</b>
                </h3>
              </div>
              {template.activities.map((activity: any, activityIndex: any) => (
                <div key={activityIndex} className="col-xs-12">
                  <div className="panel panel-default">
                    <div className="panel-body">
                      <div className="row">
                        <div className="col-xs-12">
                          <label className="control-label">Description : </label>
                          <div style={{ height: 'auto', border: 'none', boxShadow: 'none', marginBottom: '10px' }} dangerouslySetInnerHTML={{ __html: DOMPurify.sanitize(activity.description) }} />
                        </div>
                        <div className="col-xs-12">
                          <label className="control-label">Responsible Party : </label>
                          {activity.responsibleParty !== '' ? activity.responsibleParty : ' [none]'}
                        </div>
                        <div className="col-xs-3">
                          <label className="control-label">Depends On Previous : </label>
                          {activity.dependsOnPreviousActivity ? ' Yes' : ' No'}
                        </div>
                        <div className="col-xs-3">
                          <label className="control-label">Allow Attachments : </label>
                          {activity.allowAttachments ? ' Yes' : ' No'}
                        </div>
                        <div className="col-xs-3">
                          <label>Require Attachments : </label>
                          {activity.requireAttachments ? ' Yes' : ' No'}
                        </div>
                        <div className="col-xs-3">
                          <label className="control-label">Requires Secondary Review : </label>
                          {activity.requiresReview ? ' Yes' : ' No'}
                        </div>
                      </div>
                      <div className="row">
                        <div style={{ height: 'auto', border: 'none', boxShadow: 'none', marginTop: '10px' }} className="col-xs-12">
                          <label className="control-label">Tooltip : </label>
                          {activity.tooltip ? activity.tooltip : ' [none]'}
                        </div>
                      </div>
                    </div>
                  </div>
                </div>
              ))}
            </div>
          </div>
        </body>
      </html>
    );
  }

  static exportIndex(templates: any) {
    return renderToStaticMarkup(this.renderIndex(templates));
  }

  static exportTemplate(template: any) {
    return renderToStaticMarkup(this.renderProcessTemplate(template));
  }
}
