import React from 'react';
import { renderToStaticMarkup } from 'react-dom/server';
import find from 'lodash/find';
import startCase from 'lodash/startCase';
import delay from '../util/delay';
import JSZip from 'jszip';
import axios from 'axios';
import moment from 'moment';
import DOMPurify from 'dompurify';
import { Process, Activity } from '../ppp';
import { MonitoringAndReviewStatus } from '../ppp/MonitoringAndReviewStatus';
import { NotedException } from '../ppp/NotedException';

export class ProcessExporter {
  private static defaultDelayMs = 1000;
  private static style = `
  .dataTables_scroll div.dataTables_scrollBody>table>tbody>tr>td {
        vertical-align: top !important;
    }
  .dataTables_scroll div.dataTables_scrollBody {
      position: initial !important;
      width: 100% !important;
      height: unset !important;
  }
  #data_info{
    display:none;
  }
    `;

  private static printStyle = `
      body {zoom: 82%;
        orientation:landscape;
    `;
  private static script = `
  $(document).ready(function () {
      window.mytable = $('#data').DataTable({
          'paging': false,
          'lengthMenu': [
              [10, 25, 50, 100, -1],
              [10, 25, 50, 100, 'All']
          ],
          "scrollX": false,
          "responsive": true
      });

      window.mytable.columns().every(function () {
          var that = this;
          $('input', this.footer()).on('keyup change', function () {
              if (that.search() !== this.value) {
                  that.search(this.value).draw();
              }
          });
      });

      window.mytable = $('#dataNotedExceptions').DataTable({
        'paging': false,
        'lengthMenu': [
            [10, 25, 50, 100, -1],
            [10, 25, 50, 100, 'All']
        ],
        "scrollX": false,
        "responsive": true
      });

  });`;

  static renderProcess(process: Process) {
    var pdfFileName = `${process.name.replace(/[^a-z0-9]/gi, '_')}.pdf`;
    var showProcessDownloadLink = process.status === 'CompletedAndSigned' ? { display: 'none' } : { display: 'none' };
    const columns = [
      {
        title: 'Activity #',
        render: (activity: Activity, activityIndex: number) => {
          return activityIndex + 1;
        }
      },
      {
        title: 'Description',
        render: (activity: Activity, activityIndex: number) => {
          const activityTemplate = find(process.template.activities, { id: activity.id });
          if (!activityTemplate) {
            return;
          }
          return <div dangerouslySetInnerHTML={{ __html: DOMPurify.sanitize(activityTemplate.description) }} />;
        }
      },
      {
        title: 'Responsible Party',
        render: (activity: Activity, activityIndex: number) => {
          return <div>{activity.responsibleParty}</div>;
        }
      },
      {
        title: 'Attachments',
        render: (activity: Activity, activityIndex: number) => {
          return (
            <div>
              <ol>
                {activity.attachments.map((attachment, ix) => (
                  <li>
                    <a key={ix} href={`./activity-${activityIndex + 1}/${attachment.originalFileName}`}>
                      {attachment.originalFileName}
                    </a>
                  </li>
                ))}
              </ol>
            </div>
          );
        }
      },
      {
        title: 'Activity Status',
        render: (activity: Activity, activityIndex: number) => {
          return <div>{startCase(activity.status)}</div>;
        }
      },
      {
        title: 'Completed By',
        render: (activity: Activity, activityIndex: number) => {
          return <div>{activity.completedBy && activity.completedBy.displayName}</div>;
        }
      },
      {
        title: 'Completion Date',
        render: (activity: Activity, activityIndex: number) => {
          return <div>{activity.completedOn && moment(activity.completedOn).format('lll')}</div>;
        }
      },
      {
        title: 'Reviewed By',
        render: (activity: Activity, activityIndex: number) => {
          return <div>{activity.reviewedBy && activity.reviewedBy.displayName}</div>;
        }
      },
      {
        title: 'Reviewed Date',
        render: (activity: Activity, activityIndex: number) => {
          return <div>{activity.reviewedOn && moment(activity.reviewedOn).format('lll')}</div>;
        }
      },
      {
        title: 'Notes',
        render: (activity: Activity, activityIndex: number) => {
          return <div dangerouslySetInnerHTML={{ __html: DOMPurify.sanitize(activity.comments) }} />;
        }
      }
    ];

    const mrExceptionsColumns = [
      {
        title: 'Exception Noted',
        render: (ne: NotedException, index: number) => {
          return <div dangerouslySetInnerHTML={{ __html: DOMPurify.sanitize(ne.exceptionNoted || '') }} />;
        }
      },
      {
        title: 'Attachments',
        render: (ne: NotedException, index: number) => {
          return (
            <div>
              <ol>
                {ne.attachments &&
                  ne.attachments.map((attachment, ix) => (
                    <li>
                      <a key={ix} href={`./MonitoringAndReview/${attachment.originalFileName}`}>
                        {attachment.originalFileName}
                      </a>
                    </li>
                  ))}
              </ol>
            </div>
          );
        }
      },
      {
        title: 'Suggested Corrective Action',
        render: (ne: NotedException, index: number) => {
          return <div>{ne.suggestedCorrectiveAction || ''}</div>;
        }
      },
      {
        title: 'Noted By',
        render: (ne: NotedException, index: number) => {
          return <div>{ne.exceptionNotedBy && ne.exceptionNotedBy.displayName}</div>;
        }
      },
      {
        title: 'Noted Date',
        render: (ne: NotedException, index: number) => {
          return <div>{ne.exceptionNotedTimeStamp && moment(ne.exceptionNotedTimeStamp).format('lll')}</div>;
        }
      },
      {
        title: 'Process Owner Response',
        render: (ne: NotedException, index: number) => {
          return <div>{ne.processOwnerResponse || ''}</div>;
        }
      },
      {
        title: 'Process Owner',
        render: (ne: NotedException, index: number) => {
          return <div>{ne.processOwner && ne.processOwner.displayName}</div>;
        }
      },
      {
        title: 'Response Date',
        render: (ne: NotedException, index: number) => {
          return <div>{ne.processOwnerResponseTimeStamp && moment(ne.processOwnerResponseTimeStamp).format('lll')}</div>;
        }
      }
    ];

    const showQ4 = process && process.monitoringAndReview && (process.monitoringAndReview.q1 === 'No' || process.monitoringAndReview.q2 === 'No' || process.monitoringAndReview.q3 === 'No');

    return (
      <html lang="en">
        <head>
          <meta charSet="utf-8" />
          <meta name="viewport" content="width=device-width, initial-scale=1, shrink-to-fit=no" />
          <meta httpEquiv="X-UA-Compatible" content="IE=edge" />
          <link rel="stylesheet" href="https://maxcdn.bootstrapcdn.com/bootstrap/3.3.7/css/bootstrap.min.css" />
          <link rel="stylesheet" href="https://cdn.datatables.net/1.10.15/css/jquery.dataTables.min.css" />
          <link rel="stylesheet" href="https://cdn.datatables.net/colreorder/1.3.3/css/colReorder.dataTables.min.css" />
          <link rel="stylesheet" href="https://cdn.datatables.net/rowgroup/1.0.0/css/rowGroup.dataTables.min.css" />
          <script src="https://code.jquery.com/jquery-1.12.4.js" />
          <script src="https://cdn.datatables.net/1.10.15/js/jquery.dataTables.min.js" />
          <script src="https://cdn.datatables.net/colreorder/1.3.3/js/dataTables.colReorder.min.js" />
          <script src="https://cdn.datatables.net/rowgroup/1.0.0/js/dataTables.rowGroup.min.js" />
          <style type="text/css" dangerouslySetInnerHTML={{ __html: this.style }} />
          <style type="text/css" media="print" dangerouslySetInnerHTML={{ __html: this.printStyle }} />
        </head>
        <body style={{ margin: '10px' }}>
          <div className="col-sm-12">
            <h1>
              {process.template.title} - {process.template.version} - {process.name}
            </h1>
          </div>
          <div className="col-sm-4">
            <h4>{process.template.description}</h4>
            <h6 style={showProcessDownloadLink}>
              <a href={pdfFileName}>Click here to download process pdf</a>
            </h6>
            <h6>Status: {startCase(process.status)}</h6>
            <h6>Fiscal Year: {process.fiscalYear}</h6>
            <h6>Initiated On: {moment(process.initiatedOn).format('lll')}</h6>
            <h6>Initiated By: {process.initiatedBy && process.initiatedBy.displayName}</h6>
          </div>
          <div className="col-sm-4">
            <h4>This process has been completed and signed off</h4>
            <h6>Signed On: {moment(process.signOff!.timeStamp).format('lll')}</h6>
            <h6>Signed By: {process.signOff!.signer.displayName}</h6>
          </div>
          <table id="data" className="display" cellSpacing="0" style={{ width: '100%' }}>
            <thead>
              <tr>
                {columns.map((col, ix) => (
                  <th key={ix}>{col.title}</th>
                ))}
              </tr>
            </thead>
            <tfoot>
              <tr>
                <td colSpan={8}>
                  <b>Notes:</b>
                  {process.notes}
                </td>
              </tr>
            </tfoot>
            <tbody>
              {process.activities.map((activity, activityIndex) => (
                <tr key={activityIndex}>
                  {columns.map((col, ix) => (
                    <td key={ix}>{col.render && col.render(activity, activityIndex)}</td>
                  ))}
                </tr>
              ))}
            </tbody>
          </table>
          {process.monitoringAndReview && (
            <div className="col-sm-12">
              <hr style={{ width: '100%', textAlign: 'left', marginLeft: 0 }} />
              <h1>Monitoring And Review Section</h1>
              <div className="col-sm-12">
                <h5>
                  Status: <b>{startCase(process.monitoringAndReview ? process.monitoringAndReview.status : MonitoringAndReviewStatus.NotStarted)}</b>
                </h5>
                <h5>
                  Signed On: <b>{process.monitoringAndReview.signOff && process.monitoringAndReview.signOff.timeStamp && moment(process.monitoringAndReview.signOff.timeStamp).format('lll')}</b>
                </h5>
                <h5>
                  Signoff By: <b>{process.monitoringAndReview.signOff && process.monitoringAndReview.signOff.signer && process.monitoringAndReview.signOff.signer.displayName}</b>
                </h5>
              </div>
              <p className="col-sm-12">
                <b>{process.monitoringAndReview.q1 || ''}</b> - Verify the activities in the checklist were reviewed by the appropriate personnel(s).
              </p>
              <p className="col-sm-12">
                <b>{process.monitoringAndReview.q2 || 'N/A'}</b> - Verify the checklist is completed in a sufficiently timely manner. And verify each activity in the checklist was completed properly
                by inspecting the attachments/evidences, if available, and as needed.
              </p>
              <p className="col-sm-12">
                <b>{process.monitoringAndReview.q3 || 'N/A'}</b> - Verify the final review signature of the checklist was signed by the appropriate personnel.
              </p>

              {showQ4 === true && (
                <p className="col-sm-12">
                  <b>{process.monitoringAndReview.q4 || 'N/A'}</b> - If the checklist fails any of the first three steps above, see exceptions.
                </p>
              )}

              {process.monitoringAndReview.notedExceptions && (
                <div>
                  <h4>Noted Exceptions</h4>
                  <table id="dataNotedExceptions" className="display" cellSpacing="0" style={{ width: '100%' }}>
                    <thead>
                      <tr>
                        {mrExceptionsColumns.map((col, ix) => (
                          <th key={ix}>{col.title}</th>
                        ))}
                      </tr>
                    </thead>
                    <tbody>
                      {process.monitoringAndReview &&
                        process.monitoringAndReview.notedExceptions &&
                        process.monitoringAndReview.notedExceptions.map((ne, index) => (
                          <tr key={index}>
                            {mrExceptionsColumns.map((col, ix) => (
                              <td key={ix}>{col.render && col.render(ne, index)}</td>
                            ))}
                          </tr>
                        ))}
                    </tbody>
                  </table>
                </div>
              )}
              {process.monitoringAndReview.generalNotes && (
                <div>
                  <h4>General Notes:</h4>
                  <div dangerouslySetInnerHTML={{ __html: DOMPurify.sanitize(process.monitoringAndReview.generalNotes || '') }} />
                </div>
              )}
            </div>
          )}

          <script type="text/javascript" dangerouslySetInnerHTML={{ __html: this.script }} />
        </body>
      </html>
    );
  }

  static async exportProcess(process: Process, processUrl: string, origin: string, statusChangeCallback?: (newStatus: string) => void): Promise<JSZip> {
    const zipFile = new JSZip();
    this.updateStatus('Generating Process Export...', statusChangeCallback);
    await delay(this.defaultDelayMs);

    const html = renderToStaticMarkup(this.renderProcess(process));
    zipFile.file(`index.html`, html);
    await delay(this.defaultDelayMs);

    this.updateStatus('Exporting Activity Attachments...', statusChangeCallback);
    await delay(this.defaultDelayMs);
    for (let i = 0; i < process.activities.length; i++) {
      const activity = process.activities[i];
      if (activity.attachments) {
        for (let j = 0; j < activity.attachments.length; j++) {
          const attachment = activity.attachments[j];
          this.updateStatus(`Retrieving ${attachment.originalFileName}...`, statusChangeCallback);
          let stName = '/' + attachment.serverRelativeUrl.split('/')[1];
          const url = `${origin + stName}/_layouts/15/download.aspx?SourceUrl=${attachment.serverRelativeUrl}`;
          const response = await axios.get(url, { responseType: 'blob' });
          zipFile.file(`activity-${i + 1}/${attachment.originalFileName}`, response.data);
        }
      }
      if (process.monitoringAndReview && process.monitoringAndReview.notedExceptions) {
        for (let ii = 0; ii < process.monitoringAndReview.notedExceptions.length; ii++) {
          const ne = process.monitoringAndReview.notedExceptions[ii];
          if (ne.attachments) {
            for (let j = 0; j < ne.attachments.length; j++) {
              const attachment = ne.attachments[j];
              this.updateStatus(`Retrieving ${attachment.originalFileName}...`, statusChangeCallback);
              let stName = '/' + attachment.serverRelativeUrl.split('/')[1];
              const url = `${origin + stName}/_layouts/15/download.aspx?SourceUrl=${attachment.serverRelativeUrl}`;
              const response = await axios.get(url, { responseType: 'blob' });
              zipFile.file(`MonitoringAndReview/${attachment.originalFileName}`, response.data);
            }
          }
        }
      }
    }
    return zipFile;
  }

  private static updateStatus(newStatus: string, callback?: (newStatus: string) => void) {
    if (callback) {
      callback(newStatus);
    }
  }
}
