import React from 'react';
import Fade from '@material-ui/core/Fade';
import MenuItem from '@material-ui/core/MenuItem';
import Grid from '@material-ui/core/Grid';
import Box from '@material-ui/core/Box';
import { Button, Menu, MenuList, Collapse, ListItemText, Paper, Chip, Tooltip } from '@material-ui/core';
import { WithStyles, Theme, createStyles, withStyles } from '@material-ui/core/styles';
import autobind from 'autobind-decorator';
import Clear from '@material-ui/icons/Clear';
import ExpandLess from '@material-ui/icons/ExpandLess';
import ExpandMore from '@material-ui/icons/ExpandMore';
import CheckCircle from '@material-ui/icons/CheckCircle';
import CheckCircleOutline from '@material-ui/icons/RadioButtonUnchecked';
import FilterList from '@material-ui/icons/FilterList';
import { AppContext } from '../../AppContext';
import { MuiPickersUtilsProvider, DatePicker } from '@material-ui/pickers';
import DateFnsUtils from '@date-io/date-fns';
import SaveCustomFilterDialog from '../dialogs/SaveCustomFilterDialog';
import { observer } from 'mobx-react';
import { FilterObject } from '../../ppp/FilterObject';

const styles = (theme: Theme) =>
  createStyles({
    displayHeader: {
      marginLeft: theme.spacing(1),
      color: theme.palette.primary.dark,
      fontSize: '1.4em'
    }
  });

@observer
class ProcessFilters extends React.Component<ProcessFiltersProps, ProcessFiltersState> {
  static contextType = AppContext;
  context!: React.ContextType<typeof AppContext>;

  constructor(props: ProcessFiltersProps) {
    super(props);

    this.state = {
      anchorEl: undefined,
      collapse: {}
    };
  }

  @autobind
  handleAddChangeFilterClick(event: any) {
    this.setState({ anchorEl: event.currentTarget });
  }

  @autobind
  toggleCollapse(categoryKey: string) {
    const { collapse } = this.state;
    if (collapse[categoryKey]) {
      collapse[categoryKey] = !collapse[categoryKey];
    } else {
      collapse[categoryKey] = true;
    }
    this.setState({ collapse: collapse });
  }

  @autobind
  handleClose() {
    this.setState({ anchorEl: undefined });
  }

  @autobind
  handleAddFilter(data: FilterObject) {
    this.props.onAddFilterFunction(data);
  }

  @autobind
  handleFilterDelete(data: FilterObject) {
    this.props.onRemoveFilterFunction(data);
  }

  @autobind
  handleClearFilters() {
    this.props.onClearFilterFunction();
  }

  @autobind
  handleSaveView(name: string, global: boolean, overwriteViewId?: string): void {
    this.props.onSaveView(name, global, overwriteViewId);
  }

  private renderFilters(filters: FilterObject[], categoryLabel: string): React.ReactElement<any>[] {
    const components: React.ReactElement<any>[] = [];
    filters &&
      filters.map(filterItem => {
        if (filterItem.DateRange) {
          components.push(
            <MenuItem key={filterItem.key}>
              {this.context.personalizationStore.selectedViewFilter.filterSet.filter(x => {
                return x.key === filterItem.key;
              }).length > 0 ? (
                <CheckCircle />
              ) : (
                <CheckCircleOutline />
              )}
              <MuiPickersUtilsProvider utils={DateFnsUtils}>
                <DatePicker
                  label={filterItem.key}
                  value={new Date(filterItem.Date)}
                  format="MM/dd/yyyy"
                  onChange={date => {
                    filterItem.Date = date;
                    this.handleAddFilter({
                      key: filterItem.key,
                      category: filterItem.category,
                      label: categoryLabel + ': ' + filterItem.label + ' - ' + new Date(date).toLocaleDateString('en-US'),
                      DateRange: true,
                      LessThan: filterItem.LessThan,
                      Date: date
                    });
                  }}
                />
              </MuiPickersUtilsProvider>
            </MenuItem>
          );
        } else {
          components.push(
            <MenuItem
              disabled={this.props.isLoading}
              key={filterItem.key}
              onClick={e => {
                this.handleAddFilter({
                  key: filterItem.key,
                  category: filterItem.category,
                  label: categoryLabel + ': ' + filterItem.label,
                  DateRange: filterItem.DateRange,
                  LessThan: filterItem.LessThan,
                  Date: ''
                });
              }}
            >
              {this.context.personalizationStore.selectedViewFilter.filterSet.filter(x => {
                return x.key === filterItem.key;
              }).length > 0 ? (
                <CheckCircle />
              ) : (
                <CheckCircleOutline />
              )}
              <ListItemText inset primary={filterItem.label} />
            </MenuItem>
          );
        }
      });

    return components;
  }

  private renderFilterMenu(): React.ReactElement<any>[] {
    const components: React.ReactElement<any>[] = [];
    const { collapse } = this.state;
    this.context.personalizationStore.filterConfig.categories &&
      this.context.personalizationStore.filterConfig.categories.map(category =>
        components.push(
          <React.Fragment key={category.key}>
            <MenuItem
              onClick={e => {
                this.toggleCollapse(category.key);
              }}
            >
              <ListItemText inset={false} primary={category.name} />
              {collapse[category.key] ? <ExpandLess /> : <ExpandMore />}
            </MenuItem>
            <Collapse in={collapse[category.key]} timeout="auto" unmountOnExit>
              <MenuList test-id={`menu-list-${category.key}`}>
                {this.renderFilters(this.context.personalizationStore.filterConfig.filters.filter(f => f.category === category.key), category.name)}
              </MenuList>
            </Collapse>
          </React.Fragment>
        )
      );
    return components;
  }

  render() {
    const { anchorEl } = this.state;
    const { classes } = this.props;
    const open = Boolean(anchorEl);
    const { filterSet, name, isDirty } = this.context.personalizationStore.selectedViewFilter;

    return (
      <>
        <Grid item xs={12}>
          <Button
            id="add-change-filter-button"
            variant="outlined"
            aria-owns={open ? 'filter-menu' : undefined}
            aria-haspopup="true"
            onClick={this.handleAddChangeFilterClick}
            style={{ padding: 4, margin: 5 }}
          >
            <FilterList /> Add/Change Filter
          </Button>
          <Box className={classes.displayHeader} m={0} p={0} component="span">
            {`Current View:  ${name ? name : 'No View Selected'}${isDirty === true ? ' *' : ''}`}
          </Box>
          <Tooltip title="Clear All Filters">
            <span>
              <Button id="clear-all-filters" variant="outlined" style={{ float: 'right' }} onClick={this.handleClearFilters}>
                <Clear />
              </Button>
            </span>
          </Tooltip>
          <Menu id="filter-menu" anchorEl={anchorEl} open={open} onClose={this.handleClose} TransitionComponent={Fade}>
            <MenuList>
              <div>{this.renderFilterMenu()}</div>
            </MenuList>
          </Menu>
        </Grid>
        <Paper style={{ padding: 2, paddingBottom: 1, marginBottom: 8, flex: 1 }}>
          Filters:
          {filterSet &&
            filterSet.map(data => {
              return (
                <Chip
                  key={data.key}
                  label={data.label}
                  style={{ margin: 1 }}
                  onDelete={e => {
                    this.handleFilterDelete(data);
                  }}
                />
              );
            })}
          <Grid container alignItems="flex-start" justify="flex-end" direction="row" style={{ margin: 0, padding: 0 }}>
            <SaveCustomFilterDialog onSaveView={this.handleSaveView} />
          </Grid>
        </Paper>
      </>
    );
  }
}

export default withStyles(styles)(ProcessFilters);

interface ProcessFiltersProps extends WithStyles<typeof styles> {
  style?: React.CSSProperties;
  onChange?: (fy: string | undefined) => void;
  onAddFilterFunction: (data: FilterObject) => void;
  onRemoveFilterFunction: (data: FilterObject) => void;
  onClearFilterFunction: () => void;
  onSaveView: (name: string, global: boolean, overwriteViewId?: string) => void;
  isLoading: boolean;
}

interface ProcessFiltersState {
  anchorEl?: HTMLElement;
  collapse: CollapseArray;
}

interface CollapseArray {
  [key: string]: boolean;
}
