import React from 'react';
import moment from 'moment';
import Button from '@material-ui/core/Button';
import Card from '@material-ui/core/Card';
import CardHeader from '@material-ui/core/CardHeader';
import CardContent from '@material-ui/core/CardContent';
import CardActions from '@material-ui/core/CardActions';
import ProcessInitiatedIcon from '@material-ui/icons/Assignment';
import ProcessActiveIcon from '@material-ui/icons/AssignmentReturned';
import ProcessCompletedNotSignedIcon from '@material-ui/icons/AssignmentInd';
import ProcessCompletedAndSignedIcon from '@material-ui/icons/AssignmentTurnedIn';
import NotificationImportantOutlinedIcon from '@material-ui/icons/NotificationImportantOutlined';
import { Process, ProcessStatus } from '../../ppp';
import autobind from 'autobind-decorator';
import { Tooltip } from '@material-ui/core';

export default class ProcessStatusCard extends React.Component<ProcessStatusCardProps, { rejectOpen: boolean; anchorEl: any }> {
  constructor(props: any) {
    super(props);
    this.state = { rejectOpen: false, anchorEl: null };
  }

  @autobind
  handleRejectActions(event: React.MouseEvent<HTMLButtonElement, MouseEvent>) {
    this.props.beginReject('1');
    this.setState({ rejectOpen: false, anchorEl: null });
  }

  @autobind
  handleToggle(event: React.MouseEvent<HTMLButtonElement>) {
    const anchorEl = event.currentTarget.parentElement;
    if (anchorEl) {
      this.setState((state, props) => {
        return { rejectOpen: !state.rejectOpen, anchorEl };
      });
    }
  }

  @autobind
  handleRejectClose(event: React.MouseEvent<Document, MouseEvent>) {
    this.setState({ rejectOpen: false, anchorEl: null });
  }

  @autobind
  handleGoToRejectedProcess(id: string) {
    if (this.props.onNavigateToRejectedProcess) {
      this.props.onNavigateToRejectedProcess(id);
    }
  }

  getAssociatedRejectedInfo(process: Process) {
    return (
      process.relatedRejectedProcessInstance && (
        <>
          <div>Associated rejected process:</div>
          <div style={{ marginTop: 10 }}>
            <Button
              size="small"
              onClick={() => {
                if (process.relatedRejectedProcessInstance) {
                  this.handleGoToRejectedProcess(process.relatedRejectedProcessInstance.id);
                }
              }}
            >
              <NotificationImportantOutlinedIcon />
              <span style={{ fontWeight: 800 }}>{` ${process.relatedRejectedProcessInstance.name}`} </span>
            </Button>
          </div>
        </>
      )
    );
  }

  render() {
    const { classes, process, beginSignOff, beginUnsign } = this.props;

    return (
      <Card>
        <CardHeader title="Process Status:" />
        {(() => {
          switch (process.status) {
            case ProcessStatus.Initiated:
              return (
                <>
                  <CardContent>
                    <ProcessInitiatedIcon className={classes.processIcon} />
                    <div>This Process is newly initiated</div>
                    {this.getAssociatedRejectedInfo(process)}
                  </CardContent>
                  <CardActions>
                    <div style={{ marginTop: 5 }}>
                      <Button title="Reject Process Instance" onClick={this.handleRejectActions} color="primary" style={{ flex: 1 }}>
                        Reject (with reason)
                      </Button>
                    </div>
                  </CardActions>
                </>
              );
            case ProcessStatus.Active:
              return (
                <>
                  <CardContent>
                    <ProcessActiveIcon className={classes.processIcon} />
                    <div>This Process is currently active</div>
                    {this.getAssociatedRejectedInfo(process)}
                  </CardContent>
                  <CardActions>
                    <div style={{ marginTop: 5 }}>
                      <Button title="Reject Process Instance" onClick={this.handleRejectActions} color="primary" style={{ flex: 1 }}>
                        Reject (with reason)
                      </Button>
                    </div>
                  </CardActions>
                </>
              );
            case ProcessStatus.CompletedNotSigned:
              return (
                <>
                  <CardContent>
                    <ProcessCompletedNotSignedIcon className={classes.processIcon} />
                    <div>All activities associated with this process have been completed. This process now requires final signoff.</div>
                    <div style={{ paddingTop: '20px' }}>
                      <strong>By clicking the Sign Off button below, I acknowledge that all activities have been reviewed and completed.</strong>
                    </div>
                    {this.getAssociatedRejectedInfo(process)}
                  </CardContent>
                  <CardActions>
                    <Button onClick={beginSignOff} color="primary" style={{ flex: 1 }}>
                      Sign off
                    </Button>
                    <div style={{ marginTop: 5 }}>
                      <Button title="Reject Process Instance" onClick={this.handleRejectActions} color="primary" style={{ flex: 1 }}>
                        Reject (with reason)
                      </Button>
                    </div>
                  </CardActions>
                </>
              );
            case ProcessStatus.CompletedAndSigned:
              return (
                <CardContent>
                  <ProcessCompletedAndSignedIcon className={classes.processIcon} />
                  <div>
                    <strong>This process has been completed and Signed Off:</strong>
                  </div>
                  <div>
                    Signed By: <strong>{process.signOff!.signer.displayName}</strong>
                  </div>
                  <div>
                    Signed On: <strong>{moment(process.signOff!.timeStamp).format('lll')}</strong>
                  </div>
                  <br />
                  <div>
                    <Button onClick={beginUnsign} color="primary" style={{ flex: 1 }}>
                      Unsign Process
                    </Button>
                  </div>
                  {this.getAssociatedRejectedInfo(process)}
                </CardContent>
              );
            case ProcessStatus.Rejected:
              return (
                <CardContent>
                  <Tooltip title={`Reasons: ${process.rejection!.reason}`}>
                    <div>
                      <ProcessCompletedAndSignedIcon className={classes.processIcon} />
                      <div>
                        <strong>This process has been rejected</strong>
                      </div>
                      <div>
                        Rejected By: <strong>{process.rejection!.rejectedBy!.displayName}</strong>
                      </div>
                      <div>
                        Rejected On: <strong>{moment(process.rejection!.timeStamp).format('lll')}</strong>
                      </div>
                    </div>
                  </Tooltip>
                  {this.getAssociatedRejectedInfo(process)}
                </CardContent>
              );
            default:
              return <CardContent>{'Unknown Status ' + process.status}</CardContent>;
          }
        })()}
      </Card>
    );
  }
}

interface ProcessStatusCardProps {
  classes: {
    processIcon: string;
  };
  process: Process;
  beginSignOff: () => void;
  beginUnsign: () => void;
  beginReject: (actionId: string) => void;
  onNavigateToRejectedProcess?: (id: string) => void;
}
