import React from 'react';
import CircularProgress from '@material-ui/core/CircularProgress';
import Button from '@material-ui/core/Button';
import { ActivityStatus, Activity } from '../../ppp';
//import moment from 'moment';
import autobind from 'autobind-decorator';
import { AppContext } from '../../AppContext';
import { Tooltip } from '@material-ui/core';

export default class ActivityActionsArea extends React.PureComponent<ActivityActionsAreaProps, ActivityActionsState> {
  static contextType = AppContext;
  context!: React.ContextType<typeof AppContext>;

  constructor(props: ActivityActionsAreaProps) {
    super(props);

    this.state = {
      notRequiredDialogOpen: false,
      currentUser: ''
    };
  }

  async componentDidMount() {
    const { currentUser } = this.state;

    if (currentUser === '') {
      try {
        const user: any = await this.context.spContext.getCurrentUser();
        if (user && user.Title && user.Title.length > 0) {
          await this.setState({
            currentUser: user.Title
          });
        }
      } catch (err) {}
    }
  }

  @autobind
  getCompletedBy(activity: Activity) {
    if (typeof activity.completedBy !== 'undefined') {
      return activity.completedBy.displayName;
    } else {
      return '';
    }
  }

  render() {
    const { classes, activity, canCompleteActivity, isPerformingAction, allowActions } = this.props;
    const { onMarkAsComplete, onMarkAsIncomplete, onMarkAsNotRequired, onMarkAsReviewed } = this.props;
    const { currentUser } = this.state;

    // const activityMarkedAsNotRequiredby = () => {
    //   let msg = 'Activity has been marked as not required';
    //   if (activity.notRequiredSet) {
    //     if (activity.notRequiredSet.byUser) {
    //       msg = `${msg} by ${activity.notRequiredSet.byUser.displayName} on ${moment(activity.notRequiredSet.onDate).format('lll')}`;
    //     }
    //   }
    //   return msg;
    // };

    if (!allowActions) {
      return null;
    }

    if (isPerformingAction) {
      return <CircularProgress variant="indeterminate" disableShrink className={classes.progressSpinner} size={24} thickness={4} />;
    }

    switch (activity.status) {
      case ActivityStatus.Incomplete:
        return (
          <>
            <Button size="small" color="primary" onClick={onMarkAsNotRequired}>
              Mark activity as Not Required
            </Button>
            <Button size="small" color="primary" onClick={onMarkAsComplete} disabled={!canCompleteActivity}>
              Complete Activity
            </Button>
          </>
        );
      case ActivityStatus.CompleteRequiresReview:
        if (this.getCompletedBy(activity) === currentUser) {
          return (
            <Tooltip title="Activity Reviewer must be different from Activity Completer">
              <span>
                <Button size="small" color="primary" disabled={this.getCompletedBy(activity) === currentUser} onClick={onMarkAsReviewed}>
                  Mark as Reviewed
                </Button>
              </span>
            </Tooltip>
          );
        } else {
          return (
            <Button size="small" color="primary" disabled={this.getCompletedBy(activity) === currentUser} onClick={onMarkAsReviewed}>
              Mark as Reviewed
            </Button>
          );
        }
      case ActivityStatus.Complete:
        return (
          <Button size="small" color="primary" onClick={onMarkAsIncomplete}>
            Mark activity as Incomplete
          </Button>
        );
      case ActivityStatus.NotRequired:
        return (
          <>
            {/* <div style={{ paddingLeft: '8px' }}>
              <span>{activityMarkedAsNotRequiredby()}</span>
            </div> */}
            <Button size="small" color="primary" onClick={onMarkAsIncomplete}>
              Mark activity as Required and Incomplete
            </Button>
          </>
        );
      default:
        return <div>Unknown Status</div>;
    }
  }
}

interface ActivityActionsState {
  notRequiredDialogOpen: boolean;
  currentUser: string;
}

interface ActivityActionsAreaProps {
  classes: {
    progressSpinner: string;
  };
  activity: Activity;
  canCompleteActivity: boolean;
  isPerformingAction: boolean;
  allowActions: boolean;
  onMarkAsComplete: () => void;
  onMarkAsIncomplete: () => void;
  onMarkAsNotRequired: () => void;
  onMarkAsReviewed: () => void;
}
