import React from 'react';
import { computed } from 'mobx';
import { observer } from 'mobx-react';
import orderBy from 'lodash/orderBy';
import autobind from 'autobind-decorator';

import { withStyles, WithStyles } from '@material-ui/core/styles';
import cn from 'classnames';
import Collapse from '@material-ui/core/Collapse';
import CircularProgress from '@material-ui/core/CircularProgress';
import List from '@material-ui/core/List';
import ListItem from '@material-ui/core/ListItem';
import ListItemIcon from '@material-ui/core/ListItemIcon';
import ListItemText from '@material-ui/core/ListItemText';
import MoreVertIcon from '@material-ui/icons/MoreVert';
import FolderIcon from '@material-ui/icons/Folder';
import FolderOpenIcon from '@material-ui/icons/FolderOpen';
import IndicateFolderClosedIcon from '@material-ui/icons/ArrowRight';
import IndicateFolderOpenIcon from '@material-ui/icons/ArrowDropDown';
import Button from '@material-ui/core/Button';
import { Droppable, Draggable, DroppableProvided, DroppableStateSnapshot } from 'react-beautiful-dnd';

import styles from './style';
import ProcessTemplateNode from './ProcessTemplateNode';
import { ProcessTemplateContainer } from '../../ppp/ProcessTemplateContainer';
import { ProcessTemplate } from '../../ppp';

@observer
export class ProcessTemplateContainerNode extends React.Component<ProcessTemplateContainerProps> {
  static defaultProps = {
    className: ''
  };

  @autobind
  getContainerStyle(isDraggingOver: boolean) {
    if (isDraggingOver) {
      return { backgroundColor: 'aliceblue' };
    }
    return {};
  }

  @autobind
  getProcessTemplateStyle(isDragging: boolean, draggableStyle: any) {
    return {
      ...draggableStyle
    };
  }

  @autobind
  toggleContainer() {
    this.props.toggleContainerShowChildren(this.props.container);
  }

  @computed get containerName() {
    const { container } = this.props;
    let containerName = container.name;
    if (containerName === '/') {
      containerName = '';
    }
    return containerName;
  }

  render() {
    const { classes, className, container, templatesToExport } = this.props;

    const getExportedData = (id: string | undefined): string | undefined => {
      if (templatesToExport) {
        const index = templatesToExport.findIndex(i => i.id === id);
        if (index >= 0) {
          return templatesToExport[index].version ? templatesToExport[index].version : undefined;
        }
      }
      return undefined;
    };

    return (
      <>
        <Droppable droppableId={`ptc-${container.index}`}>
          {(ptcProvider: DroppableProvided, ptcSnapshot: DroppableStateSnapshot) => (
            <div ref={ptcProvider.innerRef}>
              <ListItem
                dense
                button
                className={classes.treeNode}
                id={`process-template-container-${container.index}`}
                onClick={this.toggleContainer}
                style={this.getContainerStyle(ptcSnapshot.isDraggingOver)}
              >
                <ListItemIcon className={classes.containerIcon}>
                  {container.showChildren ? (
                    <>
                      <IndicateFolderOpenIcon />
                      <FolderOpenIcon />
                    </>
                  ) : (
                    <>
                      <IndicateFolderClosedIcon />
                      <FolderIcon />
                    </>
                  )}
                </ListItemIcon>
                <ListItemText
                  inset
                  primary={this.containerName}
                  style={{ paddingRight: '5px' }}
                  className={cn(classes.treeText, {
                    [classes.treeTextFlex]: false
                  })}
                />
                <Button className={classes.actionButton} size="small" onClick={e => this.props.showProcessTemplateContainerMenu(e, this.props.container)}>
                  <MoreVertIcon />
                </Button>
              </ListItem>
              <div style={{ height: '0' }}>{ptcProvider.placeholder}</div>
            </div>
          )}
        </Droppable>
        <Collapse in={container.showChildren} unmountOnExit>
          {container.isLoading && <CircularProgress id="loading-container-children" className={classes.progress} size={20} thickness={5} />}
          {!container.isLoading && container.subFolders && container.subFolders.length > 0 && (
            <List dense className={className} style={{ paddingLeft: 24 }}>
              {container.subFolders.map((child, ix) => (
                <Wrapper {...this.props} key={ix} initialVisibleDepth={this.props.initialVisibleDepth} container={child} />
              ))}
            </List>
          )}
          {!container.isLoading && container.processTemplates && container.processTemplates.length > 0 && (
            <List dense className={className} style={{ paddingLeft: 48 }}>
              {' '}
              {/*  Indent 'Files' to look "inside" folders */}
              {orderBy(container.processTemplates, ['title'], ['asc']).map((processTemplate, ix) => (
                <Draggable key={ix} draggableId={`pt-${processTemplate.id!}`} index={ix}>
                  {(ptProvider, ptSnapshot) => (
                    <div
                      key={processTemplate.id!}
                      ref={ptProvider.innerRef}
                      {...ptProvider.draggableProps}
                      {...ptProvider.dragHandleProps}
                      style={this.getProcessTemplateStyle(ptSnapshot.isDragging, ptProvider.draggableProps.style)}
                    >
                      <ProcessTemplateNode
                        key={ix}
                        container={container}
                        processTemplate={processTemplate}
                        showProcessTemplateMenu={this.props.showProcessTemplateMenu}
                        initiateProcess={this.props.initiateProcess}
                        navigateToProcessTemplate={this.props.navigateToProcessTemplate}
                        selectedProcessTemplateId={this.props.selectedProcessTemplateId}
                        selectProcessTemplate={this.props.selectProcessTemplate}
                        exportMode={this.props.exportMode}
                        onSelectProcessTemplateForExport={this.props.onSelectProcessTemplateForExport}
                        selectedVersionNumer={getExportedData(processTemplate.id)}
                      />
                    </div>
                  )}
                </Draggable>
              ))}
            </List>
          )}
        </Collapse>
      </>
    );
  }
}

const Wrapper = withStyles(styles, { withTheme: true })(ProcessTemplateContainerNode);
export default Wrapper;

interface ProcessTemplateContainerProps extends WithStyles<typeof styles> {
  className?: string;
  initialVisibleDepth: number;
  container: ProcessTemplateContainer;
  exportMode: string;
  showProcessTemplateMenu: (event: React.MouseEvent<HTMLElement>, container: ProcessTemplateContainer, processTemplate: ProcessTemplate) => void;
  showProcessTemplateContainerMenu: (event: React.MouseEvent<HTMLElement>, container: ProcessTemplateContainer) => void;
  navigateToProcessTemplate: (processTemplateId: string) => void;
  initiateProcess: (container: ProcessTemplateContainer, processTemplate: ProcessTemplate) => void;
  selectedProcessTemplateId?: string;
  toggleContainerShowChildren: (container: ProcessTemplateContainer) => void;
  selectProcessTemplate: (processTemplateId?: string) => void;
  onSelectProcessTemplateForExport: (processTemplateId?: string, version?: string) => void;
  templatesToExport: Array<{ id: string; version: string }>;
}
