import React from 'react';
import autobind from 'autobind-decorator';
import { RouteComponentProps, withRouter } from 'react-router-dom';
import { Theme, withStyles, WithStyles, createStyles } from '@material-ui/core/styles';
import Button from '@material-ui/core/Button';
import InputIcon from '@material-ui/icons/Input';
import ProcessTable from './ProcessTable';
import { AppContext } from '../AppContext';
import { Process } from '../ppp';
import ExportProcessIcon from '@material-ui/icons/SaveAlt';
import InitiateProcessDialog from './dialogs/InitiateProcessDialog';
import { Grid } from '@material-ui/core';
import ProcessTemplateList from './ProcessTemplates/ProcessTemplateList';

export const styles = (theme: Theme) =>
  createStyles({
    icons: { marginRight: theme.spacing() },
    toolbarTitle: {
      flex: 1
    },
    titleLink: {
      textDecoration: 'none',
      color: theme.palette.common.black
    },
    header: {
      marginBottom: theme.spacing(1)
    }
  });

class Main extends React.Component<MainProps, MainState> {
  static contextType = AppContext;
  context!: React.ContextType<typeof AppContext>;
  private child: any;
  constructor(props: MainProps, context?: any) {
    super(props, context);
    this.child = React.createRef();
    this.state = {
      isInitiateDialogOpen: false
    };
  }

  @autobind
  async refreshData() {
    if (this.child && this.child.current) {
      await this.child.current.getFiscalYears();
    }
  }

  @autobind
  handleClickOpen() {
    this.setState({ isInitiateDialogOpen: true });
  }

  @autobind
  async handleExport() {
    if (this.child && this.child.current) {
      await this.child.current.handleCompleteExport();
    }
  }

  @autobind
  async handleClose(newProcess?: Process) {
    this.setState({ isInitiateDialogOpen: false });

    if (newProcess) {
      this.handleNavigateToProcess(newProcess);
      // refresh fiscal years in filters just in case a new was added
      setTimeout(async () => {
        const fiscalYears = await this.context.processStore.getAvailableFiscalYears(true);
        this.context.personalizationStore.configureFilters(fiscalYears);
      }, 1000);
    }
  }

  @autobind
  handleNavigateToProcess(process: Process) {
    const newPath = `/process/${process.id}`;
    if (this.props.history.location.pathname !== newPath) {
      this.props.history.push(newPath);
    }
  }

  public render() {
    const { tab, classes } = this.props;
    return (
      <>
        <div style={{ display: tab === 'processes' ? 'block' : 'none' }} id="mainComponent">
          <Grid container alignItems="flex-start" justify="space-between" direction="row" className={classes.header}>
            <Button variant="contained" color="primary" onClick={this.handleClickOpen}>
              <InputIcon className={this.props.classes.icons} /> Initiate Process
            </Button>
            <Button variant="contained" color="primary" onClick={this.handleExport}>
              <ExportProcessIcon className={this.props.classes.icons} /> Export
            </Button>
          </Grid>
          <ProcessTable innerRef={this.child} onNavigateToProcess={this.handleNavigateToProcess} currentUser={this.props.currentUser} />
          {this.state.isInitiateDialogOpen && <InitiateProcessDialog open={this.state.isInitiateDialogOpen} onClose={this.handleClose} />}
        </div>
        {tab === 'templates' && <ProcessTemplateList />}
      </>
    );
  }
}

export default withRouter(withStyles(styles)(Main));

interface MainProps extends WithStyles<typeof styles>, RouteComponentProps<ProcessListRouterProps> {
  tab: string;
  currentUser: any;
}

interface ProcessListRouterProps {
  path?: string;
}

interface MainState {
  isInitiateDialogOpen: boolean;
}
