import React from 'react';
import find from 'lodash/find';
import { withStyles, WithStyles } from '@material-ui/core/styles';
import autobind from 'autobind-decorator';
import CircularProgress from '@material-ui/core/CircularProgress';
import Dialog from '@material-ui/core/Dialog';
import DialogTitle from '@material-ui/core/DialogTitle';
import DialogContent from '@material-ui/core/DialogContent';
import DialogActions from '@material-ui/core/DialogActions';
import TextField from '@material-ui/core/TextField';
import Button from '@material-ui/core/Button';

import { styles } from './styles';
import { AppContext } from '../../AppContext';
import { ProcessTemplate } from '../../ppp';
import { ProcessTemplateContainer } from '../../ppp/ProcessTemplateContainer';
import Grid from '@material-ui/core/Grid';

class CopyProcessTemplateDialog extends React.Component<CopyProcessTemplateDialogProps, CopyProcessTemplateDialogState> {
  static contextType = AppContext;
  context!: React.ContextType<typeof AppContext>;
  private _isMounted: boolean = false;

  constructor(props: CopyProcessTemplateDialogProps) {
    super(props);

    this.state = {
      isLoading: true,
      isCopying: false
    };
  }

  @autobind
  handleOnClose() {
    if (this.props.onClose) {
      this.props.onClose();
    }
  }

  @autobind
  handleProcessTemplateTitleChange(event: React.ChangeEvent<HTMLInputElement>) {
    const { processTemplate } = this.state;
    if (!processTemplate) {
      return;
    }

    processTemplate.title = event.currentTarget.value;

    this.setState({
      processTemplate
    });
  }

  @autobind
  async copyProcessTemplate() {
    const { processTemplateContainer, onClose } = this.props;
    const { processTemplate } = this.state;
    const { processTemplateStore } = this.context;

    if (!processTemplate) {
      return;
    }

    this.setState({
      isCopying: true
    });

    const result = await processTemplateStore.saveProcessTemplateAsDraft(processTemplateContainer.path, processTemplate);

    this.setState({
      isCopying: false
    });

    if (onClose) {
      onClose(result);
    }
  }

  public async componentDidMount() {
    this._isMounted = true;
  }

  public componentWillUnmount() {
    this._isMounted = false;
  }

  async componentWillMount() {
    const { processTemplateId } = this.props;
    const { processTemplateStore } = this.context;

    const processTemplate = await processTemplateStore.getProcessTemplateById(processTemplateId);

    const clonedProcessTemplate: ProcessTemplate = {
      activities: processTemplate.activities,
      fields: processTemplate.fields,
      description: processTemplate.description,
      title: processTemplate.title
    };

    let ix = 1;
    while (find(this.props.processTemplateContainer.processTemplates.slice(), { title: `${clonedProcessTemplate.title} (${ix})` })) {
      ix++;
    }

    clonedProcessTemplate.title = `${clonedProcessTemplate.title} (${ix})`;

    this._isMounted === true &&
      this.setState({
        isLoading: false,
        processTemplate: clonedProcessTemplate
      });
  }

  render() {
    const { classes, processTemplateContainer, open } = this.props;
    const { processTemplate, isLoading, isCopying } = this.state;

    let isDuplicateTitle = false;
    if (processTemplate && find(processTemplateContainer.processTemplates.slice(), { title: processTemplate.title })) {
      isDuplicateTitle = true;
    }

    return (
      <Dialog open={open} onClose={this.handleOnClose} aria-labelledby="copy-process-template-dialog-title" fullWidth={true} maxWidth="xs">
        <DialogTitle id="copy-process-template-dialog-title">Copy Process Template</DialogTitle>
        <DialogContent>
          {isLoading && <CircularProgress variant="indeterminate" disableShrink className={classes.facebook2} size={24} thickness={4} />}
          {!isLoading && processTemplate && (
            <TextField
              error={isDuplicateTitle}
              fullWidth={true}
              value={processTemplate.title}
              margin="normal"
              label="New Process Template Title"
              helperText={isDuplicateTitle ? <i>A Process Template with this name already exists.</i> : undefined}
              onChange={this.handleProcessTemplateTitleChange}
            />
          )}
        </DialogContent>
        <DialogActions>
          {isCopying && !isLoading && (
            <Grid container style={{ margin: '15px' }}>
              <CircularProgress variant="indeterminate" disableShrink className={classes.facebook2} size={24} thickness={4} />
            </Grid>
          )}
          {!isCopying && !isLoading && (
            <>
              <Button onClick={this.handleOnClose} color="primary">
                Cancel
              </Button>
              <Button onClick={this.copyProcessTemplate} color="primary" disabled={isDuplicateTitle}>
                Copy
              </Button>
            </>
          )}
        </DialogActions>
      </Dialog>
    );
  }
}

export default withStyles(styles)(CopyProcessTemplateDialog);

interface CopyProcessTemplateDialogProps extends WithStyles<typeof styles> {
  open: boolean;
  onClose?: (copiedProcessTemplate?: ProcessTemplate) => void;
  processTemplateContainer: ProcessTemplateContainer;
  processTemplateId: string;
}

interface CopyProcessTemplateDialogState {
  isLoading: boolean;
  isCopying: boolean;
  processTemplate?: ProcessTemplate;
}
