import React from 'react';
import { Theme, withStyles, WithStyles } from '@material-ui/core/styles';
import { Link, RouteComponentProps, withRouter, Prompt } from 'react-router-dom';
import ProcessHeader from '../controls/process-header';
import green from '@material-ui/core/colors/green';
import { ProcessTemplate, ActivityTemplate, ProcessTemplateResponse, ProcessTemplateVersion, Attachment } from '../../ppp';
import Typography from '@material-ui/core/Typography';
import Button from '@material-ui/core/Button';
import Grid from '@material-ui/core/Grid';
import CircularProgress from '@material-ui/core/CircularProgress';

import AddIcon from '@material-ui/icons/Add';
import SaveIcon from '@material-ui/icons/Save';
import PublishIcon from '@material-ui/icons/Publish';
import ClearIcon from '@material-ui/icons/Clear';
import ArrowUpward from '@material-ui/icons/ArrowUpward';
import LaunchRounded from '@material-ui/icons/LaunchRounded';
import autobind from 'autobind-decorator';
import ActivityTemplateDialog from '../dialogs/ActivityTemplateDialog';
import ConfirmationDialog from '../dialogs/ConfirmationDialog';
import ActivitiesList from '../controls/ActivitiesList';
import { getGUID } from '@pnp/common';
import { AppContext } from '../../AppContext';
import trim from 'lodash/trim';
import toUpper from 'lodash/toUpper';
import DeleteActivityTemplateDialog from '../dialogs/DeleteActivityTemplateDialog';
import DisplayEditableField from '../controls/DisplayEditableField';
import find from 'lodash/find';

const styles = (theme: Theme) => ({
  section: {
    marginTop: theme.spacing(),
    marginBottom: theme.spacing()
  },
  toolbarTitle: {},
  buttonActions: {
    margin: '0px 2px'
  },
  wrapper: {
    margin: theme.spacing()
  },
  facebook2: {
    color: '#6798e5',
    animationDuration: '550ms'
  },
  icons: {
    marginRight: theme.spacing()
  },
  buttonSuccess: {
    backgroundColor: green[500],
    '&:hover': {
      backgroundColor: green[700]
    }
  },
  fabProgress: {
    color: green[500],
    top: -6,
    left: -6,
    zIndex: 1
  },
  buttonProgress: {
    color: green[500],
    top: '50%',
    left: '50%',
    marginTop: -12,
    marginLeft: -12
  },
  input: {}
});

class ProcessTemplateEditor extends React.Component<ProcessTemplateEditorProps, ProcessTemplateEditorState> {
  private _isMounted: boolean = false;
  static contextType = AppContext;
  context!: React.ContextType<typeof AppContext>;

  constructor(props: ProcessTemplateEditorProps) {
    super(props);

    const processTemplate: ProcessTemplate = { title: '', description: '' };
    this.state = {
      readOnly: true,
      data: processTemplate,
      open: false,
      path: '',
      parties: [],
      versionHistory: [],
      loading: false,
      selectedActivityTemplateIndex: -1,
      deleteConfirmationOpen: false,
      confirmationOpen: false,
      confirmationTitle: '',
      confirmationMessage: '',
      isPerformingAction: false,
      isUploadingAttachment: false,
      dataIsDirty: false,
      height: 0,
      origin: ''
    };
  }

  public async componentDidMount() {
    this._isMounted = true;
    const { match } = this.props;
    const { id, ver } = match.params;
    this.getAllData(id, ver);
  }

  public componentWillUnmount() {
    this._isMounted = false;
  }

  @autobind
  async getSharePointOrigin() {
    /* istanbul ignore next */
    if (process.env.NODE_ENV !== 'production') {
      return 'http://localhost:3001';
    }

    const ctx = (await this.context.spContext.getContextInfo()) as any;
    const a = document.createElement('a');
    a.setAttribute('href', ctx.WebFullUrl);

    const { hostname, port, protocol } = a;
    const origin = `${protocol}//${hostname}${port.length ? `:${port}` : ''}`;
    return origin;
  }

  @autobind
  handleImport(event: React.ChangeEvent<HTMLInputElement>) {
    if (event && event.target && event.target.files && event.target.files.length >= 0) {
      const file = event.target.files[0];
      this.importFile(file, event.target);
    }
  }

  @autobind
  importFile(file: File, target: any) {
    if (file) {
      this.setState({ isPerformingAction: true }, () => {
        const reader = new FileReader();
        reader.addEventListener(
          'load',
          async (evt: any) => {
            try {
              if (evt && evt.currentTarget && evt.currentTarget.result) {
                let binary = '';
                const bytes = new Uint8Array(evt.currentTarget.result);
                const length = bytes.byteLength;
                for (var i = 0; i < length; i++) {
                  binary += String.fromCharCode(bytes[i]);
                }
                const XLSX = await import('xlsx');
                let workbook = XLSX.read(binary, {
                  type: 'binary'
                });
                let spreadsheetdata = XLSX.utils.sheet_to_json(workbook.Sheets[workbook.SheetNames[0]], {
                  range: 0
                });
                let row: any;
                const { data } = this.state;
                let activitiesAddedCount = 0;
                for (row of spreadsheetdata) {
                  if (row !== undefined && row.Activity !== undefined) {
                    const responsibleParty = row.ResponsibleParty || '';
                    const allowAttachments = this.getboolean(row.Attachments);
                    const requireAttachment = this.getboolean(row.RequireAttachment);
                    const dependsPrior = this.getboolean(row.DependsPrior);
                    const externalReviewer = this.getboolean(row.ExternalReviewer);
                    const tooltip = row.Tooltip;
                    const activity: ActivityTemplate = {
                      id: getGUID(),
                      description: row.Activity,
                      allowAttachments: allowAttachments,
                      dependsOnPreviousActivity: dependsPrior,
                      responsibleParty: responsibleParty,
                      requireAttachments: requireAttachment,
                      requiresReview: externalReviewer,
                      tooltip: tooltip,
                      jobAidAttachments: []
                    };
                    if (data.activities === undefined) {
                      data.activities = [];
                    }
                    data.activities.push(activity);
                    activitiesAddedCount++;
                  }
                }
                if (activitiesAddedCount > 0) {
                  this.setState({
                    data,
                    dataIsDirty: true,
                    confirmationOpen: true,
                    confirmationTitle: 'Import Activities',
                    confirmationMessage: `${activitiesAddedCount} Activities were imported successfully.`
                  });
                }
              }
            } catch (e) {
              this.setState({ isPerformingAction: false });
              console.log(e);
            }
            this.setState({ isPerformingAction: false });
            target.value = ''; // HACK to circumvent opening again the same file name issue.
          },
          false
        );

        reader.addEventListener(
          'error',
          e => {
            this.setState({ isPerformingAction: false });
          },
          false
        );

        if (reader) {
          setTimeout(() => reader.readAsArrayBuffer(file), 200);
        }
      });
    }
  }

  getboolean(value: string): boolean {
    if (!value || value === undefined) {
      return false;
    }
    if (value && trim(value).length === 0) {
      return false;
    }
    value = toUpper(value);
    if (value && value.length > 0 && (value.charAt(0) === 'N' || value.charAt(0) === 'F')) {
      return false;
    }
    return true;
  }

  @autobind
  async handleSaveDraft(event: React.MouseEvent) {
    event.preventDefault();
    event.stopPropagation();
    if (this.state.readOnly === true) {
      return;
    }
    const { data } = this.state;
    const { processTemplateStore } = this.context;

    try {
      this.setState({ isPerformingAction: true });
      const response = await processTemplateStore.saveProcessTemplateAsDraft(this.state.path, data);
      this.setState({ data: response, isPerformingAction: false, dataIsDirty: false });
    } catch (e) {
      console.log(e);
      this.setState({ isPerformingAction: false });
    }
  }

  @autobind
  handleAddNewActivityClickOpen() {
    if (this.state.readOnly === true) {
      return;
    }
    const newActivityTemplate: ActivityTemplate = {
      id: getGUID(),
      description: '',
      allowAttachments: true,
      dependsOnPreviousActivity: false,
      responsibleParty: '',
      requireAttachments: false,
      requiresReview: false,
      jobAidAttachments: []
    };
    this.setState({ selectedActivityTemplate: newActivityTemplate, selectedActivityTemplateIndex: -1, open: true });
  }

  @autobind
  handleClickEdit(activityTemplate: ActivityTemplate, index: number) {
    if (this.state.readOnly === true) {
      return;
    }
    if (this.state.data.activities && index >= 0) {
      this.setState({ selectedActivityTemplate: this.state.data.activities[index], selectedActivityTemplateIndex: index, open: true });
    }
  }

  @autobind
  handleClickDelete(activityTemplate: ActivityTemplate, index: number) {
    if (this.state.readOnly === true) {
      return;
    }
    this.setState({
      deleteConfirmationOpen: true,
      selectedActivityTemplate: activityTemplate,
      selectedActivityTemplateIndex: index
    });
  }

  @autobind
  deleteActivity(activityTemplate: ActivityTemplate, index: number) {
    let { activities } = this.state.data;
    if (activities !== undefined) {
      const newActivitiesState = [...activities]; // clone the array
      if (activityTemplate.id === newActivitiesState[index].id) {
        // just a safeguard
        newActivitiesState.splice(index, 1);
        this.state.data.activities = newActivitiesState;
        this.setState({ data: this.state.data, open: false, dataIsDirty: true });
      } else {
        throw Error('ID doesnt match to delete.');
      }
    }
  }

  @autobind
  handleClose() {
    this.setState({ open: false });
  }

  @autobind
  handleActivitySave(item: ActivityTemplate, index: number) {
    this.updateActivity(item, index);
    setTimeout(() => {
      this.highlightItem(item.id);
      if (index < 0) {
        this.scrollToElement(item.id); // Only scroll for new rows
      }
    }, 500);
  }

  @autobind
  async handlePublishTemplate(event: React.MouseEvent) {
    event.preventDefault();
    event.stopPropagation();
    const { processTemplateStore } = this.context;
    const { id } = this.state.data;
    if (id !== undefined && id.length > 0) {
      try {
        this._isMounted && this.setState({ isPerformingAction: true });
        const response: ProcessTemplateResponse = await processTemplateStore.publishProcessTemplateById(id);
        this.setState({
          data: response,
          isPerformingAction: false,
          dataIsDirty: false,
          confirmationOpen: true,
          confirmationTitle: 'Published Template',
          confirmationMessage: `Process template ${this.state.data.description} has been published successfully.`
        });
      } catch (e) {
        console.log(e);
        this.setState({
          isPerformingAction: false,
          dataIsDirty: false,
          confirmationOpen: true,
          confirmationTitle: 'Error Message',
          confirmationMessage: `Error pubishing template. Error: ${e}.`
        });
      }
    }
  }

  @autobind
  handleCancelEdits(event: React.MouseEvent) {
    event.preventDefault();
    event.stopPropagation();
    const { match } = this.props;
    const { id, ver } = match.params;
    if (this._isMounted) {
      this.getTemplate(id, ver);
    }
  }

  @autobind
  handleActivityTemplateChange(item: ActivityTemplate, index: number) {
    this.updateActivity(item, index);
  }

  @autobind
  handleActivityTemplateDialogChange(activityTemplate: ActivityTemplate, index: number) {
    this.setState({
      selectedActivityTemplate: activityTemplate
    });
  }

  @autobind
  async handleAttachmentUpload(activity: ActivityTemplate, file: File) {
    const { processTemplateStore } = this.context;
    const { id } = this.state.data;
    if (id) {
      const response = await processTemplateStore.uploadProcessTemplateAttachment(id, file.name, file, activity.id);
      if (response) {
        this.updateActivityJobAidAttachments(activity.id, response as Attachment);
      }
    }
  }

  @autobind
  async beginAttachmentDelete(activity: ActivityTemplate, attachment: Attachment) {
    const { selectedActivityTemplate } = this.state;
    const { id } = this.state.data;
    if (id && selectedActivityTemplate) {
      const jaAttachment = find(selectedActivityTemplate.jobAidAttachments, { fileName: attachment.fileName });
      if (jaAttachment) {
        const idxJA = selectedActivityTemplate.jobAidAttachments.indexOf(jaAttachment);
        if (idxJA >= 0) {
          let buffer = [...selectedActivityTemplate.jobAidAttachments];
          buffer.splice(idxJA, 1);
          selectedActivityTemplate.jobAidAttachments = buffer;
          this.setState({ selectedActivityTemplate });
        }
      }
    }
  }

  @autobind
  handleActivitiesOrderChange(activities: Array<ActivityTemplate>) {
    if (this.state.readOnly === true) {
      return;
    }
    if (activities === undefined) {
      activities = [];
    }
    this.state.data.activities = activities;
    if (activities.length > 0) {
      activities[0].dependsOnPreviousActivity = false;
    }
    this.setState({ data: this.state.data, dataIsDirty: true });
  }

  @autobind
  async getAllData(id: string, ver?: string) {
    const { processTemplateStore } = this.context;
    if (id && id.length > 10) {
      if (ver && ver.length > 0) {
        this.setState({ readOnly: true, isPerformingAction: true });
      } else {
        this.setState({ readOnly: false, isPerformingAction: true });
      }
      try {
        const [containerPath, data, versionHistory, parties, origin] = await Promise.all([
          processTemplateStore.getProcessTemplateContainerById(id),
          processTemplateStore.getProcessTemplateById(id, ver),
          processTemplateStore.getProcessTemplateVersionHistoryById(id),
          processTemplateStore.getPartiesList(),
          this.getSharePointOrigin()
        ]);
        this._isMounted &&
          this.setState(
            {
              path: containerPath,
              data: data,
              versionHistory: versionHistory,
              parties: parties,
              isPerformingAction: false,
              dataIsDirty: false,
              readOnly: data.version !== data.currentVersionNumber,
              origin: origin
            },
            () => {
              this.setState({ height: window.innerHeight });
            }
          );
      } catch (e) {
        console.log(`ERROR -> ${e}`);
        this.setState({ isPerformingAction: false });
      }
    }
  }

  async getTemplate(id: string, ver?: string) {
    const { processTemplateStore } = this.context;
    if (id && id.length > 10) {
      this.setState({ isPerformingAction: true });
      try {
        const data = await processTemplateStore.getProcessTemplateById(id); // this so far is  used only in Non ReadOnly, Version is not nessesary.
        this._isMounted &&
          this.setState(
            {
              data: data,
              isPerformingAction: false,
              dataIsDirty: false,
              readOnly: false
            },
            () => {
              this.setState({ height: window.innerHeight });
            }
          );
      } catch (e) {
        console.log(`ERROR -> ${e}`);
        this.setState({ isPerformingAction: false });
      }
    }
  }

  @autobind
  updateActivity(item: ActivityTemplate, index?: number) {
    const { data } = this.state;
    if (this.state.readOnly === true) {
      return;
    }
    if (data.activities === undefined) {
      data.activities = [];
    }
    // BR: if requireAttachments = true then set allowAttachments = true
    if (item && (item.allowAttachments === false && item.requireAttachments === true)) {
      item.requireAttachments = false;
    }
    if (item && (item.allowAttachments === false && item.requireAttachments === true)) {
      item.allowAttachments = true;
    }

    if (index !== undefined && index >= 0) {
      data.activities[index].id = item.id;
      data.activities[index].description = item.description;
      data.activities[index].allowAttachments = item.allowAttachments;
      data.activities[index].dependsOnPreviousActivity = item.dependsOnPreviousActivity;
      data.activities[index].responsibleParty = item.responsibleParty;
      data.activities[index].requireAttachments = item.requireAttachments;
      data.activities[index].requiresReview = item.requiresReview;
      data.activities[index].tooltip = item.tooltip !== undefined ? item.tooltip : undefined;
      if (item.jobAidAttachments) {
        data.activities[index].jobAidAttachments = [...item.jobAidAttachments]; //
      } else {
        data.activities[index].jobAidAttachments = [];
      }
    } else {
      data.activities.push(item);
    }

    // BR: Make sure the first activity is not depending on any activity.
    if (data.activities.length > 0) {
      data.activities[0].dependsOnPreviousActivity = false;
    }
    this.setState({ data, open: false, dataIsDirty: true }, () => {
      /*  this.setState({ height: window.innerHeight }); */
    });
  }

  @autobind
  updateActivityJobAidAttachments(id: string, item: Attachment) {
    const { selectedActivityTemplate } = this.state;
    if (this.state.readOnly === true) {
      return;
    }
    if (selectedActivityTemplate) {
      if (selectedActivityTemplate.id === id) {
        if (!selectedActivityTemplate.jobAidAttachments) {
          selectedActivityTemplate.jobAidAttachments = [];
        }
        const jaAttachment = find(selectedActivityTemplate.jobAidAttachments, { fileName: item.fileName });
        if (!jaAttachment) {
          selectedActivityTemplate.jobAidAttachments = [...selectedActivityTemplate.jobAidAttachments, item];
          this.setState({ selectedActivityTemplate, dataIsDirty: true });
        }
      }
    }
  }

  @autobind
  updateProcessJobAidAttachments(id: string, item: Attachment) {
    const { data } = this.state;

    if (this.state.readOnly === true) {
      return;
    }
    if (data.id && data.id === id) {
      if (!data.jobAidAttachments) {
        data.jobAidAttachments = [];
      }
      const jaAttachment = find(data.jobAidAttachments, { fileName: item.fileName });
      if (!jaAttachment) {
        data.jobAidAttachments = [...data.jobAidAttachments, item];
        this.setState({ data, dataIsDirty: true });
      }
    }
  }

  scrollToElement(id: string) {
    const ele = document.getElementById(id);
    if (ele) {
      const clientRects = ele.getClientRects();
      /* istanbul ignore next */
      const top = clientRects[0] ? clientRects[0].top : 0;
      /* istanbul ignore next */
      if (top > window.pageYOffset + window.innerHeight || top < window.pageYOffset) {
        // @ts-ignore
        const isIE = /*@cc_on!@*/ false || !!document.documentMode;
        if (isIE) {
          document.documentElement.scrollTop = top;
        } else {
          window.scroll({
            top: top,
            behavior: 'smooth'
          });
        }
      }
    }
  }

  highlightItem(id: string) {
    const ele = document.getElementById(id);
    if (ele) {
      ele.classList.add('backgroundAnimated');
      setTimeout(() => {
        ele.classList.remove('backgroundAnimated');
      }, 5000);
    }
  }

  hasScrollbar() {
    const body = document.getElementsByTagName('body')[0];
    if (body) {
      return body.scrollHeight > window.innerHeight;
    }
    return false;
  }

  @autobind
  handleAttachmentClicked(attachment: Attachment) {
    window.open(this.state.origin + attachment.serverRelativeUrl, '_blank');
  }

  @autobind
  async handleProcessAttachmentUpload(attachmentFile: File) {
    const { processTemplateStore } = this.context;
    const { id } = this.state.data;
    this.setState({
      isPerformingAction: true,
      isUploadingAttachment: true
    });
    if (attachmentFile && id) {
      const response = await processTemplateStore.uploadProcessTemplateAttachment(id, attachmentFile.name, attachmentFile);
      if (response) {
        this.updateProcessJobAidAttachments(id, response as Attachment);
      }
    }
    this.setState({
      isPerformingAction: false,
      isUploadingAttachment: false
    });
  }

  @autobind
  async handleProcessAttachmentDelete(attachment: Attachment) {
    const { data } = this.state;

    this.setState({
      isPerformingAction: true,
      isUploadingAttachment: true
    });

    if (data.id) {
      const jaAttachment = find(data.jobAidAttachments, { fileName: attachment.fileName });
      if (jaAttachment && jaAttachment !== undefined && data.jobAidAttachments) {
        const idx = data.jobAidAttachments.indexOf(jaAttachment as Attachment);
        if (idx >= 0) {
          let buffer = [...data.jobAidAttachments];
          buffer.splice(idx, 1);
          data.jobAidAttachments = buffer;
          this.setState({ data, dataIsDirty: true });
        }
      }
    }
    this.setState({
      isPerformingAction: false,
      isUploadingAttachment: false
    });
  }

  public render() {
    const { classes, ...others } = this.props;
    const { data, loading, isPerformingAction, isUploadingAttachment, dataIsDirty, path, readOnly, origin } = this.state;
    let pathDisplay = path;
    if (pathDisplay) {
      pathDisplay = pathDisplay // Clean a bit the path for display
        .replace(/^\/|\/$/g, '')
        .replace(/\//g, ' / ');
    }
    return (
      <React.Fragment>
        <Prompt when={dataIsDirty} message="You have unsaved changes, are you sure you want to leave?" />
        <p>
          <Link to="/">Home</Link> &gt; <Link to="/templates">Process Templates</Link>
        </p>
        <DisplayEditableField
          readOnly={readOnly}
          isLoading={this.state.isPerformingAction}
          label="Process Template Title"
          title="Template Editor: "
          field={this.state.data.title}
          onSave={value => {
            data.title = value;
            this.setState({ data, dataIsDirty: true });
          }}
        />

        <ProcessHeader
          {...others}
          id={this.props.match.params.id}
          data={data}
          path={pathDisplay}
          origin={origin}
          isUploadingAttachment={isUploadingAttachment}
          onAttachmentClicked={this.handleAttachmentClicked}
          onProcessAttachmentUpload={this.handleProcessAttachmentUpload}
          onProcessAttachmentDelete={this.handleProcessAttachmentDelete}
        />

        <Grid container direction="row" alignItems="center" justify="space-between">
          <Grid item className={classes.section}>
            <Typography variant="h5" color="inherit" noWrap className={classes.toolbarTitle} id="addActivityButton">
              Activities
            </Typography>
          </Grid>

          {isPerformingAction && (
            <Grid item xs={3} className={classes.section}>
              <CircularProgress style={{ margin: '2px' }} variant="indeterminate" disableShrink className={classes.facebook2} size={30} thickness={4} />
            </Grid>
          )}
          {!isPerformingAction && (
            <>
              <Grid item className={classes.section}>
                <Button variant="outlined" color="primary" className={classes.buttonActions} onClick={this.handleCancelEdits} disabled={!dataIsDirty || readOnly}>
                  <ClearIcon className={classes.icons} />
                  Cancel Changes
                </Button>
                <Button variant="outlined" color="primary" className={classes.buttonActions} onClick={this.handleSaveDraft} disabled={!dataIsDirty || readOnly}>
                  <SaveIcon className={classes.icons} />
                  Save Draft
                </Button>
                <Button
                  variant="outlined"
                  color="primary"
                  className={classes.buttonActions}
                  onClick={this.handlePublishTemplate}
                  disabled={dataIsDirty || (!data.activities || data.activities.length === 0) || readOnly}
                >
                  <PublishIcon className={classes.icons} />
                  Publish Template
                </Button>
              </Grid>
            </>
          )}
        </Grid>

        <Grid item className={classes.section}>
          <Grid container justify="space-between">
            <Grid item>
              <Button variant="contained" color="primary" className={classes.buttonActions} onClick={this.handleAddNewActivityClickOpen} disabled={readOnly}>
                <AddIcon className={classes.icons} /> Activity
              </Button>
              <input
                accept=".csv, application/vnd.openxmlformats-officedocument.spreadsheetml.sheet, application/vnd.ms-excel"
                className={classes.input}
                style={{ display: 'none' }}
                id="import-button-file"
                type="file"
                onChange={this.handleImport}
              />
              <label htmlFor="import-button-file">
                <Button component="span" disabled={loading || readOnly}>
                  <ArrowUpward className={classes.icons} />
                  Import Activities from Excel
                </Button>
                {loading && <CircularProgress size={24} className={classes.buttonProgress} />}
              </label>
              <Button href="ActivityTemplate.xlsx">
                <LaunchRounded className={classes.icons} />
                Download Excel Template
              </Button>
            </Grid>
          </Grid>
        </Grid>

        {data.activities && (
          <ActivitiesList
            readOnly={this.state.readOnly}
            activities={this.state.data.activities}
            onActivityTemplateChange={this.handleActivityTemplateChange}
            onClickEdit={this.handleClickEdit}
            onClickDelete={this.handleClickDelete}
            onActivitiesOrderChange={this.handleActivitiesOrderChange}
          />
        )}
        {!isPerformingAction && this.hasScrollbar() && (
          <Grid container direction="row" justify="center" alignItems="center" style={{ padding: 10 }}>
            <Button
              id="scrollToBottomButton"
              style={{ width: '300px' }}
              onClick={() => {
                this.scrollToElement('addActivityButton');
              }}
            >
              <ArrowUpward />
              <span style={{ paddingLeft: 5 }}> Scroll Up </span>
            </Button>
          </Grid>
        )}
        <ActivityTemplateDialog
          activityTemplate={this.state.selectedActivityTemplate}
          index={this.state.selectedActivityTemplateIndex}
          parties={this.state.parties}
          open={this.state.open}
          onActivityTemplateChange={this.handleActivityTemplateDialogChange}
          onClose={this.handleClose}
          onSave={this.handleActivitySave}
          origin={this.state.origin}
          onAttachmentUpload={this.handleAttachmentUpload}
          onAttachmentDelete={this.beginAttachmentDelete}
        />
        <ConfirmationDialog
          open={this.state.confirmationOpen}
          title={this.state.confirmationTitle}
          message={this.state.confirmationMessage}
          onClose={() => {
            this.setState({ confirmationOpen: false });
          }}
        />
        <DeleteActivityTemplateDialog
          open={this.state.deleteConfirmationOpen}
          index={this.state.selectedActivityTemplateIndex}
          activityTemplate={this.state.selectedActivityTemplate}
          onClose={() => {
            this.setState({ deleteConfirmationOpen: false });
          }}
          onDelete={(activityTemplate, index) => {
            if (activityTemplate && index >= 0) {
              this.deleteActivity(activityTemplate, index);
            }
          }}
        />
      </React.Fragment>
    );
  }
}

export default withRouter(withStyles(styles)(ProcessTemplateEditor));

interface ProcessTemplateEditorProps extends WithStyles<typeof styles>, RouteComponentProps<DetailRouterProps> {
  // TODO add more props here if nessesary
}

interface DetailRouterProps {
  id: string;
  ver: string;
}

interface ProcessTemplateEditorState {
  versionHistory: ProcessTemplateVersion[];
  data: ProcessTemplate;
  open: boolean;
  path: string;
  parties: Array<{ value: string; text: string }>;
  readOnly: boolean;
  loading: boolean;
  confirmationTitle: string;
  confirmationMessage: string;
  confirmationOpen: boolean;
  deleteConfirmationOpen: boolean;
  selectedActivityTemplate?: ActivityTemplate;
  selectedActivityTemplateIndex: number;
  isPerformingAction: boolean;
  isUploadingAttachment: boolean;

  dataIsDirty: boolean;
  height: number;
  origin: string;
}
