import { BaristaService } from '../util/BaristaService';
import { Filter } from './Filter';
import { observable } from 'mobx';
import { UserConfig } from './UserConfig';
import { FilterConfiguration, FilterObject } from './FilterObject';
import { getFiscalYear } from '../util/pppUtils';
import { Process } from './Process';

export class PersonalizationStore {
  private _baristaService: BaristaService;
  private _storage: any;
  private readonly _userConfig = 'UserConfig';

  @observable
  selectedViewFilter: Filter = this.getDefaultFilter();

  @observable
  filterConfig: FilterConfiguration = {
    categories: [],
    filters: []
  };

  @observable
  availableResponsibleParties: { value: string; text: string }[] = [];

  @observable
  filterViews: Filter[] = [];

  @observable
  areFiltersLoaded: boolean = false;

  @observable
  isConfiguredFiltersLoaded: boolean = false;

  /**
   * Creates a new ProcessStore
   */
  constructor(baristaService: BaristaService, storage: any) {
    this._baristaService = baristaService;
    this._storage = storage;
  }

  public async loadViews(userName?: string) {
    this.areFiltersLoaded = false;
    const [local, global] = await Promise.all([this.getPersonalFilters(userName || 'N/A'), this.getGlobalFilters()]);
    this.filterViews = [...local, ...global];
    this.areFiltersLoaded = true;
  }

  public async configureFilters(fiscalYears: string[]) {
    const filterConfig: FilterConfiguration = {
      categories: [],
      filters: []
    };
    this.isConfiguredFiltersLoaded = false;

    // Add Responsible Parties
    this.availableResponsibleParties.forEach(arp => {
      filterConfig.filters.push({
        key: arp.text,
        category: 'ResponsibleParty',
        label: arp.text,
        DateRange: false,
        LessThan: false,
        Date: ''
      });
    });

    // Add filter categories
    filterConfig.categories.push({ key: 'FY', name: 'Fiscal Year' });
    filterConfig.categories.push({ key: 'FunctionalArea', name: 'Functional Area' });
    filterConfig.categories.push({ key: 'ResponsibleParty', name: 'Responsible Party' });
    filterConfig.categories.push({ key: 'status', name: 'Status' });
    filterConfig.categories.push({ key: 'TransactionDate', name: 'Transaction Date' });
    filterConfig.categories.push({ key: 'CreationDate', name: 'Creation Date' });
    filterConfig.categories.push({ key: 'CreatedBy', name: 'Initiated By' });

    // Add status filters
    filterConfig.filters.push({ key: 'Active', category: 'status', label: 'Active', DateRange: false, LessThan: false, Date: '' });
    filterConfig.filters.push({ key: 'CompletedNotSigned', category: 'status', label: 'Completed But Not Signed', DateRange: false, LessThan: false, Date: '' });
    filterConfig.filters.push({ key: 'CompletedAndSigned', category: 'status', label: 'Completed And Signed', DateRange: false, LessThan: false, Date: '' });
    filterConfig.filters.push({ key: 'Initiated', category: 'status', label: 'Initiated', DateRange: false, LessThan: false, Date: '' });
    filterConfig.filters.push({ key: 'Rejected', category: 'status', label: 'Rejected', DateRange: false, LessThan: false, Date: '' });

    fiscalYears.forEach(fy => {
      filterConfig.filters.push({ key: fy, category: 'FY', label: fy, DateRange: false, LessThan: false, Date: '' });
    });

    // Add Transaction Date
    filterConfig.filters.push({
      key: 'Transaction Starting Date',
      category: 'TransactionDate',
      label: 'Starting Date',
      DateRange: true,
      LessThan: false,
      Date: new Date().toDateString()
    });
    filterConfig.filters.push({ key: 'Transaction Ending Date', category: 'TransactionDate', label: 'Ending Date', DateRange: true, LessThan: true, Date: new Date().toDateString() });

    // Add Creation Date
    filterConfig.filters.push({ key: 'Creation Starting Date', category: 'CreationDate', label: 'Starting Date', DateRange: true, LessThan: false, Date: new Date().toLocaleDateString('en-US') });
    filterConfig.filters.push({ key: 'Creation Ending Date', category: 'CreationDate', label: 'Ending Date', DateRange: true, LessThan: true, Date: new Date().toLocaleDateString('en-US') });

    this.filterConfig = filterConfig;
    this.isConfiguredFiltersLoaded = true;
  }

  public async configureDataSpecificFilters(processes: Process[]) {
    const tempFunctionalAreas = processes.map(e => e.functionArea);
    var functionalAreas: string[] = [];
    tempFunctionalAreas.forEach(e => {
      if (functionalAreas.indexOf(e) < 0) {
        functionalAreas.push(e);
      }
    });

    // process initiators
    const tempProcessInitiators = processes.map(e => e.initiatedBy);
    let initiators: any[] = [];
    tempProcessInitiators.forEach(z => {
      if (typeof z !== 'undefined') {
        if (initiators.filter(j => j === z.displayName).length === 0) {
          initiators.push(z.displayName);
        }
      }
    });

    // Remove these two to re-add below
    let i = this.filterConfig.filters.length;
    while (i--) {
      if (this.filterConfig.filters[i].category === 'CreatedBy' || this.filterConfig.filters[i].category === 'FunctionalArea') {
        this.filterConfig.filters.splice(i, 1);
      }
    }
    initiators.forEach(item => {
      this.filterConfig.filters.push({ key: item, category: 'CreatedBy', label: item, DateRange: false, LessThan: false, Date: '' });
    });

    // Add Functional Areas
    functionalAreas.forEach(fa => {
      this.filterConfig.filters.push({
        key: fa,
        category: 'FunctionalArea',
        label: fa,
        DateRange: false,
        LessThan: false,
        Date: ''
      });
    });
  }

  /**
   * Save a filter
   */
  public async saveFilter(filterSet: Filter, global: boolean): Promise<number> {
    const response = await this._baristaService.post('Personalization/saveFilter.js', {
      data: {
        filter: filterSet,
        global: global
      }
    });
    return response.status;
  }

  /**
   * Retrieve a filter by the specified id.
   * @param filterId the id of the filter
   */
  public async getFilterById(filterId: string): Promise<Filter> {
    const response = await this._baristaService.get('Personalization/getFilterById.js', { params: { filterId } });
    return response.data;
  }

  /**
   * Save user's dateTime of last viewd Notifications
   * Use session cache for performance
   */
  public async saveNotificationsToken(): Promise<string> {
    const response = await this._baristaService.get('Personalization/saveNotificationsToken.js', { params: {} });
    if (response && response.data && response.data) {
      this._storage.setItem(this._userConfig, JSON.stringify(response.data));
    }
    return response.data;
  }

  /**
   * Retrieve user's dateTime of last viewd Notifications
   * Use session cache for performance
   */
  public async getUserConfig(): Promise<UserConfig | undefined> {
    const userConfig = this._storage.getItem(this._userConfig);
    if (userConfig) {
      return JSON.parse(userConfig);
    } else {
      const response = await this._baristaService.get('Personalization/getUserConfig.js', { params: {} });
      if (response && response.data && response.data) {
        this._storage.setItem(this._userConfig, JSON.stringify(response.data));
        return response.data;
      }
    }
  }

  /**
   * Delete a saved filter by the specified id.
   * @param filterId  the id of the filter
   */
  public async deleteFilterById(filterId: string): Promise<boolean> {
    const response = await this._baristaService.delete('Personalization/deleteFilter.js', { data: { filterId } });
    await this.loadViews();
    return response.data;
  }

  /*
   * saved if user wants its own data in the log notification area..
   * @param filterId  the id of the filter
   */
  public async saveIncludeOwn(value: boolean): Promise<boolean> {
    const response = await this._baristaService.get('Personalization/saveIncludeOwn.js', { params: { value } });
    if (response && response.data && response.data) {
      this._storage.setItem(this._userConfig, JSON.stringify(response.data));
    }
    return response.data;
  }

  private getDefaultFilterSet(): Array<FilterObject> {
    const august = new Date(new Date().getFullYear(), 7, 1);
    const december = new Date(new Date().getFullYear(), 11, 1);
    const today = new Date();
    const resturnSet = [] as Array<FilterObject>;

    if (august < today && today < december) {
      const forceCurrentYear = new Date(new Date().getFullYear(), 6);
      resturnSet.push({
        key: getFiscalYear(forceCurrentYear),
        label: 'Fiscal Year: ' + getFiscalYear(forceCurrentYear),
        category: 'FY',
        DateRange: false,
        LessThan: false,
        Date: ''
      });
    }

    resturnSet.push({
      key: getFiscalYear(),
      label: 'Fiscal Year: ' + getFiscalYear(),
      category: 'FY',
      DateRange: false,
      LessThan: false,
      Date: ''
    });
    return resturnSet;
  }

  private getDefaultFilter(): Filter {
    return { id: '', global: false, name: 'Default View', filterSet: this.getDefaultFilterSet() };
  }

  /*
   * Retrieve all fiters the user has saved.
   */
  public async getPersonalFilters(userName?: string): Promise<Filter[]> {
    const response = await this._baristaService.get('Personalization/getSavedFilters.js');
    // Add My Dashboard default filterSet
    const localFilterViewsLocal: Filter[] = [
      {
        id: '0',
        global: false,
        name: 'My Dashboard',
        filterSet: [
          ...this.getDefaultFilterSet(),
          {
            key: userName || 'N/A',
            category: 'CreatedBy',
            DateRange: false,
            LessThan: false,
            Date: '',
            label: 'Initiated By: ' + userName || 'N/A'
          },
          {
            key: 'Active',
            category: 'status',
            DateRange: false,
            LessThan: false,
            Date: '',
            label: 'Status: Active'
          }
        ]
      }
    ];
    if (response && response.data) {
      response.data.forEach((pf: any) => {
        pf.global = false;
        localFilterViewsLocal.push(pf);
      });
    }
    return localFilterViewsLocal;
  }

  public setDefaultFilter(): void {
    this.selectedViewFilter = this.getDefaultFilter();
  }

  /**
   * Retrieve all global filters
   *
   */
  public async getGlobalFilters(): Promise<Filter[]> {
    const response = await this._baristaService.get('Personalization/getGlobalFilters.js');
    if (response && response.data) {
      response.data.forEach((pf: any) => {
        pf.global = true;
      });
    }
    return response.data;
  }
}
