import { BaristaService } from '../util/BaristaService';
import { Process } from './Process';
import { getFiscalYear } from '../util/pppUtils';
/* import { observable } from 'mobx'; */
import { MonitoringAndReview } from './MonitoringAndReview';
import { Attachment } from './Attachment';
import { NotedException } from './NotedException';

export class ProcessStore {
  private _baristaService: BaristaService;
  private _fiscalYearList: string[] = [];

  constructor(baristaService: BaristaService) {
    this._baristaService = baristaService;
  }

  /**
   * Create a new process from a process template.
   */
  public async createProcessFromProcessTemplate(
    processTemplateId: string,
    version: string,
    name: string,
    transactionDate?: Date,
    fiscalYear?: string,
    relatedRejectedProcessInstance?: { id: string; name: string }
  ): Promise<Process> {
    const response = await this._baristaService.post('Process/createProcessFromProcessTemplate.js', {
      data: {
        processTemplateId,
        version,
        name,
        transactionDate,
        fiscalYear,
        relatedRejectedProcessInstance
      }
    });
    return response.data;
  }

  /**
   * Retrieve a process by the specified id.
   * @param processId the id of the process
   */
  public async getProcessById(processId: string): Promise<Process> {
    const response = await this._baristaService.get('Process/getProcessById.js', { params: { processId } });
    return response.data;
  }

  /**
   * Delete a process by the specified id.
   * @param processId the id of the process
   */
  public async deleteProcessById(processId: string): Promise<boolean> {
    const response = await this._baristaService.delete('Process/deleteProcessById.js', { data: { processId } });
    return response.data;
  }

  /**
   * Retrieve all processes as a hash where the fiscalYearis the key.
   */
  public async listAllProcesses(): Promise<Process[]> {
    const response = await this._baristaService.get('Process/listAllProcesses.js');
    return response.data;
  }

  /**
   * Retrieve all processes for the specified fiscal year.
   * @param fiscalYear the fiscal year to retrieve processes for.
   */
  public async listAllProcessesForFiscalYear(fiscalYear?: string): Promise<Process[]> {
    if (!fiscalYear) {
      fiscalYear = getFiscalYear();
    }

    const response = await this._baristaService.get('Process/listAllProcessesForFiscalYear.js', { params: { fiscalYear } });
    return response.data;
  }
  /**
   * Retrieve all processes for the specified fiscal year.
   * @param fiscalYear the fiscal year to retrieve processes for.
   */
  public async listAllProcessesForFiscalYears(fiscalYears: Array<string | undefined>): Promise<Process[]> {
    const p = JSON.stringify(fiscalYears);
    const response = await this._baristaService.get('Process/listAllProcessesForFiscalYears.js', { params: { fiscalYears: p } });
    return response.data;
  }
  /**
   * Retrieve all fiscal years that have a process in the system
   */
  public async getAvailableFiscalYears(noCache?: boolean): Promise<string[]> {
    if (noCache && noCache === true) {
      this._fiscalYearList = [];
    }
    if (this._fiscalYearList && this._fiscalYearList.length > 0) {
      return this._fiscalYearList || [];
    } else {
      const response = await this._baristaService.get('Process/getAvailableFiscalYears.js');
      if (response && response.data) {
        this._fiscalYearList = response.data.sort().reverse();
        return this._fiscalYearList;
      }
    }
    return [];
  }

  /**
   * Retrieve process url.
   * @param fiscalYear the fiscal year to retrieve processes for.
   */
  public async getProcessPathByProcessId(processId?: string): Promise<string> {
    const response = await this._baristaService.get('Process/getProcessPathByProcessId.js', { params: { processId } });
    return response.data;
  }
  /**
   * Mark the specified process activity as complete.
   * @param processId the id of the process
   * @param activityId the id of the activity to mark complete
   */
  public async markProcessActivityAsComplete(processId: string, activityId: string): Promise<Process> {
    const response = await this._baristaService.put('Process/markProcessActivityAsComplete.js', { data: { processId, activityId } });
    return response.data;
  }

  /**
   * Mark the specified process activity as incomplete.
   * @param processId the id of the process
   * @param activityId the id of the activity to mark incomplete
   */
  public async markProcessActivityAsIncomplete(processId: string, activityId: string): Promise<Process> {
    const response = await this._baristaService.put('Process/markProcessActivityAsIncomplete.js', { data: { processId, activityId } });
    return response.data;
  }

  /**
   * Mark the specified process activity as not required.
   * @param processId the id of the process
   * @param activityId the id of the activity to mark not required
   */
  public async markProcessActivityAsNotRequired(processId: string, activityId: string, comments: string): Promise<Process> {
    const response = await this._baristaService.put('Process/markProcessActivityAsNotRequired.js', { data: { processId, activityId, comments } });
    return response.data;
  }

  /**
   * Mark the specified process activity as reviewed by the current user
   * @param processId the id of the process
   * @param activityId the id of the activity to mark reviewed
   */
  public async markProcessActivityAsReviewed(processId: string, activityId: string): Promise<Process> {
    const response = await this._baristaService.put('Process/markProcessActivityAsReviewed.js', { data: { processId, activityId } });
    return response.data;
  }

  /**
   * Remove the specified process activity attachment.
   * @param processId the id of the process
   * @param activityId the id of the activity
   * @param fileName the filename of the attachment to remove.
   */
  public async removeProcessActivityAttachment(processId: string, activityId: string, fileName: string): Promise<Process> {
    const response = await this._baristaService.delete('Process/removeProcessActivityAttachment.js', { data: { processId, activityId, fileName } });
    return response.data;
  }

  /**
   * Indicate that the specified process has been signed off by the current user.
   * @param processId the id of the process
   */
  public async signCompletedProcess(processId: string): Promise<Process> {
    const response = await this._baristaService.put('Process/signCompletedProcess.js', { data: { processId } });
    return response.data;
  }

  /**
   * Retrieve a process by the specified id.
   * @param processId the id of the process
   */
  public async createProcessPDF(processId: string) {
    await this._baristaService.get('Process/createProcessPDF.js', { params: { processId } });
  }

  /**
   * Unsign process for continued editing
   *
   * @param processId Id of the process
   * @param reason logged reason for unsigning
   */
  public async unsignProcess(processId: string, reason: string): Promise<Process> {
    const response = await this._baristaService.put('Process/unsignProcess.js', { data: { processId, reason } });
    return response.data;
  }

  /**
   * reject process for
   *
   * @param processId Id of the process
   */
  public async rejectProcess(processId: string, reason: string): Promise<Process> {
    const response = await this._baristaService.put('Process/rejectProcess.js', { data: { processId, reason } });
    return response.data;
  }

  /**
   * Update the comments associated with the specified process activity
   * @param processId the id of the process
   * @param activityId the id of the activity
   * @param comments the comments
   */
  public async updateProcessActivityComments(processId: string, activityId: string, comments: string): Promise<Process> {
    const response = await this._baristaService.put('Process/updateProcessActivityComments.js', { data: { processId, activityId, comments } });
    return response.data;
  }

  /**
   * Update the responsible party associated with the specified process activity
   * @param processId the id of the process
   * @param activityId the id of the activity
   * @param responsibleParty the responsible party
   */
  public async updateProcessActivityResponsibleParty(processId: string, activityId: string, responsibleParty: string): Promise<Process> {
    const response = await this._baristaService.put('Process/updateProcessActivityResponsibleParty.js', { data: { processId, activityId, responsibleParty } });
    return response.data;
  }

  /**
   * Update the process field with the specified value.
   * @param processId the id of the process
   * @param fieldId the id of the field
   * @param fieldValue the field value
   */
  public async updateProcessFieldValue(processId: string, fieldId: string, fieldValue: string): Promise<Process> {
    const response = await this._baristaService.put('Process/updateProcessFieldValue.js', { data: { processId, fieldId, fieldValue } });
    return response.data;
  }

  /**
   * Update the notes associated with a process
   * @param processId the id of the process
   * @param notes notes
   */
  public async updateProcessNotes(processId: string, notes: string): Promise<Process> {
    const response = await this._baristaService.put('Process/updateProcessNotes.js', { data: { processId, notes } });
    return response.data;
  }

  /**
   * Update the monitoring And Review  data associated with a process
   * @param processId the id of the process
   * @param monitoring And Review Info
   */
  public async updateMonitoringAndReviewQuestions(processId: string, monitoringAndReview: MonitoringAndReview): Promise<Process> {
    const response = await this._baristaService.put('Process/updateMonitoringAndReviewQuestions.js', { data: { processId, monitoringAndReview } });
    return response.data;
  }

  /**
   * Update the monitoring And Review  data associated with a process
   * @param processId the id of the process
   * @param monitoring And Review Info
   */
  public async updateMonitoringAndReviewNotes(processId: string, generalNotes: string): Promise<Process> {
    const response = await this._baristaService.put('Process/UpdateMonitoringAndReviewNotes.js', { data: { processId, generalNotes } });
    return response.data;
  }

  /**
   * Update the monitoring And Review  data associated with a process
   * @param processId the id of the process
   * @param monitoring And Review Info
   */
  public async updateMonitoringAndReviewNotedExceptions(processId: string, notedExceptions: NotedException[] | undefined): Promise<Process> {
    const response = await this._baristaService.put('Process/updateMonitoringAndReviewNotedExceptions.js', { data: { processId, notedExceptions } });
    return response.data;
  }

  /**
   * Complete the monitoring And Review
   * @param processId the id of the process
   */
  public async markMonitoringAndReviewAsComplete(processId: string): Promise<Process> {
    const response = await this._baristaService.put('Process/markMonitoringAndReviewCompletedProcess.js', { data: { processId } });
    return response.data;
  }

  /**
   * Update process owner information into review and monitoring
   * @param processId the id of the process
   */
  public async updateMonitoringAndReviewProcessOwner(processId: string, notedExceptionId: string, value: string): Promise<Process> {
    const response = await this._baristaService.put('Process/updateMonitoringAndReviewProcessOwner.js', { data: { processId, notedExceptionId, value } });
    return response.data;
  }

  /**
   * Update the name associated with a process
   * @param processId the id of the process
   * @param name name
   */
  public async updateProcessName(processId: string, name: string): Promise<Process> {
    const response = await this._baristaService.put('Process/updateProcessName.js', { data: { processId, name } });
    return response.data;
  }

  /**
   * Upload a process activity attachment
   * @param processId the id of the process
   * @param activityId the id of the activity
   * @param fileName the filename of the attachment
   * @param file the file blob
   */
  public async uploadProcessActivityAttachment(processId: string, activityId: string, fileName: string, originalFileName: string, file: Blob): Promise<Process> {
    var fd = new FormData();
    fd.append('files[0]', file, fileName);
    const response = await this._baristaService.post('Process/uploadProcessActivityAttachment.js', {
      headers: {
        'Content-Type': 'multipart/form-data'
      },
      params: { processId, activityId, fileName, originalFileName },
      data: fd
    });
    return response.data;
  }

  /**
   * Move Process to a different Fiscal Year
   * @param processId the id of the process
   * @param toFiscalYear t
   */
  public async moveProcessToAnotherFiscalYear(processId: string, toFiscalYear: string): Promise<string> {
    const response = await this._baristaService.put('Process/moveProcessToAnotherFiscalYear.js', { data: { processId, toFiscalYear } });
    return response.data;
  }

  /**
   * Upload an attachment to Noted Exceptions
   * @param processId the id of the process
   * @param notedExceptionId the id of the noted Exception
   * @param fileName the filename of the attachment
   * @param file the file blob
   */
  public async uploadNotedExceptionAttachment(processId: string, fileName: string, file: Blob, notedExceptionId: string): Promise<Attachment> {
    var fd = new FormData();
    fd.append('files[0]', file, fileName);
    var originalFileName = fileName;
    var params = { processId, notedExceptionId, fileName, originalFileName };
    const response = await this._baristaService.post('Process/uploadNotedExceptionAttachment.js', {
      headers: {
        'Content-Type': 'multipart/form-data'
      },
      params: params,
      data: fd
    });
    return response.data;
  }
}
