import { BaristaService } from '../util/BaristaService';
import { Process } from './Process';

export class ReportingStore {
  private _baristaService: BaristaService;

  /**
   * Creates a new ProcessStore
   */
  constructor(baristaService: BaristaService) {
    this._baristaService = baristaService;
  }

  public async clearReportingSPList(toDelete: number): Promise<number> {
    const response = await this._baristaService.put('Reporting/syncClearList.js', { data: { toDelete: toDelete } });
    return response.data;
  }

  /**
   * Update repository.
   */
  public async updateRepository(processes: Process[]): Promise<{ processInstanceCount: number; processInError: Array<any>[] }> {
    const response = await this._baristaService.put('Reporting/syncBatchProcessesRepository.js', { data: { processes } });
    return response.data;
  }
}
