import React from 'react';
import moment from 'moment';
import startCase from 'lodash/startCase';
import Grid from '@material-ui/core/Grid';
import Paper from '@material-ui/core/Paper';
import TextField from '@material-ui/core/TextField';
import { Process } from '../../ppp';
import AttachmentsDisplay from '../controls/AttachmentsDisplay';
import { MonitoringAndReviewStatus } from '../../ppp/MonitoringAndReviewStatus';

export default class ProcessHeader extends React.Component<ProcessHeaderProps, { fy: string }> {
  constructor(props: ProcessHeaderProps) {
    super(props);
    this.state = {
      fy: ''
    };
  }

  async componentDidMount() {
    this.setState({ fy: this.props.process.fiscalYear || '' });
  }

  async componentDidUpdate() {
    if (this.props.process.fiscalYear && this.props.process.fiscalYear !== this.state.fy) {
      this.setState({ fy: this.props.process.fiscalYear });
    } else {
      //Leave it as is. This is a hack so that when data is saved and the fiscalYear is not included, the FY remains in the header.
    }
  }

  render() {
    const { classes, process, origin } = this.props;

    return (
      <Paper className={classes.paper}>
        <Grid container className={classes.container} spacing={6} justify="space-between">
          <Grid item xs={3}>
            <TextField fullWidth label="Status" className={classes.textField} value={startCase(process.status)} />
          </Grid>
          <Grid item xs={3}>
            <TextField fullWidth label="Initiated On" className={classes.textField} value={moment(process.initiatedOn).format('lll')} />
          </Grid>
          <Grid item xs={3}>
            <TextField fullWidth label="Initiated By" className={classes.textField} value={process.initiatedBy && process.initiatedBy.displayName} />
          </Grid>
          <Grid item xs={3}>
            <TextField
              fullWidth
              label="M&amp;R Status"
              className={classes.textField}
              value={process.monitoringAndReview ? process.monitoringAndReview.status.replace(/([a-z])([A-Z])/g, '$1 $2') : MonitoringAndReviewStatus.NotStarted.replace(/([a-z])([A-Z])/g, '$1 $2')}
            />
          </Grid>
          <Grid item xs={3}>
            <TextField fullWidth label="Process Template" className={classes.textField} value={process.template.title} />
          </Grid>
          <Grid item xs={3}>
            <TextField fullWidth label="Transacton Date" className={classes.textField} value={process.transactionDate ? moment(new Date(process.transactionDate)).format('MM-DD-YYYY') : ''} />
          </Grid>
          <Grid item xs={3}>
            <TextField fullWidth label="Template Version" className={classes.textField} value={process.template.version} />
          </Grid>
          <Grid item xs={3}>
            <TextField fullWidth label="Fiscal Year" className={classes.textField} value={this.state.fy} />
          </Grid>
          {this.props.process && this.props.process.template && this.props.process.template.jobAidAttachments && this.props.process.template.jobAidAttachments.length > 0 && (
            <Grid item xs={12}>
              <AttachmentsDisplay origin={origin} attachments={this.props.process.template.jobAidAttachments} labelText={'Job Aids'} />
            </Grid>
          )}
        </Grid>
      </Paper>
    );
  }
}

interface ProcessHeaderProps {
  classes: {
    paper: string;
    container: string;
    textField: string;
  };
  process: Process;
  origin: string;
}
