import { FieldTemplate } from './FieldTemplate';
import { ActivityTemplate } from './ActivityTemplate';
import { User } from './User';
import { Attachment } from './Attachment';

/**
 * Represents a process template
 */
export interface ProcessTemplate {
  /**
   * Gets the id of the Process Template
   */
  id?: string; //
  /**
   * Gets the title of the Process Template
   */
  title: string;
  /**
   * gets the status of the Process Template
   */
  status?: ProcessTemplateStatus;
  /**
   * Gets the version number of the Process Template
   */
  version?: string;
  /**
   * Gets the comment associated with the version of the process template.
   */
  versionComment?: string;
  /**
   * Gets the description of the Process Template
   */
  description: string;
  /**
   * Gets and sets the field templates associated with the process template.
   */
  fields?: FieldTemplate[];
  /**
   * Gets and sets the activity templates associated with the process template.
   */
  activities?: ActivityTemplate[];
  /**
   * Gets the timestamp of when the process template was created.
   */
  created?: string;
  /**
   * Gets the user who created the process template.
   */
  createdBy?: User;

  /**
   * Gets the most current version number. Used when getting older versions.
   * To detect if template should be readOnly... for example.
   */
  currentVersionNumber?: string;

  /**
   * Gets a value of jobaid attachments
   */
  jobAidAttachments?: Attachment[];
}

export enum ProcessTemplateStatus {
  /**
   * Indicates that the process template has been checked out to a user.
   */
  Checkout = 'Checkout',
  /**
   * Indicates that the process template is in draft.
   */
  Draft = 'Draft',
  /**
   * Indicates that the process template has been published.
   */
  Published = 'Published'
}

export interface ProcessTemplateResponse extends ProcessTemplate {
  id: string;
  status: ProcessTemplateStatus;
  version: string;
  versionComment: string;
  created: string;
  createdBy: User;
}
