import React from 'react';
import autobind from 'autobind-decorator';
import { withStyles } from '@material-ui/core/styles';
import { ProcessTemplateContainer } from '../../ppp/ProcessTemplateContainer';

import Menu from '@material-ui/core/Menu';
import MenuItem from '@material-ui/core/MenuItem';
import ListItemIcon from '@material-ui/core/ListItemIcon';
import ListItemText from '@material-ui/core/ListItemText';

import AddIcon from '@material-ui/icons/AddBox';
import DeleteIcon from '@material-ui/icons/Delete';
import NoteAddIcon from '@material-ui/icons/NoteAdd';

class ProcessTemplateContainerMenu extends React.Component<ProcessTemplateContainerMenuProps> {
  @autobind
  createProcessTemplateClicked(event: React.MouseEvent) {
    const { container, createProcessTemplate, hideProcessTemplateContainerMenu } = this.props;
    hideProcessTemplateContainerMenu();

    createProcessTemplate(container);
  }

  @autobind
  createProcessTemplateContainerClicked(event: React.MouseEvent) {
    const { container, createProcessTemplateContainer, hideProcessTemplateContainerMenu } = this.props;
    hideProcessTemplateContainerMenu();

    createProcessTemplateContainer(container);
  }

  @autobind
  deleteProcessTemplateContainerClicked(event: React.MouseEvent) {
    const { container, deleteProcessTemplateContainer, hideProcessTemplateContainerMenu } = this.props;
    hideProcessTemplateContainerMenu();

    deleteProcessTemplateContainer(container);
  }

  render() {
    const { menuAnchor, container, hideProcessTemplateContainerMenu } = this.props;
    const open = Boolean(menuAnchor);

    return (
      <Menu open={open} anchorEl={menuAnchor} onClose={hideProcessTemplateContainerMenu}>
        <MenuItem onClick={this.createProcessTemplateClicked}>
          <ListItemIcon>
            <NoteAddIcon color="action" />
          </ListItemIcon>
          <ListItemText inset primary="Create Process Template" />
        </MenuItem>
        <MenuItem onClick={this.createProcessTemplateContainerClicked}>
          <ListItemIcon>
            <AddIcon color="action" />
          </ListItemIcon>
          <ListItemText inset primary="Create Subfolder" />
        </MenuItem>
        <MenuItem onClick={this.deleteProcessTemplateContainerClicked} disabled={container.subFolders.length !== 0 || container.processTemplates.length !== 0}>
          <ListItemIcon>
            <DeleteIcon color="secondary" />
          </ListItemIcon>
          <ListItemText inset primary="Delete Folder" />
        </MenuItem>
      </Menu>
    );
  }
}

export default withStyles({})(ProcessTemplateContainerMenu);

export interface ProcessTemplateContainerMenuProps {
  container: ProcessTemplateContainer;
  menuAnchor?: HTMLElement;
  hideProcessTemplateContainerMenu: () => void;
  createProcessTemplate: (container: ProcessTemplateContainer) => void;
  createProcessTemplateContainer: (container: ProcessTemplateContainer) => void;
  deleteProcessTemplateContainer: (container: ProcessTemplateContainer) => void;
}
