import React from 'react';
import autobind from 'autobind-decorator';
import { withStyles, WithStyles } from '@material-ui/core/styles';
import CircularProgress from '@material-ui/core/CircularProgress';
import Dialog from '@material-ui/core/Dialog';
import DialogTitle from '@material-ui/core/DialogTitle';
import DialogContent from '@material-ui/core/DialogContent';
import DialogActions from '@material-ui/core/DialogActions';
import Button from '@material-ui/core/Button';

import { styles } from './styles';
import { AppContext } from '../../AppContext';
import Grid from '@material-ui/core/Grid';
import { TextField, FormControl, FormHelperText } from '@material-ui/core';

class MoveProcessToAnotherFiscalYearDialog extends React.Component<MoveProcessToAnotherFiscalYearDialogProps, MoveProcessToAnotherFiscalYearDialogState> {
  static contextType = AppContext;
  context!: React.ContextType<typeof AppContext>;

  constructor(props: MoveProcessToAnotherFiscalYearDialogProps) {
    super(props);

    this.state = {
      isMoving: false,
      to: 'FY',
      isInvalidEntry: true
    };
  }

  @autobind
  handleOnClose() {
    if (this.props.onClose) {
      this.props.onClose();
    }
  }

  @autobind
  handleChange(event: React.ChangeEvent<HTMLInputElement>) {
    const to = event.target.value.toLocaleUpperCase().slice(0, 4);
    const d = new Date();

    this.setState({
      isInvalidEntry: true,
      to
    });

    if (!to.startsWith('FY')) {
      return;
    }
    if (to.length !== 4) {
      return;
    }

    if (to.length === 4 && !(Number('20' + to.substring(2, 4)) >= 2015 && Number('20' + to.substring(2, 4)) <= d.getFullYear() + 5)) {
      return;
    }

    this.setState({
      isInvalidEntry: false
    });
  }

  @autobind
  async moveProcessToAnotherFiscalYear() {
    const { onClose, id } = this.props;
    const { to } = this.state;
    const { processStore } = this.context;

    if (!id || !this.state.to) {
      if (onClose) {
        onClose('');
      }
      return;
    }

    this.setState({
      isMoving: true
    });

    try {
      const result = await processStore.moveProcessToAnotherFiscalYear(id, to);
      if (onClose) {
        onClose(result, to);
      }
    } catch (error) {
      console.log(error);
    } finally {
      this.setState({
        isMoving: false
      });
    }
  }

  render() {
    const { classes } = this.props;
    const { to, isMoving } = this.state;

    return (
      <Dialog open={this.props.open} onClose={this.handleOnClose} aria-labelledby="confirm-move-process-dialog-title" fullWidth={true} maxWidth="sm">
        <DialogTitle id="confirm-move-process-dialog-title">Move Process to Another Fiscal Year?</DialogTitle>
        <DialogContent>
          <div>
            Are you sure you wish to move this process instance to{' '}
            <strong>
              <i>{to}</i>
            </strong>
          </div>
          <div style={{ marginTop: 15 }}>
            <FormControl>
              <TextField required label="To Fiscal Year" onChange={this.handleChange} value={this.state.to} variant="outlined" error={this.state.isInvalidEntry} />
              <FormHelperText id="standard-weight-helper-text">Fiscal year format is 'FY19', 'FY20' etc. Only 4 characters are allowed.</FormHelperText>
            </FormControl>
          </div>
        </DialogContent>
        <DialogActions>
          {isMoving && (
            <Grid container style={{ margin: '15px' }}>
              <CircularProgress variant="indeterminate" disableShrink className={classes.facebook2} size={24} thickness={4} />
            </Grid>
          )}
          {!isMoving && (
            <>
              <Button onClick={this.handleOnClose} color="primary">
                Cancel
              </Button>
              <Button onClick={this.moveProcessToAnotherFiscalYear} color="primary" disabled={this.state.isInvalidEntry}>
                Move
              </Button>
            </>
          )}
        </DialogActions>
      </Dialog>
    );
  }
}

export default withStyles(styles)(MoveProcessToAnotherFiscalYearDialog);

interface MoveProcessToAnotherFiscalYearDialogProps extends WithStyles<typeof styles> {
  open: boolean;
  onClose?: (msg?: string, to?: string) => void;
  from: string;
  id: string;
}

interface MoveProcessToAnotherFiscalYearDialogState {
  isMoving: boolean;
  to: string;
  isInvalidEntry: boolean;
}
