import React from 'react';
import { observer } from 'mobx-react';
import autobind from 'autobind-decorator';
import { withStyles, WithStyles } from '@material-ui/core/styles';
import cn from 'classnames';
import ListItem from '@material-ui/core/ListItem';
import ListItemIcon from '@material-ui/core/ListItemIcon';
import ListItemText from '@material-ui/core/ListItemText';
import AssignmentIcon from '@material-ui/icons/Assignment';
import MoreVertIcon from '@material-ui/icons/MoreVert';
import InputIcon from '@material-ui/icons/Input';
import EditIcon from '@material-ui/icons/Edit';
import styles from './style';
import { ProcessTemplateContainer } from '../../ppp/ProcessTemplateContainer';
import { ProcessTemplate } from '../../ppp';
import { Button, Tooltip, Checkbox, Box, FormControl, Select } from '@material-ui/core';
import { AppContext } from '../../AppContext';

@observer
export class ProcessTemplateNode extends React.Component<ProcessTemplateNodeProps, { versions: Array<string>; versionDisabled: boolean }> {
  static contextType = AppContext;
  context!: React.ContextType<typeof AppContext>;

  constructor(props: ProcessTemplateNodeProps) {
    super(props);
    this.state = {
      versions: [],
      versionDisabled: false
    };
  }

  @autobind
  navigateToProcessTemplate(event: React.MouseEvent<HTMLElement>) {
    event.stopPropagation();
    const { processTemplate, navigateToProcessTemplate } = this.props;
    if (!processTemplate.id) {
      return;
    }

    navigateToProcessTemplate(processTemplate.id);
  }

  @autobind
  initiateProcess() {
    const { container, processTemplate, initiateProcess } = this.props;
    initiateProcess(container, processTemplate);
  }

  @autobind
  getProcessTemplateListItemStyle(): React.CSSProperties {
    const result: React.CSSProperties = {
      // paddingLeft: 0
    };

    if (this.props.selectedProcessTemplateId === this.props.processTemplate.id) {
      result.backgroundColor = '#FFF3E0';
    }

    return result;
  }

  @autobind
  selectProcessTemplate() {
    this.props.selectProcessTemplate(this.props.processTemplate.id);
  }

  @autobind
  async setVersionOptions(id: string | undefined) {
    if (id) {
      this.setState({ versionDisabled: true });
      const versions = await this.context.processTemplateStore.getProcessTemplateVersionHistoryById(id);
      const versionsToShow: Array<string> = [];
      versions.forEach(ver => {
        if (ver.isCurrentVersion === false) {
          versionsToShow.push(ver.version);
        }
      });
      this.setState({ versions: versionsToShow, versionDisabled: false });
    }
  }

  render() {
    const { classes, processTemplate } = this.props;

    if (!processTemplate) {
      return;
    }

    return (
      <ListItem id={`process-template-${processTemplate.id}`} dense button className={classes.treeNode} style={this.getProcessTemplateListItemStyle()} onClick={this.selectProcessTemplate}>
        <ListItemIcon classes={{ root: classes.treeNodeListItemIcon }}>
          <Box display="flex" alignItems={'center'}>
            {this.props.exportMode === 'Active' && (
              <>
                <Checkbox
                  edge="start"
                  onChange={e => {
                    let version = undefined;
                    if (e.target && e.target.checked === true) {
                      this.setVersionOptions(processTemplate.id);
                      version = this.props.selectedVersionNumer || processTemplate.version;
                    }
                    return this.props.onSelectProcessTemplateForExport(processTemplate.id, version);
                  }}
                  checked={this.props.selectedVersionNumer && this.props.selectedVersionNumer.length > 0 ? true : false}
                  tabIndex={-1}
                  disableRipple
                  inputProps={{ 'aria-labelledby': 'Select to export' }}
                />
                <FormControl variant="outlined" className={classes.formControl}>
                  <Select
                    disabled={this.props.selectedVersionNumer && this.props.selectedVersionNumer.length > 0 ? false : true || this.state.versionDisabled}
                    native
                    value={this.props.selectedVersionNumer}
                    onChange={e => {
                      if (e.target && e.target.value) {
                        this.props.onSelectProcessTemplateForExport(processTemplate.id, e.target.value as string | undefined);
                      }
                    }}
                    inputProps={{
                      name: 'version',
                      id: 'version-simple'
                    }}
                  >
                    <option style={{ fontWeight: 'bold' }} value={processTemplate.version || ''}>{`${processTemplate.version}*`}</option>
                    {this.state.versions &&
                      this.state.versions.map(i => (
                        <option key={i} value={i}>
                          {i}
                        </option>
                      ))}
                  </Select>
                </FormControl>
              </>
            )}
            <AssignmentIcon />
          </Box>
        </ListItemIcon>
        <ListItemText
          onClick={this.navigateToProcessTemplate}
          inset
          primary={processTemplate.title}
          className={cn(classes.treeText, classes.processTemplateNodeText, {
            [classes.treeTextFlex]: false
          })}
        />
        <Tooltip title="Show more menu items">
          <span>
            <Button className={classes.listitemButton} size="small" onClick={e => this.props.showProcessTemplateMenu(e, this.props.container, this.props.processTemplate)}>
              <MoreVertIcon />
              {/* actions */}
            </Button>
          </span>
        </Tooltip>
        <Tooltip title="Edit/View Process Template">
          <Button className={classes.listitemButton} size="small" aria-label="Edit Template" onClick={this.navigateToProcessTemplate}>
            <EditIcon />
            {/* edit */}
          </Button>
        </Tooltip>
        <Tooltip title="Initiate Process">
          <span>
            <Button color="primary" size="small" aria-label="Initiate Process" onClick={this.initiateProcess}>
              <InputIcon />
              {/* initiate */}
            </Button>
          </span>
        </Tooltip>
      </ListItem>
    );
  }
}

export default withStyles(styles)(ProcessTemplateNode);

export interface ProcessTemplateNodeProps extends WithStyles<typeof styles> {
  container: ProcessTemplateContainer;
  processTemplate: ProcessTemplate;
  showProcessTemplateMenu: (event: React.MouseEvent<HTMLElement>, container: ProcessTemplateContainer, processTemplate: ProcessTemplate) => void;
  navigateToProcessTemplate: (processTemplateId: string) => void;
  initiateProcess: (container: ProcessTemplateContainer, processTemplate: ProcessTemplate) => void;
  selectedProcessTemplateId?: string;
  selectProcessTemplate: (processTemplateId?: string) => void;
  onSelectProcessTemplateForExport: (processTemplateId?: string, version?: string) => void;
  exportMode: string;
  selectedVersionNumer?: string | undefined;
}
