import React from 'react';
import find from 'lodash/find';
import { withStyles, WithStyles } from '@material-ui/core/styles';
import autobind from 'autobind-decorator';
import CircularProgress from '@material-ui/core/CircularProgress';
import Dialog from '@material-ui/core/Dialog';
import DialogTitle from '@material-ui/core/DialogTitle';
import DialogContent from '@material-ui/core/DialogContent';
import DialogActions from '@material-ui/core/DialogActions';
import TextField from '@material-ui/core/TextField';
import Button from '@material-ui/core/Button';

import { styles } from './styles';
import { AppContext } from '../../AppContext';
import { ProcessTemplate } from '../../ppp';
import { ProcessTemplateContainer } from '../../ppp/ProcessTemplateContainer';
import Grid from '@material-ui/core/Grid';

class CreateProcessTemplateDialog extends React.Component<CreateProcessTemplateDialogProps, CreateProcessTemplateDialogState> {
  static contextType = AppContext;
  context!: React.ContextType<typeof AppContext>;

  constructor(props: CreateProcessTemplateDialogProps) {
    super(props);

    this.state = {
      isCreating: false,
      newProcessTemplate: {
        title: '',
        description: ''
      }
    };
  }

  @autobind
  handleOnClose() {
    if (this.props.onClose) {
      this.props.onClose();
    }
  }

  @autobind
  handleNewProcessTemplateTitleChange(event: React.ChangeEvent<HTMLInputElement>) {
    const { newProcessTemplate } = this.state;
    newProcessTemplate.title = event.currentTarget.value;
    this.setState({
      newProcessTemplate
    });
  }

  @autobind
  async createProcessTemplate() {
    const { processTemplateContainer, onClose } = this.props;
    const { newProcessTemplate } = this.state;
    const { processTemplateStore } = this.context;

    this.setState({
      isCreating: true
    });

    const result = await processTemplateStore.saveProcessTemplateAsDraft(processTemplateContainer.path, newProcessTemplate);

    this.setState(
      {
        isCreating: false
      },
      () => {
        if (onClose) {
          onClose(result);
        }
      }
    );
  }

  render() {
    const { classes, processTemplateContainer } = this.props;
    const { isCreating, newProcessTemplate } = this.state;

    let isDuplicateTitle = false;
    if (newProcessTemplate && find(processTemplateContainer.processTemplates.slice(), { title: newProcessTemplate.title })) {
      isDuplicateTitle = true;
    }

    return (
      <Dialog open={this.props.open} onClose={this.handleOnClose} aria-labelledby="new-process-template-dialog-title" fullWidth={true} maxWidth="xs">
        <DialogTitle id="new-process-template-dialog-title">New Process Template</DialogTitle>
        <DialogContent>
          <TextField
            autoFocus
            error={isDuplicateTitle}
            label="New Process Template Name"
            style={{ width: 300 }}
            value={newProcessTemplate.title}
            onChange={this.handleNewProcessTemplateTitleChange}
            helperText={isDuplicateTitle ? <i>A Process Template with this name already exists.</i> : undefined}
            margin="normal"
          />
        </DialogContent>
        <DialogActions>
          {isCreating && (
            <Grid container style={{ margin: '15px' }}>
              <CircularProgress variant="indeterminate" disableShrink className={classes.facebook2} size={24} thickness={4} />
            </Grid>
          )}
          {!isCreating && (
            <>
              <Button onClick={this.handleOnClose} color="primary">
                Cancel
              </Button>
              <Button onClick={this.createProcessTemplate} color="primary" disabled={isDuplicateTitle}>
                Create
              </Button>
            </>
          )}
        </DialogActions>
      </Dialog>
    );
  }
}

export default withStyles(styles)(CreateProcessTemplateDialog);

interface CreateProcessTemplateDialogProps extends WithStyles<typeof styles> {
  open: boolean;
  onClose?: (copiedProcessTemplate?: ProcessTemplate) => void;
  processTemplateContainer: ProcessTemplateContainer;
}

interface CreateProcessTemplateDialogState {
  isCreating: boolean;
  newProcessTemplate: ProcessTemplate;
}
