export function getFiscalYear(date?: Date) {
  if (!date) {
    date = new Date();
  }

  if (date.getMonth() <= 8) {
    return (
      'FY' +
      date
        .getFullYear()
        .toString()
        .substring(2)
    );
  }

  return 'FY' + (date.getFullYear() + 1).toString().substring(2);
}
