import React from 'react';
import autobind from 'autobind-decorator';

import { withStyles, WithStyles } from '@material-ui/core/styles';
import CircularProgress from '@material-ui/core/CircularProgress';
import Dialog from '@material-ui/core/Dialog';
import DialogTitle from '@material-ui/core/DialogTitle';
import DialogContent from '@material-ui/core/DialogContent';
import DialogActions from '@material-ui/core/DialogActions';
import Button from '@material-ui/core/Button';

import { styles } from './styles';
import { AppContext } from '../../AppContext';
import { ActivityTemplate } from '../../ppp';
import Grid from '@material-ui/core/Grid';
import { truncate } from 'lodash';

class DeleteActivityTemplateDialog extends React.Component<DeleteActivityTemplateDialogProps, DeleteActivityTemplateDialogState> {
  static contextType = AppContext;
  context!: React.ContextType<typeof AppContext>;

  constructor(props: DeleteActivityTemplateDialogProps) {
    super(props);

    this.state = {
      isDeleting: false
    };
  }

  @autobind
  handleOnClose() {
    if (this.props.onClose) {
      this.setState({
        isDeleting: false
      });
      this.props.onClose(false);
    }
  }

  get activitySummaryText(): string {
    const { activityTemplate } = this.props;
    if (activityTemplate) {
      const text = new DOMParser().parseFromString(activityTemplate.description, 'text/html');
      if (text.body && text.body.textContent) {
        return truncate((text.body.textContent || '').trim(), { length: 100 });
      }
    }
    return '[ Not Available ]';
  }

  @autobind
  async handleDelete() {
    const { onClose, activityTemplate, index } = this.props;

    if (activityTemplate === undefined || !activityTemplate.id) {
      if (onClose) {
        onClose(false);
      }
      return;
    }

    if (activityTemplate) {
      await this.setState({
        isDeleting: true
      });
      await new Promise(resolve => setTimeout(resolve, 500)); // give time to spin the wheel
      try {
        this.props.onDelete(activityTemplate, index);
      } catch (error) {
        // TODO: just   delete error if any. Make sure the popup closes and isDeleting reset
      }
      if (onClose) {
        onClose(false);
      }
      this.setState({
        isDeleting: false
      });
    }
  }

  render() {
    const { classes, activityTemplate } = this.props;
    const { isDeleting } = this.state;

    return (
      <Dialog open={this.props.open} onClose={this.handleOnClose} aria-labelledby="confirm-delete-Activity-dialog-title" fullWidth={true} maxWidth="xs">
        <DialogTitle id="confirm-delete-activity-dialog-title">Delete Activity Template?</DialogTitle>
        <DialogContent>
          {activityTemplate && (
            <div>
              Are you sure you wish to delete the activity titled{' '}
              <strong>
                <i>{this.activitySummaryText}</i>
              </strong>
            </div>
          )}
        </DialogContent>
        <DialogActions>
          {isDeleting && (
            <Grid container style={{ marginLeft: '50%', marginBottom: '15px' }}>
              <CircularProgress variant="indeterminate" disableShrink className={classes.facebook2} size={24} thickness={4} />
            </Grid>
          )}
          {!isDeleting && (
            <>
              <Button onClick={this.handleOnClose} color="primary" tabIndex={1}>
                Cancel
              </Button>
              <Button onClick={this.handleDelete} color="primary" tabIndex={2} disabled={isDeleting}>
                Delete
              </Button>
            </>
          )}
        </DialogActions>
      </Dialog>
    );
  }
}

export default withStyles(styles)(DeleteActivityTemplateDialog);

interface DeleteActivityTemplateDialogProps extends WithStyles<typeof styles> {
  open: boolean;
  onClose?: (deleted: boolean) => void;
  onDelete: (activityTemplate: ActivityTemplate, index: number) => void;
  activityTemplate?: ActivityTemplate;
  index: number;
}

interface DeleteActivityTemplateDialogState {
  isDeleting: boolean;
}
