import React from 'react';
import { Paper, Popper, MenuItem, MenuList, ClickAwayListener, ButtonGroup, Button, Grow, Divider, Typography, ListItemSecondaryAction } from '@material-ui/core';
import autobind from 'autobind-decorator';
import ArrowDropDownIcon from '@material-ui/icons/ArrowDropDown';
import AccountIcon from '@material-ui/icons/AccountCircle';
import { Theme, createStyles, withStyles, WithStyles } from '@material-ui/core';
import IconButton from '@material-ui/core/IconButton';
import { AppContext } from '../../AppContext';
import DeleteIcon from '@material-ui/icons/Delete';
import DoneIcon from '@material-ui/icons/Done';
import SyncIcon from '@material-ui/icons/Sync';
import CircularProgress from '@material-ui/core/CircularProgress';
import chunk from 'lodash/chunk';
import { observer } from 'mobx-react';
import { withRouter, RouteComponentProps } from 'react-router-dom';
import { green } from '@material-ui/core/colors';
import ConfirmationDialog from '../dialogs/ConfirmationDialog';

export const styles = (theme: Theme) =>
  createStyles({
    profileIcon: {
      minWidth: 40,
      minHeight: 40,
      color: theme.palette.grey.A200,
      marginRight: theme.spacing()
    },
    snackbarError: {
      backgroundColor: theme.palette.error.dark
    },
    snackbarInfo: { backgroundColor: green[800] },
    close: {}
  });

@observer
class AppBarProfile extends React.Component<AppBarProfileProps, AppBarProfileState> {
  static contextType = AppContext;
  context!: React.ContextType<typeof AppContext>;

  constructor(props: AppBarProfileProps) {
    super(props);
    this.state = {
      open: false,
      anchorEl: null,
      isSynching: false,
      progress: 0,
      syncInfo: { open: false, text: '', isError: false }
    };
  }

  @autobind
  async componentDidUpdate(prevProps: AppBarProfileProps) {
    // TODO:
  }

  @autobind
  handleMenuItemClick(event: React.MouseEvent<HTMLLIElement, MouseEvent>, index: number, id: string) {
    this.setState({ open: false });
    this.props.onViewChange(id);
  }

  @autobind
  handleGlobalMenuItemClick(event: React.MouseEvent<HTMLLIElement, MouseEvent>, index: number, id: string) {
    this.setState({ open: false });
    this.props.onViewChange(id);
  }

  @autobind
  handleToggle(event: React.MouseEvent<HTMLButtonElement>) {
    const anchorEl = event.currentTarget.parentElement;
    if (anchorEl) {
      this.setState((state, props) => {
        return { open: !state.open, anchorEl };
      });
    }
  }

  @autobind
  getTitle(currentUser: any): string {
    if (currentUser && currentUser.Title) {
      return currentUser.Title;
    }
    return '';
  }

  @autobind
  async handleDelete(event: React.MouseEvent<HTMLButtonElement, MouseEvent>, index: number, id: string, isGlobal: boolean) {
    if (isGlobal === false && index === 0) {
      alert('You cannot delete this view');
    } else {
      if (confirm('Do you want to delete this view?')) {
        const view = this.context.personalizationStore.filterViews.filter(i => i.id === id);
        if (view && view.length > 0) {
          await this.context.personalizationStore.deleteFilterById(view[0].id);
          if (this.context.personalizationStore.selectedViewFilter && this.context.personalizationStore.selectedViewFilter.id === id) {
            this.context.personalizationStore.setDefaultFilter();
            this.props.history.push(`/processes/`);
          }
          await this.context.personalizationStore.loadViews();
        }
      }
    }
  }

  @autobind
  handleClose(event: React.MouseEvent<Document, MouseEvent>) {
    this.setState({ open: false, anchorEl: null });
  }

  increaseProgress(by: number, setTo?: number) {
    this.setState((state: AppBarProfileState, props: AppBarProfileProps) => {
      return {
        progress: setTo && setTo > 0 ? setTo : state.progress + by
      };
    });
  }

  @autobind
  async handleSyncRepository() {
    let totalCount = 0;
    let totalErrorCount = 0;
    const deleteBatchSize = 50;
    const procChunkSize = 30;
    if (this.state.isSynching === false) {
      this.setState({ isSynching: true, progress: 5, syncInfo: { open: false, text: '', isError: false } });
      try {
        // *** STEP ONE ***
        await this.context.spContext.buildReportingItemList();
        this.increaseProgress(0, 10);

        // *** STEP TWO ***
        let deletedItems = await this.context.reportingStore.clearReportingSPList(deleteBatchSize);
        while (deletedItems > 0) {
          // deleting in batches
          deletedItems = await this.context.reportingStore.clearReportingSPList(deleteBatchSize);
          this.increaseProgress(1, undefined);
        }
        this.setState({ progress: 35 });

        if (deletedItems === 0) {
          // *** STEP THREE ***
          const processes = await this.context.processStore.listAllProcesses();
          this.increaseProgress(0, 50);

          // *** STEP FOUR ***
          const chunks = chunk(processes, procChunkSize);
          for (let i = 0; i < chunks.length; i++) {
            const r = await this.context.reportingStore.updateRepository(chunks[i]);
            if (r) {
              totalCount += r.processInstanceCount;
              if (r.processInError) {
                totalErrorCount += r.processInError.length;
                console.log(`Batch ${i + 1} errors: `, r.processInError);
              }
            }
            if (this.state.progress < 100) {
              const progressUnit = Math.ceil(50 / chunks.length); // 50 is the  50% progress remaining to increase.
              this.increaseProgress(progressUnit, undefined);
            }
          }
          this.increaseProgress(0, 100);
        }

        this.setState({
          isSynching: false,
          syncInfo: {
            open: true,
            text: `The Data Synchronization operation completed. 
          ${totalCount} processes were synced with ${totalErrorCount} errors. `,
            isError: false
          }
        });
      } catch (error) {
        console.log('There was an error with the Reporting Data Synchronization', JSON.stringify(error));
        this.setState({ isSynching: false, syncInfo: { open: true, text: 'There was an error Reporting Data Synchronization.', isError: true } });
      }
    }
  }

  @autobind
  async handleInfoClose() {
    this.setState({ syncInfo: { open: false, text: '', isError: false } });
  }

  @autobind
  getHyperlink() {
    return (
      <Paper>
        <Button fullWidth color="primary" onClick={this.handleIButtonClick}>
          Go To Sharepoint Reporting List
        </Button>
      </Paper>
    );
  }

  @autobind
  async handleIButtonClick() {
    const url = (await this.context.spContext.getSharePointOrigin()) + `/Lists/${this.context.spContext.getReportingListViewName()}.aspx`;
    window.open(url, '_blank');
  }

  render() {
    const { open, anchorEl } = this.state;
    const { currentUser } = this.props;
    const { filterViews, selectedViewFilter } = this.context.personalizationStore;
    return (
      <>
        {currentUser && (
          <>
            <AccountIcon className={this.props.classes.profileIcon} />
            <ButtonGroup variant="contained" aria-label="Split button">
              <Button title="Logged User" onClick={this.handleToggle}>
                {this.getTitle(currentUser)}
              </Button>
              <Button variant="contained" size="small" aria-owns={open ? 'menu-list-grow' : undefined} aria-haspopup="true" onClick={this.handleToggle}>
                <ArrowDropDownIcon />
              </Button>
            </ButtonGroup>
          </>
        )}
        <Popper style={{ width: 400 }} open={open} anchorEl={anchorEl} transition disablePortal>
          {({ TransitionProps, placement }) => (
            <ClickAwayListener onClickAway={this.handleClose}>
              <Grow
                {...TransitionProps}
                style={{
                  transformOrigin: placement === 'bottom' ? 'center top' : 'center bottom'
                }}
              >
                <Paper id="menu-list-grow">
                  <MenuList>
                    {filterViews
                      .filter((i, idx) => i.global === false)
                      .map((option, index) => (
                        <MenuItem key={`l${option.id}`} onClick={event => this.handleMenuItemClick(event, index, option.id)}>
                          {selectedViewFilter && selectedViewFilter.id === option.id && <DoneIcon style={{ paddingRight: 5 }} />}
                          {option.name}
                          {index !== 0 && (
                            <ListItemSecondaryAction>
                              <IconButton edge="end" aria-label="Delete View" key={`l${option.id}-${index}`} onClick={event => this.handleDelete(event, index, option.id, false)}>
                                <DeleteIcon />
                              </IconButton>
                            </ListItemSecondaryAction>
                          )}
                        </MenuItem>
                      ))}
                  </MenuList>
                  <Divider />
                  <Typography style={{ paddingLeft: 5, paddingTop: 5, fontWeight: 600 }}>Global Views/Searches</Typography>
                  <MenuList>
                    {filterViews
                      .filter((i, idx) => i.global === true)
                      .map((option, index) => (
                        <MenuItem key={`g${option.id}`} id={option.id} onClick={event => this.handleGlobalMenuItemClick(event, index, option.id)}>
                          {selectedViewFilter && selectedViewFilter.id === option.id && <DoneIcon style={{ paddingRight: 5 }} />}
                          {option.name}
                          {this.context.spContext.isAdministrator(currentUser) && (
                            <ListItemSecondaryAction>
                              <IconButton edge="end" aria-label="Delete View" key={`l${option.id}-${index}`} onClick={event => this.handleDelete(event, index, option.id, true)}>
                                <DeleteIcon />
                              </IconButton>
                            </ListItemSecondaryAction>
                          )}
                        </MenuItem>
                      ))}
                  </MenuList>
                  {this.context.spContext.isAdministrator(currentUser) && (
                    <>
                      <Divider />
                      <Typography style={{ paddingLeft: 5, paddingTop: 5, fontWeight: 600 }}>Admin Functionality</Typography>
                      <MenuList>
                        <MenuItem key={0} onClick={event => this.handleSyncRepository()} disabled={this.state.isSynching}>
                          <>
                            {!this.state.isSynching && <SyncIcon style={{ paddingRight: 5 }} />}
                            {this.state.isSynching && <CircularProgress variant="static" value={this.state.progress} size={30} style={{ padding: 5 }} />}
                          </>
                          Sync Reporting Data
                        </MenuItem>
                      </MenuList>
                    </>
                  )}
                </Paper>
              </Grow>
            </ClickAwayListener>
          )}
        </Popper>

        {this.state.syncInfo && this.state.syncInfo.isError && (
          <ConfirmationDialog message={this.state.syncInfo.text} title={'Error synching the Reporting List '} open={this.state.syncInfo.open} onClose={this.handleInfoClose} />
        )}

        {this.state.syncInfo && this.state.syncInfo.isError === false && (
          <ConfirmationDialog message={this.state.syncInfo.text} title={'OFS PPP Reporting List '} children={this.getHyperlink()} open={this.state.syncInfo.open} onClose={this.handleInfoClose} />
        )}
      </>
    );
  }
}

interface AppBarProfileProps extends RouteComponentProps, WithStyles {
  currentUser: any;
  onViewChange(id: string): void;
}

interface AppBarProfileState {
  open: boolean;
  anchorEl: any;
  isSynching: boolean;
  progress: number;
  syncInfo: { open: boolean; text: string; isError: boolean };
}

export default withRouter(withStyles(styles)(AppBarProfile));
