import { ProcessTemplateStore, ProcessTemplate } from '.';
import { ProcessTemplateContainer } from './ProcessTemplateContainer';
import { observable, action, flow } from 'mobx';
import find from 'lodash/find';
import { CancelTokenSource } from 'axios';

export class ProcessTemplateContainerStore {
  private _processTemplateStore: ProcessTemplateStore;
  initialVisibleDepth: number;
  @observable selectedProcessTemplateId?: string;
  @observable isLoading: boolean = true;
  @observable flatContainers: ProcessTemplateContainer[] = [];

  constructor(processTemplateStore: ProcessTemplateStore, initialVisibleDepth?: number) {
    this._processTemplateStore = processTemplateStore;
    this.initialVisibleDepth = initialVisibleDepth || 3;
  }

  private convertToTree(data: { [path: string]: ProcessTemplate[] }): ProcessTemplateContainer[] {
    // Build the node structure
    const rootNode = observable({
      index: 0,
      name: '/',
      path: '/',
      depth: 0,
      subFolders: [],
      processTemplates: data['/'],
      isLoading: false,
      showChildren: true
    });

    const flatContainers = [rootNode];
    const keys = Object.keys(data);
    for (let path of keys) {
      if (path === '/') {
        continue;
      }

      const processTemplates = data[path];
      const index = keys.indexOf(path);
      path = path.substring(1);
      path = path.substring(0, path.length - 1);

      this.buildNodeRecursive(rootNode, path, index, path.split('/'), 0, processTemplates, 1, flatContainers);
    }

    return flatContainers;
  }

  private buildNodeRecursive(
    node: ProcessTemplateContainer,
    path: string,
    sourceIndex: number,
    children: string[],
    idx: number,
    processTemplates: ProcessTemplate[],
    depth: number,
    flatContainers: ProcessTemplateContainer[]
  ) {
    if (idx < children.length) {
      let item = children[idx];
      let dir = node.subFolders.find(child => child.name === item);
      if (!dir) {
        dir = observable({
          index: sourceIndex,
          name: item,
          path: path,
          depth,
          subFolders: [],
          processTemplates: processTemplates,
          isLoading: false,
          showChildren: depth < this.initialVisibleDepth,
          parentIndex: node.index
          // parent: node
        });
        flatContainers.push(dir);
        node.subFolders.push(dir);
      }
      this.buildNodeRecursive(dir, path, sourceIndex, children, idx + 1, processTemplates, depth + 1, flatContainers);
    }
  }

  @action.bound
  public listAllProcessTemplates = flow(function*(this: ProcessTemplateContainerStore, path?: string, cancelTokenCallback?: (canceler: CancelTokenSource) => void) {
    this.isLoading = true;
    const processTemplates = yield this._processTemplateStore.listAllProcessTemplates(path, cancelTokenCallback);
    this.flatContainers = this.convertToTree(processTemplates);

    const traverseUp = (node: ProcessTemplateContainer, fn: (ptc: ProcessTemplateContainer) => void) => {
      fn(node);
      if (node.parentIndex) {
        const parentNode = this.flatContainers[node.parentIndex];
        traverseUp(parentNode, fn);
      }
    };

    if (this.selectedProcessTemplateId) {
      for (const container of this.flatContainers) {
        if (find(container.processTemplates, { id: this.selectedProcessTemplateId })) {
          traverseUp(container, ptc => (ptc.showChildren = true));
        }
      }
    }

    this.isLoading = false;
  });

  @action.bound
  public listProcessTemplatesInContainer = flow(function*(this: ProcessTemplateContainerStore, container: ProcessTemplateContainer) {
    container.isLoading = true;
    container.processTemplates = observable(yield this._processTemplateStore.listProcessTemplatesInContainer(container.path));
    container.isLoading = false;
  });

  @action.bound
  public selectProcessTemplate(processTemplateId?: string) {
    this.selectedProcessTemplateId = processTemplateId;
  }

  @action.bound
  async toggleContainerShowChildren(container: ProcessTemplateContainer) {
    if (!container.showChildren) {
      this.listProcessTemplatesInContainer(container);
      container.showChildren = true;
    } else {
      container.showChildren = !!!container.showChildren;
    }
  }
}
