import React from 'react';
import { Theme, createStyles, withStyles, WithStyles } from '@material-ui/core/styles';
import Typography from '@material-ui/core/Typography';
import { TextField, Paper, Divider, IconButton, Tooltip } from '@material-ui/core';
import DoneIcon from '@material-ui/icons/Done';
import EditIcon from '@material-ui/icons/Edit';
import ClearIcon from '@material-ui/icons/Clear';

const styles = (theme: Theme) =>
  createStyles({
    textField: {
      marginTop: 5,
      marginBottom: 5
    }
  });

class DisplayEditableField extends React.Component<DisplayEditableFieldProp, DisplayEditableFieldState> {
  constructor(props: DisplayEditableFieldProp) {
    super(props);
    this.state = {
      isEditField: false,
      isError: false,
      fieldBuffer: ''
    };
  }

  render() {
    const { classes, field, label, title, isLoading, readOnly, size } = this.props;
    const { fieldBuffer: titleBuffer, isError } = this.state;
    return (
      <>
        {!this.state.isEditField && (
          <Paper
            component="div"
            style={{
              display: 'flex',
              alignItems: 'center',
              paddingRight: '40px',
              backgroundColor: 'inherit'
            }}
            elevation={0}
          >
            {size && size === 'small' && (
              <Typography variant={'body1'} component="span" color="inherit" noWrap className={classes.textField}>
                {title} {field}
              </Typography>
            )}
            {!size && (
              <Typography variant={'h5'} component="span" color="inherit" noWrap className={classes.textField}>
                {title} {field}
              </Typography>
            )}

            {!isLoading && !readOnly && (
              <Tooltip title={label ? 'Edit ' + label : ''}>
                <span>
                  <IconButton
                    aria-label="Edit"
                    disabled={this.state.isEditField}
                    onClick={(event: React.MouseEvent) => {
                      event.preventDefault();
                      event.stopPropagation();
                      this.setState({ fieldBuffer: field, isEditField: true });
                    }}
                  >
                    <EditIcon />
                  </IconButton>
                </span>
              </Tooltip>
            )}
          </Paper>
        )}
        {this.state.isEditField && (
          <Paper
            style={{
              display: 'flex',
              alignItems: 'center',
              width: '100%',
              padding: '2px 4px'
            }}
            square={false}
          >
            <TextField
              autoFocus
              fullWidth
              error={isError}
              label={label}
              id="renameProcessTemplateTitle"
              value={this.state.fieldBuffer}
              onChange={(event: React.ChangeEvent<HTMLInputElement>) => {
                if (readOnly === true) {
                  return;
                }
                const val = event.target.value;
                this.setState({ fieldBuffer: val });
                if (val.length === 0) {
                  this.setState({ isError: true });
                } else {
                  this.setState({ isError: false });
                }
              }}
            />
            <Divider style={{ width: 1, margin: 4 }} />
            <IconButton
              disabled={isError}
              aria-label="Save"
              onClick={(event: React.MouseEvent) => {
                event.preventDefault();
                event.stopPropagation();
                if (readOnly === true) {
                  return;
                }
                if (titleBuffer && titleBuffer.length > 0) {
                  this.setState({ isEditField: false });
                  this.props.onSave(titleBuffer);
                } else {
                  this.setState({ isError: true });
                }
              }}
            >
              <DoneIcon />
            </IconButton>
            <IconButton
              aria-label="Cancel"
              onClick={(event: React.MouseEvent) => {
                event.preventDefault();
                event.stopPropagation();
                if (readOnly === true) {
                  return;
                }
                this.setState({ fieldBuffer: '', isEditField: false, isError: false });
              }}
            >
              <ClearIcon />
            </IconButton>
          </Paper>
        )}
      </>
    );
  }
}
export default withStyles(styles)(DisplayEditableField);

interface DisplayEditableFieldState {
  isEditField: boolean;
  fieldBuffer: string;
  isError: boolean;
}

interface DisplayEditableFieldProp extends WithStyles {
  field: string;
  isLoading: boolean;
  readOnly: boolean;
  label?: string;
  title?: string;
  size?: string | undefined;
  onSave(value: string): void;
}
