import React from 'react';
import autobind from 'autobind-decorator';
import orderBy from 'lodash/orderBy';
import Avatar from '@material-ui/core/Avatar';
import Chip from '@material-ui/core/Chip';
import { Attachment } from '../../ppp';
import { Theme, createStyles, withStyles, WithStyles, Grid, Card, CardHeader, CardContent } from '@material-ui/core';
import { grey } from '@material-ui/core/colors';

const styles = (theme: Theme) =>
  createStyles({
    attachmentChip: {
      height: '24px',
      margin: '2px'
    },
    attachmentChipLabel: {
      paddingLeft: '0'
    },
    attachmentAvatar: {
      margin: '5px',
      height: '15px',
      width: '15px'
    }
  });

class AttachmentsDisplay extends React.Component<AttachmentsDisplayProps> {
  @autobind
  handleAttachmentClicked(attachment: Attachment) {
    window.open(this.props.origin + attachment.serverRelativeUrl, '_blank');
  }

  render() {
    const { classes, attachments, origin, labelText } = this.props;
    return (
      <Grid item xs={12} style={{ margin: 0, padding: 0, backgroundColor: 'inherit' }}>
        <Card style={{ display: 'flex', margin: 0, padding: 0, boxShadow: 'none', backgroundColor: 'inherit', borderColor: grey[100], borderWidth: '1px', borderStyle: 'solid' }}>
          <CardHeader title={labelText} style={{ padding: 2, paddingRight: 5 }} titleTypographyProps={{ variant: 'body1' }} />
          <CardContent style={{ padding: 0 }}>
            {attachments &&
              orderBy(attachments, ['fileName'], ['asc']).map(attachment => (
                <Chip
                  classes={{ label: classes.attachmentChipLabel, deleteIcon: classes.attachmentChipDeleteIcon }}
                  className={classes.attachmentChip}
                  key={attachment.fileName}
                  avatar={<Avatar className={classes.attachmentAvatar} src={`${origin}${attachment.fileIconUrl || '/_layouts/images/ICGEN.GIF'}`} />}
                  label={attachment.fileName}
                  color="primary"
                  onClick={() => this.handleAttachmentClicked(attachment)}
                />
              ))}
          </CardContent>
        </Card>
      </Grid>
    );
  }
}

export default withStyles(styles)(AttachmentsDisplay);

interface AttachmentsDisplayProps extends WithStyles {
  attachments?: Attachment[];
  origin: string;
  labelText: string;
}
