import React from 'react';
import autobind from 'autobind-decorator';
import { withStyles } from '@material-ui/core/styles';
import { ProcessTemplateContainer } from '../../ppp/ProcessTemplateContainer';
import { ProcessTemplate } from '../../ppp';

import Menu from '@material-ui/core/Menu';
import MenuItem from '@material-ui/core/MenuItem';
import ListItemIcon from '@material-ui/core/ListItemIcon';
import ListItemText from '@material-ui/core/ListItemText';

import DeleteIcon from '@material-ui/icons/Delete';
import FileCopyIcon from '@material-ui/icons/FileCopy';
import InputIcon from '@material-ui/icons/Input';
import EditIcon from '@material-ui/icons/Edit';
import HistoryIcon from '@material-ui/icons/History';

class ProcessTemplateMenu extends React.PureComponent<ProcessTemplateMenuProps> {
  @autobind
  copyProcessTemplateClicked(event: React.MouseEvent) {
    const { container, processTemplate, copyProcessTemplate, hideProcessTemplateMenu } = this.props;
    hideProcessTemplateMenu();

    copyProcessTemplate(container, processTemplate);
  }

  @autobind
  deleteProcessTemplateClicked(event: React.MouseEvent) {
    const { container, processTemplate, deleteProcessTemplate, hideProcessTemplateMenu } = this.props;
    hideProcessTemplateMenu();

    deleteProcessTemplate(container, processTemplate);
  }

  @autobind
  editProcessTemplateClicked(event: React.MouseEvent) {
    const { processTemplate, navigateToProcessTemplate, hideProcessTemplateMenu } = this.props;
    hideProcessTemplateMenu();

    if (!processTemplate.id) {
      return;
    }

    navigateToProcessTemplate(processTemplate.id);
  }

  @autobind
  initiateProcessClicked(event: React.MouseEvent) {
    const { container, processTemplate, initiateProcess, hideProcessTemplateMenu } = this.props;
    hideProcessTemplateMenu();

    initiateProcess(container, processTemplate);
  }

  @autobind
  showVersionHistoryClicked(event: React.MouseEvent) {
    const { container, processTemplate, showVersionHistory, hideProcessTemplateMenu } = this.props;
    hideProcessTemplateMenu();

    showVersionHistory(container, processTemplate);
  }

  render() {
    const { menuAnchor, hideProcessTemplateMenu } = this.props;
    const open = Boolean(menuAnchor);

    return (
      <Menu open={open} anchorEl={menuAnchor} onClose={hideProcessTemplateMenu}>
        <MenuItem onClick={this.initiateProcessClicked}>
          <ListItemIcon>
            <InputIcon color="primary" />
          </ListItemIcon>
          <ListItemText inset primary="Initiate Process" />
        </MenuItem>
        <MenuItem onClick={this.editProcessTemplateClicked}>
          <ListItemIcon>
            <EditIcon />
          </ListItemIcon>
          <ListItemText inset primary="Edit Process Template" />
        </MenuItem>
        <MenuItem onClick={this.copyProcessTemplateClicked}>
          <ListItemIcon>
            <FileCopyIcon />
          </ListItemIcon>
          <ListItemText inset primary="Copy Process Template" />
        </MenuItem>
        <MenuItem onClick={this.showVersionHistoryClicked}>
          <ListItemIcon>
            <HistoryIcon />
          </ListItemIcon>
          <ListItemText inset primary="Version History" />
        </MenuItem>
        <MenuItem onClick={this.deleteProcessTemplateClicked}>
          <ListItemIcon>
            <DeleteIcon color="secondary" />
          </ListItemIcon>
          <ListItemText inset primary="Delete" />
        </MenuItem>
      </Menu>
    );
  }
}

export default withStyles({})(ProcessTemplateMenu);

export interface ProcessTemplateMenuProps {
  container: ProcessTemplateContainer;
  processTemplate: ProcessTemplate;
  menuAnchor?: HTMLElement;
  hideProcessTemplateMenu: () => void;
  navigateToProcessTemplate: (processTemplateId: string) => void;
  initiateProcess: (container: ProcessTemplateContainer, processTemplate: ProcessTemplate) => void;
  showVersionHistory: (container: ProcessTemplateContainer, processTemplate: ProcessTemplate) => void;
  copyProcessTemplate: (container: ProcessTemplateContainer, processTemplate: ProcessTemplate) => void;
  deleteProcessTemplate: (container: ProcessTemplateContainer, processTemplate: ProcessTemplate) => void;
}
