import React from 'react';
import { WithStyles, withStyles, Theme, createStyles } from '@material-ui/core/styles';
import Switch from '@material-ui/core/Switch';
import FormControlLabel from '@material-ui/core/FormControlLabel';
import Typography from '@material-ui/core/Typography';
import FormGroup from '@material-ui/core/FormGroup';
import Grid from '@material-ui/core/Grid';
import FormControl from '@material-ui/core/FormControl';
import Avatar from '@material-ui/core/Avatar';
import IconButton from '@material-ui/core/IconButton';
import Tooltip from '@material-ui/core/Tooltip';
import Paper from '@material-ui/core/Paper';
import DOMPurify from 'dompurify';
import { ActivityTemplate } from '../../ppp';
import UnfoldMore from '@material-ui/icons/UnfoldMore';
import Edit from '@material-ui/icons/Edit';
import Delete from '@material-ui/icons/Delete';
import AttachFile from '@material-ui/icons/AttachFile';
import HelpOutlineRounded from '@material-ui/icons/HelpOutlineRounded';
import autobind from 'autobind-decorator';
import { Fab } from '@material-ui/core';
import isEqual from 'lodash/isEqual';

const styles = (theme: Theme) =>
  createStyles({
    root: {
      flexGrow: 1,
      maxWidth: 752
    },
    listitem: {
      backgroundColor: theme.palette.background.paper
    },
    listItemContent: {
      paddingLeft: 5,
      paddingRight: 55
    },
    deleteButton: {
      color: theme.palette.secondary.dark
    },
    dependsOnRow: {
      marginLeft: 20
    },
    notDependsOnRow: {
      marginLeft: 0
    },
    paper: {},
    responsiblePartyInitials: {
      fontWeight: 100,
      minWidth: '30px',
      minHeight: '30px',
      height: '30px',
      width: '30px',
      backgroundColor: theme.palette.primary.main,
      boxShadow: 'none',
      cursor: 'default'
    }
  });

class ActivityTemplateListItem extends React.PureComponent<ActivityTemplateListItemProps, ActivityTemplateListItemState> {
  constructor(props: ActivityTemplateListItemProps) {
    super(props);
    this.state = {
      id: '',
      description: '',
      responsibleParty: '',
      allowAttachments: false,
      dependsOnPreviousActivity: false,
      requireAttachments: false,
      requiresReview: false,
      showTooltip: false,
      dependsOnClassname: props.classes.dependsOnRow,
      jobAidAttachments: []
    };
  }

  static getDerivedStateFromProps(nextProps: ActivityTemplateListItemProps, state: ActivityTemplateListItemState) {
    if (
      nextProps.activityTemplate.id !== state.id ||
      nextProps.activityTemplate.allowAttachments !== state.allowAttachments ||
      nextProps.activityTemplate.dependsOnPreviousActivity !== state.dependsOnPreviousActivity ||
      nextProps.activityTemplate.description !== state.description ||
      nextProps.activityTemplate.requireAttachments !== state.requireAttachments ||
      nextProps.activityTemplate.requiresReview !== state.requiresReview ||
      nextProps.activityTemplate.responsibleParty !== state.responsibleParty ||
      nextProps.activityTemplate.tooltip !== state.tooltip ||
      !isEqual(nextProps.activityTemplate.jobAidAttachments, state.jobAidAttachments)
    ) {
      return {
        id: nextProps.activityTemplate.id,
        description: nextProps.activityTemplate.description,
        responsibleParty: nextProps.activityTemplate.responsibleParty,
        allowAttachments: nextProps.activityTemplate.allowAttachments,
        dependsOnPreviousActivity: nextProps.activityTemplate.dependsOnPreviousActivity,
        requireAttachments: nextProps.activityTemplate.requireAttachments,
        requiresReview: nextProps.activityTemplate.requiresReview,
        tooltip: nextProps.activityTemplate.tooltip,
        jobAidAttachments: nextProps.activityTemplate.jobAidAttachments,
        dependsOnClassname: ActivityTemplateListItem.setDependsOnClassname(nextProps, nextProps.activityTemplate.dependsOnPreviousActivity)
      };
    } else {
      return null;
    }
  }

  static setDependsOnClassname(props: ActivityTemplateListItemProps, dependsOnValue: boolean): string {
    return dependsOnValue ? props.classes.dependsOnRow : props.classes.notDependsOnRow;
  }

  static getInitials(name: string): string {
    let initials = name.match(/\b\w/g) || [];
    return ((initials.shift() || '') + (initials.pop() || '')).toUpperCase();
  }

  @autobind
  handleDeleteActionTemplate(event: React.MouseEvent, activity: ActivityTemplate, index: number) {
    this.props.onClickDelete(activity, index);
  }

  @autobind
  handleBooleanChange(name: string) {
    const { onActivityTemplateChange, index } = this.props;
    const currentActivityTemplate = { ...this.state };
    return (event: React.ChangeEvent<HTMLInputElement>) => {
      switch (name) {
        case 'allowAttachments': {
          currentActivityTemplate.allowAttachments = event.target.checked;
          onActivityTemplateChange(currentActivityTemplate, index);
          break;
        }
        case 'requireAttachments': {
          currentActivityTemplate.requireAttachments = event.target.checked;
          onActivityTemplateChange(currentActivityTemplate, index);
          break;
        }
        case 'dependsOnPreviousActivity': {
          const value = this.props.index !== undefined && this.props.index === 0 ? false : event.target.checked;
          currentActivityTemplate.dependsOnPreviousActivity = value;
          currentActivityTemplate.dependsOnClassname = ActivityTemplateListItem.setDependsOnClassname(this.props, event.target.checked);
          onActivityTemplateChange(currentActivityTemplate, index);
          break;
        }
        case 'requiresReview': {
          currentActivityTemplate.requiresReview = event.target.checked;
          onActivityTemplateChange(currentActivityTemplate, index);
          break;
        }
        default: {
          // statements;
          break;
        }
      }
    };
  }

  render() {
    const { id, description, dependsOnPreviousActivity, allowAttachments, requireAttachments, requiresReview, responsibleParty, tooltip, jobAidAttachments, dependsOnClassname } = this.state;
    const { classes, activityDepth, index, readOnly } = this.props;
    return (
      <Paper className={classes.paper + ' ' + dependsOnClassname} style={{ marginLeft: `${activityDepth * 15}px` }} id={id}>
        <Grid container spacing={0} direction="row" wrap="nowrap" justify="center">
          <Grid item style={{ padding: 10 }}>
            <Avatar>
              <UnfoldMore />
            </Avatar>
          </Grid>
          <Grid item xs zeroMinWidth>
            <Grid container className={classes.listItemContent} direction="column" spacing={0} justify="flex-start">
              <Grid item className={'rte'} dangerouslySetInnerHTML={{ __html: DOMPurify.sanitize(description) }} />
              <Grid item style={{ paddingTop: 5 }}>
                {responsibleParty && (
                  <>
                    <Typography variant="subtitle1">
                      <Fab size="small" color="primary" className={classes.responsiblePartyInitials}>
                        {ActivityTemplateListItem.getInitials(responsibleParty)}
                      </Fab>
                      <span style={{ paddingLeft: 7 }}>{responsibleParty}</span>
                    </Typography>
                  </>
                )}
              </Grid>
              <Grid container justify="flex-end">
                <FormControl>
                  <FormGroup row>
                    {tooltip && tooltip.length > 0 && (
                      <FormControlLabel
                        control={
                          <Tooltip title={tooltip ? tooltip : ''} enterDelay={500} leaveDelay={200}>
                            <HelpOutlineRounded color="action" />
                          </Tooltip>
                        }
                        label=""
                      />
                    )}
                    {index > 0 && (
                      <FormControlLabel
                        hidden={index !== undefined && index === 0}
                        control={<Switch onChange={this.handleBooleanChange('dependsOnPreviousActivity')} checked={dependsOnPreviousActivity} />}
                        label="Depends On Previous"
                      />
                    )}
                    <FormControlLabel control={<Switch onChange={this.handleBooleanChange('allowAttachments')} checked={allowAttachments} />} label="Allow Attachments" />
                    <FormControlLabel
                      control={<Switch onChange={this.handleBooleanChange('requireAttachments')} checked={requireAttachments} disabled={!allowAttachments} />}
                      label="Require Attachments"
                    />
                    <FormControlLabel control={<Switch onChange={this.handleBooleanChange('requiresReview')} checked={requiresReview} />} label="Requires Secondary  Review" />
                  </FormGroup>
                </FormControl>
              </Grid>
            </Grid>
          </Grid>
          {!readOnly && (
            <Grid item>
              {jobAidAttachments && jobAidAttachments.length > 0 && (
                <Tooltip
                  title={jobAidAttachments
                    .map(elem => {
                      return elem.fileName;
                    })
                    .join(', ')}
                  enterDelay={500}
                  leaveDelay={200}
                >
                  <IconButton
                    onClick={(event: React.MouseEvent) => {
                      event.preventDefault();
                      event.stopPropagation();
                      this.props.onClickEdit(this.state, this.props.index);
                    }}
                    key={id}
                  >
                    <AttachFile />
                  </IconButton>
                </Tooltip>
              )}
              <IconButton
                aria-label="Edit / View"
                onClick={(event: React.MouseEvent) => {
                  event.preventDefault();
                  event.stopPropagation();
                  this.props.onClickEdit(this.state, this.props.index);
                }}
                key={id}
              >
                <Edit />
              </IconButton>
              <IconButton
                onClick={(e: React.MouseEvent) => {
                  e.preventDefault();
                  e.stopPropagation();
                  this.handleDeleteActionTemplate(e, this.state, this.props.index);
                }}
              >
                <Delete className={classes.deleteButton} />
              </IconButton>
            </Grid>
          )}
        </Grid>
      </Paper>
    );
  }
}

interface ActivityTemplateListItemProps extends WithStyles {
  activityTemplate: ActivityTemplate;
  index: number;
  activityDepth: number;
  readOnly: boolean;
  onClickEdit(activityTemplate: ActivityTemplate, index: number): void;
  onActivityTemplateChange(item: ActivityTemplate, index: number): void;
  onClickDelete(activityTemplate: ActivityTemplate, index: number): void;
}

interface ActivityTemplateListItemState extends ActivityTemplate {
  showTooltip: boolean;
  dependsOnClassname: string;
}

export default withStyles(styles)(ActivityTemplateListItem);
