import React from 'react';
import autobind from 'autobind-decorator';

import { withStyles, WithStyles } from '@material-ui/core/styles';
import CircularProgress from '@material-ui/core/CircularProgress';
import Dialog from '@material-ui/core/Dialog';
import DialogTitle from '@material-ui/core/DialogTitle';
import DialogContent from '@material-ui/core/DialogContent';
import DialogActions from '@material-ui/core/DialogActions';
import Button from '@material-ui/core/Button';

import { styles } from './styles';
import { AppContext } from '../../AppContext';
import { Process } from '../../ppp';
import Grid from '@material-ui/core/Grid';

class DeleteProcessDialog extends React.Component<DeleteProcessDialogProps, DeleteProcessDialogState> {
  static contextType = AppContext;
  context!: React.ContextType<typeof AppContext>;

  constructor(props: DeleteProcessDialogProps) {
    super(props);

    this.state = {
      isDeleting: false
    };
  }

  @autobind
  handleOnClose() {
    if (this.props.onClose) {
      this.props.onClose(false);
    }
  }

  @autobind
  async deleteProcess() {
    const { onClose, process } = this.props;
    const { processStore } = this.context;

    if (!process.id) {
      if (onClose) {
        onClose(false);
      }
      return;
    }

    this.setState({
      isDeleting: true
    });

    const result = await processStore.deleteProcessById(process.id);

    this.setState({
      isDeleting: false
    });

    if (onClose) {
      onClose(result);
    }
  }

  render() {
    const { classes, process } = this.props;
    const { isDeleting } = this.state;

    return (
      <Dialog open={this.props.open} onClose={this.handleOnClose} aria-labelledby="confirm-delete-process-dialog-title" fullWidth={true} maxWidth="xs">
        <DialogTitle id="confirm-delete-process-dialog-title">Delete Process Template?</DialogTitle>
        <DialogContent>
          <div>
            Are you sure you wish to delete the process titled{' '}
            <strong>
              <i>{process.name}</i>
            </strong>
          </div>
        </DialogContent>
        <DialogActions>
          {isDeleting && (
            <Grid container style={{ margin: '15px' }}>
              <CircularProgress variant="indeterminate" disableShrink className={classes.facebook2} size={24} thickness={4} />
            </Grid>
          )}
          {!isDeleting && (
            <>
              <Button onClick={this.handleOnClose} color="primary">
                Cancel
              </Button>
              <Button onClick={this.deleteProcess} color="primary">
                Delete
              </Button>
            </>
          )}
        </DialogActions>
      </Dialog>
    );
  }
}

export default withStyles(styles)(DeleteProcessDialog);

interface DeleteProcessDialogProps extends WithStyles<typeof styles> {
  open: boolean;
  onClose?: (deleted: boolean) => void;
  process: Process;
}

interface DeleteProcessDialogState {
  isDeleting: boolean;
}
