import React from 'react';
import autobind from 'autobind-decorator';
import orderBy from 'lodash/orderBy';
import Dropzone from 'react-dropzone';
import Typography from '@material-ui/core/Typography';
import CircularProgress from '@material-ui/core/CircularProgress';
import Avatar from '@material-ui/core/Avatar';
import Chip from '@material-ui/core/Chip';
import UploadIcon from '@material-ui/icons/CloudUpload';
import { Attachment } from '../../ppp';
import { Theme, createStyles, withStyles, WithStyles, ListItem, ListItemIcon, ListItemText } from '@material-ui/core';

const styles = (theme: Theme) =>
  createStyles({
    activityDescriptionColumn: {
      flexBasis: '70%'
    },
    activityAttachmentsColumn: {
      flexBasis: '30%',
      borderLeft: `2px solid ${theme.palette.divider}`,
      padding: `${theme.spacing()}px ${theme.spacing() * 2}px`
    },
    activityCardHeading: {},
    activityCommentsTextField: {
      flexBasis: '70%',
      marginLeft: theme.spacing(),
      marginRight: theme.spacing()
    },
    activityCommentsInput: {
      fontSize: '12px'
    },
    attachmentChip: {
      height: '24px',
      margin: '2px'
    },
    attachmentChipLabel: {
      paddingLeft: '0'
    },
    attachmentChipDeleteIcon: {
      height: '15px',
      width: '15px'
    },
    attachmentAvatar: {
      margin: '5px',
      height: '15px',
      width: '15px'
    }
  });

class AttachmentsArea extends React.Component<AttachmentsAreaProps> {
  @autobind
  handleAttachmentDrop(acceptedFiles: File[]) {
    if (!acceptedFiles || acceptedFiles.length !== 1) {
      if (acceptedFiles.length === 0) {
        alert('Please select one file at a time.');
      }
      return;
    }

    if (this.props.onAttachmentUpload) {
      let isInvalidFileName = this.isFileNameInvalid(acceptedFiles[0]);
      if (isInvalidFileName) {
        return;
      }
      this.props.onAttachmentUpload(acceptedFiles[0]);
    }
  }

  isFileNameInvalid(file: File) {
    let noExtFileName = file.name.substring(0, file.name.lastIndexOf('.'));
    if (noExtFileName.length > 52) {
      alert('Filename is too long, max limit is 52 chars');
      return true;
    }

    let invalidFileExt = /\.(ade|adp|asa|exe|ashx|asp|cmd|db|tmp|config|dll|ini|aspx|svc|lnk)$/i.test(file.name);
    if (invalidFileExt) {
      alert('Invalid File');
      return invalidFileExt;
    }

    let regEx = /^(?!\.)(?!com[0-9]$)(?!con$)(?!lpt[0-9]$)(?!nul$)(?!prn$)[^\|\*\?\\:%&{}~#<>/"]*[^\.\|\*\?\\:<>/$"]+$/;
    // new RegExp('[# % & * : < > ? / { | } ~ \\ "]');
    let inValidChars = regEx.test(file.name);
    if (!inValidChars) {
      alert('Invalid Filename');
    }
    return !inValidChars;
  }

  @autobind
  handleAttachmentClicked(attachment: Attachment) {
    if (this.props.onAttachmentClicked) {
      this.props.onAttachmentClicked(attachment);
    }
  }

  @autobind
  handleAttachmentDelete(attachment: Attachment) {
    if (this.props.onAttachmentDelete) {
      this.props.onAttachmentDelete(attachment);
    }
  }

  getAttachmentAreaStyle(isDragActive: boolean) {
    if (!isDragActive) {
      return {};
    }
    return {
      backgroundColor: '#eee'
    };
  }

  render() {
    const { classes, allowActions, isUploadingAttachment, attachments, origin } = this.props;
    return (
      <>
        {isUploadingAttachment && <CircularProgress variant="indeterminate" disableShrink className={classes.progressSpinner} size={24} thickness={4} />}
        {!isUploadingAttachment && (
          <>
            <Dropzone onDrop={this.handleAttachmentDrop} multiple={false} disabled={!allowActions}>
              {({ getRootProps, getInputProps, isDragActive }) => (
                <>
                  <Typography variant="caption" style={this.getAttachmentAreaStyle(isDragActive)}>
                    <div {...getRootProps()}>
                      <input {...getInputProps()} />
                      <ListItem style={{ margin: '2px', padding: '2px' }}>
                        <ListItemIcon>
                          <UploadIcon style={{ cursor: 'pointer' }} className={classes.uploadIcon} />
                        </ListItemIcon>
                        <ListItemText style={{ margin: '0', padding: 0 }} primary={this.props.placeholderText || 'Job Aid Attachments'} />
                      </ListItem>
                      <div>
                        {(!attachments || attachments.length <= 0) && (
                          <Typography variant="caption">
                            <i>No Attachments</i>
                          </Typography>
                        )}
                        {attachments &&
                          attachments.length > 0 &&
                          orderBy(attachments, ['fileName'], ['asc']).map(attachment => (
                            <Chip
                              classes={{ label: classes.attachmentChipLabel, deleteIcon: classes.attachmentChipDeleteIcon }}
                              className={classes.attachmentChip}
                              key={attachment.fileName}
                              avatar={<Avatar className={classes.attachmentAvatar} src={`${origin}${attachment.fileIconUrl || '/_layouts/images/ICGEN.GIF'}`} />}
                              label={attachment.fileName}
                              color="primary"
                              onClick={() => this.handleAttachmentClicked(attachment)}
                              onDelete={allowActions ? () => this.handleAttachmentDelete(attachment) : undefined}
                            />
                          ))}
                      </div>
                    </div>
                  </Typography>
                </>
              )}
            </Dropzone>
          </>
        )}
      </>
    );
  }
}

export default withStyles(styles)(AttachmentsArea);

interface AttachmentsAreaProps extends WithStyles {
  allowActions: boolean;
  isUploadingAttachment: boolean;
  attachments?: Attachment[];
  placeholderText?: string;
  origin: string;
  onAttachmentUpload?: (attachmentFile: File) => void;
  onAttachmentClicked?: (attachment: Attachment) => void;
  onAttachmentDelete?: (attachment: Attachment) => void;
}
