export enum ProcessStatus {
  /**
   * Indicates that the process is new.
   */
  Initiated = 'Initiated',
  /**
   * Indicates that the process is active - at least one activity has been modified since creation.
   */
  Active = 'Active',
  /**
   * Indicates that all activities on the process have been completed, but an overall sign-off has not occurred.
   */
  CompletedNotSigned = 'CompletedNotSigned',
  /**
   * Indicates that all activities on the process have been completed the process has been signed-off on.
   */
  CompletedAndSigned = 'CompletedAndSigned',
  /**
   * Indicates that the process has been marked as inactive.
   */
  Inactive = 'Inactive',
  /**
   * Indicates that the process has been marked as deleted.
   */
  Deleted = 'Deleted',
  /**
   * Indicates that the process has been rejected.
   */
  Rejected = 'Rejected'
}
