import React from 'react';
import Button from '@material-ui/core/Button';
import TextField from '@material-ui/core/TextField';
import autobind from 'autobind-decorator';
import Dialog from '@material-ui/core/Dialog';
import DialogActions from '@material-ui/core/DialogActions';
import DialogContent from '@material-ui/core/DialogContent';
import DialogTitle from '@material-ui/core/DialogTitle';
import { WithStyles, Theme, createStyles, withStyles } from '@material-ui/core/styles';
import Grid from '@material-ui/core/Grid';
import FormControlLabel from '@material-ui/core/FormControlLabel';
import Checkbox from '@material-ui/core/Checkbox';
import MenuItem from '@material-ui/core/MenuItem';
import FormControl from '@material-ui/core/FormControl';
import InputLabel from '@material-ui/core/InputLabel';
import Select from '@material-ui/core/Select';
import { ActivityTemplate, Attachment } from '../../ppp';
import { getGUID } from '@pnp/common';
import RichEditorEditor from '../controls/RichEditorEditor';
import { CircularProgress } from '@material-ui/core';
import AttachmentsArea from '../controls/AttachmentArea';

const styles = (theme: Theme) =>
  createStyles({
    root: {
      display: 'flex',
      flexWrap: 'wrap'
    },
    paper: {
      ...theme.mixins.gutters(),
      paddingTop: theme.spacing() * 2,
      paddingBottom: theme.spacing() * 2
    },
    formControl: {
      padding: '2px'
    },
    selectEmpty: {
      marginTop: theme.spacing() * 2
    },
    editor: {
      border: '1px solid grey',
      minHeight: '6em'
    },
    activityAttachmentsColumn: {
      borderLeft: `2px solid ${theme.palette.divider}`,
      padding: `${theme.spacing()}px ${theme.spacing() * 2}px`,
      marginTop: theme.spacing()
    }
  });

class ActivityTemplateDialog extends React.PureComponent<ActivityDialogProps & WithStyles, ActivityDialogState> {
  private editor: any;
  private waitForDebounce = 2000;
  constructor(props: ActivityDialogProps & WithStyles) {
    super(props);
    this.state = {
      id: getGUID(),
      description: '',
      allowAttachments: true,
      dependsOnPreviousActivity: false,
      responsibleParty: '',
      requireAttachments: false,
      requiresReview: false,
      showTooltip: false,
      editorState: {},
      isPerformingAction: false,
      jobAidAttachments: [],
      isUploadingAttachment: false
    };
  }

  static getDerivedStateFromProps(nextProps: ActivityDialogProps, state: ActivityTemplate) {
    const { activityTemplate } = nextProps;
    if (activityTemplate) {
      const showTooltip = activityTemplate.tooltip !== undefined ? true : false;
      return {
        id: activityTemplate.id,
        description: activityTemplate.description,
        responsibleParty: activityTemplate.responsibleParty,
        allowAttachments: activityTemplate.allowAttachments,
        dependsOnPreviousActivity: activityTemplate.dependsOnPreviousActivity,
        requireAttachments: activityTemplate.requireAttachments,
        requiresReview: activityTemplate.requiresReview,
        tooltip: activityTemplate.tooltip,
        showTooltip: showTooltip,
        jobAidAttachments: activityTemplate.jobAidAttachments
      };
    } else {
      return null;
    }
  }

  @autobind
  setEditor(editor: any) {
    this.editor = editor;
  }

  @autobind
  focusEditor() {
    if (this.editor) {
      this.editor.focus();
    }
  }

  @autobind
  onChange(editorState: any) {
    this.setState({ editorState });
  }

  @autobind
  handleClose(event: React.MouseEvent) {
    event.preventDefault();
    event.stopPropagation();
    this.props.onClose('');
  }

  @autobind
  handleSubmit(event: React.MouseEvent) {
    event.preventDefault();
    event.stopPropagation();
    this.setState({ isPerformingAction: true }, () => {
      // TODO: Hack, this wait half a sec.  for the last debounce.
      // Researching for a better solution. This works for now.
      setTimeout(() => {
        this.props.onSave(this.state, this.props.index);
        this.setState({ isPerformingAction: false });
      }, this.waitForDebounce + 500);
    });
  }

  @autobind
  handleStringChange(name: string) {
    const { onActivityTemplateChange, index } = this.props;
    const currentActivityTemplate = { ...this.state } as ActivityTemplate;
    return (event: React.ChangeEvent<{ name?: string | undefined; value: any }>) => {
      switch (name) {
        case 'description': {
          currentActivityTemplate.description = event.target.value;
          onActivityTemplateChange(currentActivityTemplate, index);
          break;
        }
        case 'tooltip': {
          currentActivityTemplate.tooltip = event.target.value;
          onActivityTemplateChange(currentActivityTemplate, index);
          break;
        }
        case 'responsibleParty': {
          currentActivityTemplate.responsibleParty = event.target.value;
          onActivityTemplateChange(currentActivityTemplate, index);
          break;
        }
        default: {
          // statements;
          break;
        }
      }
    };
  }

  @autobind
  handleBooleanChange(name: string) {
    const { onActivityTemplateChange, index } = this.props;
    const currentActivityTemplate = { ...this.state } as ActivityTemplate;
    return (event: React.ChangeEvent<HTMLInputElement>) => {
      switch (name) {
        case 'allowAttachments': {
          currentActivityTemplate.allowAttachments = event.target.checked;
          onActivityTemplateChange(currentActivityTemplate, index);
          break;
        }
        case 'requireAttachments': {
          currentActivityTemplate.requireAttachments = event.target.checked;
          onActivityTemplateChange(currentActivityTemplate, index);
          break;
        }
        case 'dependsOnPreviousActivity': {
          currentActivityTemplate.dependsOnPreviousActivity = event.target.checked;
          onActivityTemplateChange(currentActivityTemplate, index);
          break;
        }
        case 'requiresReview': {
          currentActivityTemplate.requiresReview = event.target.checked;
          onActivityTemplateChange(currentActivityTemplate, index);
          break;
        }
        case 'showTooltip': {
          if (event.target.checked === false) {
            currentActivityTemplate.tooltip = undefined;
          } else {
            currentActivityTemplate.tooltip = '';
          }
          onActivityTemplateChange(currentActivityTemplate, index);
          break;
        }
        default: {
          // statements;
          break;
        }
      }
    };
  }

  @autobind
  focusTooltipInputField(input: HTMLInputElement): void {
    input && input.focus();
  }

  @autobind
  handleDescriptionRTChange(rawText: string) {
    const { onActivityTemplateChange, index } = this.props;
    const currentActivityTemplate = { ...this.state } as ActivityTemplate;
    currentActivityTemplate.description = rawText;
    onActivityTemplateChange(currentActivityTemplate, index);
  }

  @autobind
  handleAttachmentClicked(attachment: Attachment) {
    window.open(this.props.origin + attachment.serverRelativeUrl, '_blank');
  }

  @autobind
  async handleAttachmentUpload(attachmentFile: File) {
    this.setState({
      isPerformingAction: true,
      isUploadingAttachment: true
    });
    const activity = this.state as ActivityTemplate;
    await this.props.onAttachmentUpload(activity, attachmentFile);

    this.setState({
      isPerformingAction: false,
      isUploadingAttachment: false
    });
  }

  @autobind
  async handleAttachmentDelete(attachment: Attachment) {
    this.setState({
      isPerformingAction: true,
      isUploadingAttachment: true
    });
    const activity = this.state as ActivityTemplate;
    await this.props.onAttachmentDelete(activity, attachment);

    this.setState({
      isPerformingAction: false,
      isUploadingAttachment: false
    });
  }

  render() {
    const { description, isPerformingAction: isMoving, isUploadingAttachment } = this.state;
    const { classes, index, origin } = this.props;
    const header = `${index >= 0 ? 'Edit' : 'Add'} Activity`;

    return (
      <Dialog fullWidth={true} maxWidth="md" open={this.props.open} onClose={this.handleClose} aria-labelledby="form-dialog-title" disableBackdropClick={true}>
        <DialogTitle id="form-dialog-title" style={{ padding: 5 }}>
          {header}
        </DialogTitle>
        <DialogContent>
          <Grid container spacing={0}>
            <Grid item xs={12}>
              <RichEditorEditor placeholder="Description..." rawText={description} onChange={this.handleDescriptionRTChange} waitForDebounce={this.waitForDebounce} />
            </Grid>
            <Grid item xs={12}>
              <Grid container>
                <Grid item xs={8}>
                  <Grid container>
                    <Grid item xs={12}>
                      <FormControlLabel
                        className={classes.formControl}
                        style={{ paddingTop: 5 }}
                        control={<Checkbox className={classes.formControl} checked={this.state.allowAttachments} onChange={this.handleBooleanChange('allowAttachments')} value="allowAttachments" />}
                        label="Allow Attachments"
                      />
                    </Grid>
                    <Grid item xs={1} />
                    <Grid item xs={11}>
                      <FormControlLabel
                        control={
                          <Checkbox
                            className={classes.formControl}
                            checked={this.state.requireAttachments}
                            onChange={this.handleBooleanChange('requireAttachments')}
                            value="requireAttachments"
                            disabled={!this.state.allowAttachments}
                          />
                        }
                        label="Require Attachments"
                      />
                    </Grid>
                    <Grid item xs={12}>
                      <FormControlLabel
                        control={
                          <Checkbox
                            className={classes.formControl}
                            style={{ paddingBottom: '13px' }}
                            checked={this.state.dependsOnPreviousActivity}
                            onChange={this.handleBooleanChange('dependsOnPreviousActivity')}
                            value="dependsOnPreviousActivity"
                          />
                        }
                        label="Depends on Prior Activity"
                      />
                    </Grid>
                    <Grid item xs={8}>
                      <FormControl fullWidth={true} style={{ paddingBottom: '15px' }}>
                        <InputLabel htmlFor="responsible-party">Responsible Party</InputLabel>
                        <Select
                          value={this.state.responsibleParty}
                          onChange={this.handleStringChange('responsibleParty')}
                          inputProps={{
                            name: 'responsibleParty',
                            id: 'responsibleParty'
                          }}
                        >
                          <MenuItem value="">
                            <em>None</em>
                          </MenuItem>
                          {this.props.parties.map((party: { value: string; text: string }) => {
                            return (
                              <MenuItem key={party.value} value={party.text}>
                                {party.text}
                              </MenuItem>
                            );
                          })}
                        </Select>
                      </FormControl>
                    </Grid>
                    <Grid item xs={4} />
                    <Grid item xs={12}>
                      <FormControlLabel
                        control={<Checkbox className={classes.formControl} checked={this.state.requiresReview} onChange={this.handleBooleanChange('requiresReview')} value="requiresReview" />}
                        label="Requires Secondary Review"
                      />
                    </Grid>
                  </Grid>
                </Grid>
                <Grid item xs={4} className={classes.activityAttachmentsColumn}>
                  <AttachmentsArea
                    allowActions={true}
                    isUploadingAttachment={isUploadingAttachment}
                    attachments={this.state.jobAidAttachments}
                    origin={origin}
                    onAttachmentClicked={this.handleAttachmentClicked}
                    onAttachmentUpload={this.handleAttachmentUpload}
                    onAttachmentDelete={this.handleAttachmentDelete}
                  />
                </Grid>
              </Grid>
            </Grid>
            <Grid item xs={12}>
              <FormControlLabel
                control={<Checkbox className={classes.formControl} checked={this.state.showTooltip} onChange={this.handleBooleanChange('showTooltip')} value="showTooltip" />}
                label="Tooltip?"
              />
            </Grid>
            {this.state.showTooltip ? (
              <Grid item xs={12}>
                <TextField
                  inputRef={this.focusTooltipInputField}
                  className={classes.formControl}
                  fullWidth={true}
                  id="activityTemplateTooltipTextField"
                  multiline
                  placeholder="Tooltip"
                  rows="4"
                  value={this.state.tooltip}
                  margin="normal"
                  onChange={this.handleStringChange('tooltip')}
                />
              </Grid>
            ) : null}
          </Grid>
        </DialogContent>
        <DialogActions>
          <Grid container direction="row" justify="flex-end" spacing={8} alignItems="flex-start" style={{ width: '100%', margin: 0 }}>
            {isMoving && (
              <Grid item style={{ marginBottom: 25, marginRight: 30 }}>
                <CircularProgress size={25} />
              </Grid>
            )}
            {!isMoving && (
              <Grid item>
                <Button style={{ margin: 5 }} variant="outlined" color="primary" onClick={this.handleSubmit}>
                  Submit
                </Button>
                <Button style={{ margin: 5 }} variant="outlined" color="primary" onClick={this.handleClose}>
                  Cancel
                </Button>
              </Grid>
            )}
          </Grid>
        </DialogActions>
      </Dialog>
    );
  }
}

export default withStyles(styles)(ActivityTemplateDialog);

interface ActivityDialogProps {
  activityTemplate?: ActivityTemplate;
  index: number;
  open: boolean;
  parties: Array<{ value: string; text: string }>;
  origin: string;
  onActivityTemplateChange(item: ActivityTemplate, index: number): void;
  onAttachmentUpload: (activity: ActivityTemplate, file: File) => Promise<void>;
  onAttachmentDelete: (activity: ActivityTemplate, attachment: Attachment) => Promise<void>;
  onClose(value: string): void;
  onSave(item: ActivityTemplate, index: number): void;
}

interface ActivityDialogState extends ActivityTemplate {
  isUploadingAttachment: boolean;
  showTooltip: boolean;
  editorState: any;
  isPerformingAction: boolean;
}
