import React from 'react';
import autobind from 'autobind-decorator';
import JSZip from 'jszip';
import { saveAs } from 'file-saver';
import moment from 'moment';
import stableSort from 'lodash/orderBy';
import startCase from 'lodash/startCase';
import kebabCase from 'lodash/kebabCase';
import { withStyles, WithStyles } from '@material-ui/core/styles';
import cn from 'classnames';
import CircularProgress from '@material-ui/core/CircularProgress';
import Table from '@material-ui/core/Table';
import TablePagination from '@material-ui/core/TablePagination';
import TableBody from '@material-ui/core/TableBody';
import TableRow from '@material-ui/core/TableRow';
import TableCell from '@material-ui/core/TableCell';
import Checkbox from '@material-ui/core/Checkbox';
import Button from '@material-ui/core/Button';
import Grid from '@material-ui/core/Grid';
import Dialog from '@material-ui/core/Dialog';
import DialogTitle from '@material-ui/core/DialogTitle';
import DialogContent from '@material-ui/core/DialogContent';
import LinearProgress from '@material-ui/core/LinearProgress';
import DialogActions from '@material-ui/core/DialogActions';
import Paper from '@material-ui/core/Paper';
import { Process } from '../../ppp';
import { AppContext } from '../../AppContext';
import { getFiscalYear } from '../../util/pppUtils';
import ProcessTableHead from './ProcessTableHead';
import { styles } from './styles';
import { ProcessExporter } from '../../services/ProcessExporter';
import ProcessFilters from './ProcessFilters';
import { FilterObject } from '../../ppp/FilterObject';
import { Filter } from '../../ppp/Filter';
import { Snackbar, IconButton, SnackbarContent } from '@material-ui/core';
import CloseIcon from '@material-ui/icons/Close';
import { RouteComponentProps, withRouter } from 'react-router-dom';
import { when } from 'mobx';
import { MonitoringAndReviewStatus } from '../../ppp/MonitoringAndReviewStatus';

export class ProcessTable extends React.Component<ProcessTableProps, ProcessTableState> {
  static contextType = AppContext;
  context!: React.ContextType<typeof AppContext>;
  private _isMount = false;

  constructor(props: ProcessTableProps) {
    super(props);

    this.state = {
      fiscalYear: getFiscalYear(),
      isLoading: true,
      showSelect: false,
      currentExportStatus: '',
      selectedProcesses: [],
      order: 'desc',
      orderBy: 'initiatedOn',
      selected: [],
      processes: [],
      page: 0,
      rowsPerPage: 25,
      availableResponsibleParties: [],
      functionalAreas: [],
      information: { open: false, text: '', isError: false }
    };
  }

  async componentDidMount() {
    this._isMount = true;

    when(
      () => this.context.personalizationStore.areFiltersLoaded && this.context.personalizationStore.isConfiguredFiltersLoaded,
      async () => {
        // check viewId
        if (this.props.match.params.viewId && this.props.match.params.viewId.length > 0) {
          // there's a selected View ID, load it.
          const id = this.props.match.params.viewId;
          const filterView = this.context.personalizationStore.filterViews.find(fv => fv.id === id);
          if (filterView) {
            this.context.personalizationStore.selectedViewFilter = {
              id: filterView.id,
              name: filterView.name,
              global: filterView.global,
              filterSet: [...filterView.filterSet]
            };
            if (this.props.location.pathname.indexOf('/processes') >= 0) {
              this.props.history.replace(`/processes/${id}`);
            }
            await this.getFiscalYears();
          }
        } else {
          await this.getFiscalYears();
        }
      }
    );
  }

  componentWillUnmount() {
    this._isMount = false;
  }

  @autobind
  async getFiscalYears(): Promise<void> {
    this.setState({ processes: [], isLoading: true });
    const filters = this.context.personalizationStore.selectedViewFilter && this.context.personalizationStore.selectedViewFilter.filterSet;
    const fys: Array<string | undefined> = filters
      .map(i => {
        if (i.category === 'FY') {
          return i.key;
        }
      })
      .filter(i => i);
    this.setState({ processes: await this.context.processStore.listAllProcessesForFiscalYears(fys), isLoading: false }, () => {
      this.context.personalizationStore.configureDataSpecificFilters(this.state.processes);
    });
  }

  @autobind
  private handleChangePage(event: React.MouseEvent<HTMLButtonElement, MouseEvent> | null, page: number) {
    this.setState({ page });
  }

  @autobind
  private handleChangeRowsPerPage(event: React.ChangeEvent<HTMLInputElement | HTMLTextAreaElement>) {
    const x = event.target.value;
    this.setState({ rowsPerPage: Number(x) });
  }

  @autobind
  private handleRequestSort(event: any, property: string) {
    const orderBy = property;
    let order: 'asc' | 'desc' = 'desc';

    if (this.state.orderBy === property && this.state.order === 'desc') {
      order = 'asc';
    }

    this.setState({ order, orderBy });
  }

  private getFilterIndex(data: Array<FilterObject>, check: FilterObject) {
    for (var x = 0; x < data.length; x++) {
      if (data[x].key === check.key) {
        return x;
      }
    }
    return -1;
  }

  @autobind
  async handleFilterDelete(data: FilterObject) {
    const filters = (this.context.personalizationStore.selectedViewFilter && this.context.personalizationStore.selectedViewFilter.filterSet) || ([] as Array<FilterObject>);
    if (this.filterDelete(data, filters)) {
      await this.getFiscalYears();
    } else {
      // this is to refresh the "soft" Filter done  at render.
      this.setState({ processes: [...this.state.processes] });
    }
  }

  @autobind
  async handleAddFilter(data: FilterObject) {
    if (this._isMount) {
      if (this.addFilter(data) === true) {
        await this.getFiscalYears();
      } else {
        // this is to refresh the "soft" Filter done at render.
        this.setState({ processes: [...this.state.processes] });
      }
    }
  }

  @autobind
  filterDelete(data: FilterObject, selectedFilters: Array<FilterObject>): boolean {
    let needsFYDataRefresh = false;
    this.context.personalizationStore.selectedViewFilter.isDirty = true;
    const yearFilter = selectedFilters.filter(sf => {
      return sf.category === 'FY';
    });
    if (data.category === 'FY') {
      if (yearFilter.length > 1) {
        const filterToDelete = this.getFilterIndex(selectedFilters, data);
        selectedFilters.splice(filterToDelete, 1);
        needsFYDataRefresh = true;
      }
    } else {
      const filterToDelete = this.getFilterIndex(selectedFilters, data);
      selectedFilters.splice(filterToDelete, 1);
    }
    return needsFYDataRefresh;
  }

  @autobind
  addFilter(data: FilterObject): boolean {
    let needsFYDataRefresh = false;
    const selectedFilters = (this.context.personalizationStore.selectedViewFilter && this.context.personalizationStore.selectedViewFilter.filterSet) || [];
    this.context.personalizationStore.selectedViewFilter.isDirty = true;

    // TODO: test for FY mods
    if (data.category === 'FY') {
      needsFYDataRefresh = true;
    }
    if (!data.DateRange) {
      if (
        selectedFilters.filter(check => {
          return check.label === data.label;
        }).length < 1
      ) {
        selectedFilters.push(data);
      } else {
        this.filterDelete(data, selectedFilters);
      }
    } else {
      if (
        selectedFilters.filter(check => {
          return check.key === data.key;
        }).length < 1
      ) {
        selectedFilters.push(data);
      } else {
        this.filterDelete(data, selectedFilters);
        selectedFilters.push(data);
      }
    }
    return needsFYDataRefresh;
  }

  @autobind
  async handleClearFilters() {
    this.context.personalizationStore.setDefaultFilter();
    await this.getFiscalYears();
    this.props.history.replace(`/processes`);
  }

  @autobind
  applyFilters(selectedFilters: FilterObject[], processes: Process[]) {
    let statusFilters: FilterObject[];
    let functionalAreaFilters: FilterObject[];
    let createdByFilters: FilterObject[];
    let responsiblePartyFilters: FilterObject[];
    let transactionDateFilters: FilterObject[];
    let creationDateFilters: FilterObject[];
    functionalAreaFilters = selectedFilters.filter(sf => sf.category === 'FunctionalArea');
    statusFilters = selectedFilters.filter(sf => sf.category === 'status');
    createdByFilters = selectedFilters.filter(sf => sf.category === 'CreatedBy');
    responsiblePartyFilters = selectedFilters.filter(sf => sf.category === 'ResponsibleParty');
    transactionDateFilters = selectedFilters.filter(sf => sf.category === 'TransactionDate');
    creationDateFilters = selectedFilters.filter(sf => sf.category === 'CreationDate');

    return processes.filter(p => {
      // true is default here because if the filter doesnt exist, it'll allow the row in.
      let isStatusFiltered = true;
      let isFunctionalAreaFiltered = true;
      let isCreatedByFiltered = true;
      let isResponsiblePartyFiltered = true;
      let isTransactionDateFiltered = true;
      let isCreationDateFiltered = true;
      // Filter By Status
      if (statusFilters && statusFilters.length > 0) {
        isStatusFiltered = statusFilters.some(sf => {
          return p.status === sf.key;
        });
      }
      // Filter by Functional Area
      if (functionalAreaFilters && functionalAreaFilters.length > 0) {
        isFunctionalAreaFiltered = functionalAreaFilters.some(sf => {
          return p.functionArea === sf.key;
        });
      }
      // Filter by Created By
      if (createdByFilters && createdByFilters.length > 0) {
        isCreatedByFiltered = createdByFilters.some(sf => {
          if (p.initiatedBy) {
            return p.initiatedBy.displayName === sf.key;
          } else {
            return false;
          }
        });
      }
      // Filter By Responsible Party
      if (responsiblePartyFilters && responsiblePartyFilters.length > 0) {
        isResponsiblePartyFiltered = responsiblePartyFilters.some(sf => {
          return p.activities.some(a => {
            return a.responsibleParty === sf.key;
          });
        });
      }
      // Filter By Transaction Date
      if (transactionDateFilters && transactionDateFilters.length > 0) {
        const pDate = new Date(p.transactionDate);
        pDate.setHours(0, 0, 0, 0);
        isTransactionDateFiltered = transactionDateFilters.every(sf => {
          if (sf.LessThan) {
            return pDate <= new Date(sf.Date);
          } else {
            return pDate >= new Date(sf.Date);
          }
        });
      }
      // Filter By Creation Date
      if (creationDateFilters && creationDateFilters.length > 0 && p.initiatedOn) {
        const pDate = new Date(p.initiatedOn);
        pDate.setHours(0, 0, 0, 0);
        isCreatedByFiltered = creationDateFilters.every(sf => {
          if (sf.LessThan) {
            return pDate <= new Date(sf.Date);
          } else {
            return pDate >= new Date(sf.Date);
          }
        });
      }
      // All filters use "AND" combined.
      return isStatusFiltered && isFunctionalAreaFiltered && isCreatedByFiltered && isResponsiblePartyFiltered && isTransactionDateFiltered && isCreationDateFiltered;
    });
  }

  @autobind
  async loadAllProcesses() {
    this.setState({
      isLoading: true
    });
    var processes: Process[];
    processes = await this.context.processStore.listAllProcesses();
    this.setState({
      processes,
      isLoading: false
    });
  }

  @autobind
  handleBeginExport() {
    this.setState({
      showSelect: true
    });
  }

  @autobind
  handleCancelExport() {
    this.setState({
      showSelect: false,
      selectedProcesses: []
    });
  }

  @autobind
  async getSharePointOrigin() {
    /* istanbul ignore next */
    if (process.env.NODE_ENV !== 'production') {
      return 'http://localhost:3001';
    }

    const ctx = (await this.context.spContext.getContextInfo()) as any;
    const a = document.createElement('a');
    a.setAttribute('href', ctx.WebFullUrl);

    const { hostname, port, protocol } = a;
    const origin = `${protocol}//${hostname}${port.length ? `:${port}` : ''}`;
    return origin;
  }

  @autobind
  public async handleCompleteExport() {
    const { selectedProcesses } = this.state;
    if (selectedProcesses.length === 0) {
      alert('Please select process');
      return;
    }
    this.setState({
      currentExportStatus: 'Initiating...',
      showSelect: false
    });

    const origin = await this.getSharePointOrigin();
    const bigZip = new JSZip();
    for (const process of selectedProcesses) {
      let processUrl = await this.context.processStore.getProcessPathByProcessId(process.id);
      const file = await ProcessExporter.exportProcess(process, processUrl, origin, (newStatus: string) => {
        this.setState({
          currentExportStatus: newStatus
        });
      });

      bigZip.file(`${process.functionArea}/${kebabCase(process.name)}.zip`, await file.generateAsync({ type: 'blob' }));
    }

    const result = await bigZip.generateAsync({ type: 'blob' });
    saveAs(result, 'processes-bulk-export.zip');

    this.setState({
      currentExportStatus: '',
      selectedProcesses: []
    });
  }

  @autobind
  handleProcessSelectedChange(e: React.ChangeEvent<HTMLInputElement>, process: Process, checked: boolean) {
    const { selectedProcesses } = this.state;

    const ix = selectedProcesses.indexOf(process);
    if (ix > -1) {
      selectedProcesses.splice(ix, 1);
    }

    if (checked) {
      selectedProcesses.push(process);
    }

    this.setState({
      selectedProcesses
    });
  }

  @autobind
  async handleSaveView(Name: string, global: boolean, overwriteViewId?: string): Promise<number> {
    const filters = (this.context.personalizationStore.selectedViewFilter && this.context.personalizationStore.selectedViewFilter.filterSet) || [];
    var filterToSave: Filter;
    filterToSave = {
      id: '',
      global: global,
      name: Name,
      filterSet: filters
    };

    if (overwriteViewId && overwriteViewId.length > 0) {
      // delete this view first
      try {
        await this.context.personalizationStore.deleteFilterById(overwriteViewId);
      } catch {
        // TODO: ignore error for now
      }
    }

    try {
      const result = await this.context.personalizationStore.saveFilter(filterToSave, global);
      if (result && result === 200) {
        this.setState({ information: { open: true, text: `View ${Name} was saved successfully.`, isError: false } });
        await this.context.personalizationStore.loadViews(this.props.currentUser.Title);
        this.context.personalizationStore.filterViews.forEach(f => {
          if (Name.toUpperCase() === f.name.toUpperCase()) {
            this.context.personalizationStore.selectedViewFilter = f;
            this.props.history.replace(`/processes/${f.id}`);
            return;
          }
        });
        return result;
      } else {
        this.setState({ information: { open: true, text: `Couldnt save view ${Name}. Status #${result}.`, isError: true } });
      }
    } catch (error) {
      this.setState({ information: { open: true, text: `There was an error saving view ${Name}.`, isError: true } });
    }
    return 0;
  }

  @autobind
  async handleInfoClose(event: any) {
    this.setState({ information: { open: false, text: '', isError: false } });
  }

  render() {
    const { classes, onNavigateToProcess } = this.props;
    const { isLoading, showSelect, selectedProcesses, currentExportStatus, processes, order, orderBy, selected, rowsPerPage, page } = this.state;
    const emptyRows = rowsPerPage - Math.min(rowsPerPage, processes.length - page * rowsPerPage);
    const filters = (this.context.personalizationStore.selectedViewFilter && this.context.personalizationStore.selectedViewFilter.filterSet) || [];
    const rows = this.applyFilters(filters, processes);

    return (
      <Paper className={classes.root}>
        {isLoading && <CircularProgress className={classes.progress} disableShrink size={50} thickness={5} />}
        <Grid container spacing={0}>
          <Grid item xs={12}>
            <ProcessFilters
              onAddFilterFunction={this.handleAddFilter}
              onRemoveFilterFunction={this.handleFilterDelete}
              onClearFilterFunction={this.handleClearFilters}
              onSaveView={this.handleSaveView}
              isLoading={this.state.isLoading}
            />
          </Grid>
        </Grid>
        <div className={classes.tableWrapper}>
          <Table className={classes.table} aria-labelledby="Process instances">
            <ProcessTableHead
              classes={classes}
              showSelect={showSelect}
              numSelected={selected.length}
              order={order}
              orderBy={orderBy}
              onRequestSort={this.handleRequestSort}
              rowCount={processes.length}
            />
            <TableBody>
              {stableSort(rows, [orderBy], [order])
                .slice(page * rowsPerPage, page * rowsPerPage + rowsPerPage)
                .map((n: Process, processIndex: number) => {
                  const isSelected = false;
                  return (
                    <TableRow hover aria-checked={isSelected} tabIndex={-1} key={n.id} selected={isSelected}>
                      {!showSelect && (
                        <TableCell className={classes.tableCell}>
                          <Checkbox
                            disabled={n.status !== 'CompletedAndSigned'}
                            checked={!!selectedProcesses.find(p => p === n)}
                            onChange={(e, checked) => this.handleProcessSelectedChange(e, n, checked)}
                            value=""
                          />
                        </TableCell>
                      )}
                      <TableCell className={classes.tableCell}>{n.functionArea}</TableCell>
                      <TableCell className={classes.tableCell}>{n.template.title}</TableCell>
                      <TableCell className={cn(classes.tableCell, classes.nameCell)} onClick={() => onNavigateToProcess(n)}>
                        {n.name}
                      </TableCell>
                      <TableCell className={classes.tableCell}>{moment(n.initiatedOn).format('lll')}</TableCell>
                      <TableCell className={classes.tableCell}>{startCase(n.status)}</TableCell>
                      <TableCell className={classes.tableCell}>{n.transactionDate ? moment(new Date(n.transactionDate)).format('MM-DD-YYYY') : ''}</TableCell>
                      <TableCell className={classes.tableCell}>{n.signOff && n.signOff.timeStamp && moment(n.signOff.timeStamp).format('lll')}</TableCell>
                      <TableCell className={classes.tableCell} style={{ wordBreak: 'break-word' }}>
                        {n.signOff && n.signOff.signer && n.signOff.signer.displayName}
                      </TableCell>
                      <TableCell className={classes.tableCell}>{startCase((n.monitoringAndReview && n.monitoringAndReview.status) || MonitoringAndReviewStatus.NotStarted)}</TableCell>
                      <TableCell>
                        <Button variant="contained" color="primary" onClick={() => onNavigateToProcess(n)}>
                          View
                        </Button>
                      </TableCell>
                    </TableRow>
                  );
                })}
              {emptyRows > 0 && (
                <TableRow style={{ height: 49 * emptyRows }}>
                  <TableCell colSpan={6} />
                </TableRow>
              )}
            </TableBody>
          </Table>
        </div>
        <TablePagination
          rowsPerPageOptions={[5, 10, 25, 50, 100, 1000]}
          component="div"
          count={rows.length}
          rowsPerPage={rowsPerPage}
          page={page}
          backIconButtonProps={{
            'aria-label': 'Previous Page'
          }}
          nextIconButtonProps={{
            'aria-label': 'Next Page'
          }}
          onChangePage={this.handleChangePage}
          onChangeRowsPerPage={this.handleChangeRowsPerPage}
        />
        <Dialog open={currentExportStatus !== ''}>
          <DialogTitle id="export-dialog-title">Exporting Processes...</DialogTitle>
          <DialogContent>
            <div>{currentExportStatus}</div>
            <LinearProgress />
          </DialogContent>
          <DialogActions />
        </Dialog>
        <Snackbar
          anchorOrigin={{
            vertical: 'bottom',
            horizontal: 'left'
          }}
          open={this.state.information.open}
          autoHideDuration={6000}
          onClose={this.handleInfoClose}
        >
          <SnackbarContent
            classes={{ root: this.state.information.isError === true ? classes.snackbarError : classes.snackbarInfo }}
            message={this.state.information.text}
            action={[
              <IconButton key="close" aria-label="Close" color="inherit" className={classes.close} onClick={this.handleInfoClose}>
                <CloseIcon />
              </IconButton>
            ]}
          />
        </Snackbar>
      </Paper>
    );
  }
}

export default withRouter(withStyles(styles)(ProcessTable));

interface ProcessTableProps extends WithStyles<typeof styles>, RouteComponentProps<ProcessTableRouterProps> {
  onNavigateToProcess: (process: Process) => void;
  currentUser: any;
}

interface ProcessTableRouterProps {
  viewId: string | undefined;
}

interface ProcessTableState {
  isLoading: boolean;
  showSelect: boolean;
  selectedProcesses: Process[];
  currentExportStatus: string;
  functionalAreas: string[];
  fiscalYear: string;
  order: 'desc' | 'asc';
  orderBy: string;
  selected: [];
  processes: Process[];
  page: number;
  rowsPerPage: number;
  availableResponsibleParties: { value: string; text: string }[];
  information: { open: boolean; text: string; isError: boolean };
}
