import React from 'react';

import autobind from 'autobind-decorator';
import { Dialog, DialogTitle, DialogContent, DialogActions, DialogContentText } from '@material-ui/core';
import TextField from '@material-ui/core/TextField';
import Button from '@material-ui/core/Button';

export default class ActivityNotRequiredComment extends React.PureComponent<ActivityNotRequiredCommentProps> {
  @autobind
  async handleNotRequiredCommentChange(event: React.ChangeEvent<HTMLInputElement>) {
    this.props.onNotRequiredCommentChange(event.target.value, (event.target as any).now);
  }

  render() {
    const { notRequiredDialogOpen, handleNRDClose, handleNRDSave, notRequiredComments } = this.props;
    return (
      <Dialog open={notRequiredDialogOpen} onClose={handleNRDClose}>
        <DialogTitle id="not-required-comment-title">Activity Not Required Comment</DialogTitle>
        <DialogContent>
          <DialogContentText>Please provide a note on this activity not being required for completion of the process:</DialogContentText>
          <TextField
            autoFocus
            margin="dense"
            id="notRequiredComment"
            label="Activity Not Required Comment"
            onChange={this.handleNotRequiredCommentChange}
            value={notRequiredComments}
            type="Text"
            fullWidth
          />
        </DialogContent>
        <DialogActions>
          <Button onClick={handleNRDSave} disabled={notRequiredComments.length < 5} color="primary">
            Save Comment
          </Button>
          <Button onClick={handleNRDClose} color="primary">
            Cancel
          </Button>
        </DialogActions>
      </Dialog>
    );
  }
}

interface ActivityNotRequiredCommentProps {
  notRequiredDialogOpen: boolean;
  handleNRDClose: () => void;
  handleNRDSave: () => void;
  notRequiredComments: string;
  onNotRequiredCommentChange: (newComment: string, now?: boolean) => void;
}
