import React from 'react';
import autobind from 'autobind-decorator';

import CircularProgress from '@material-ui/core/CircularProgress';
import Typography from '@material-ui/core/Typography';
import Select from '@material-ui/core/Select';

export default class ResponsiblePartyArea extends React.PureComponent<ResponsiblePartyAreaProps> {
  @autobind
  handleResponsiblePartyChange(event: React.ChangeEvent<{ name?: string | undefined; value: any }>) {
    if (this.props.onResponsiblePartyChange) {
      this.props.onResponsiblePartyChange(event.target.value);
    }
  }

  renderResponsibleParties(): React.ReactElement<any>[] {
    const components: React.ReactElement<any>[] = [];
    components.push(
      <option key="" value="">
        No Responsible Party
      </option>
    );
    for (const rp of this.props.availableResponsibleParties) {
      components.push(
        <option key={rp.text} value={rp.text}>
          {rp.text}
        </option>
      );
    }
    return components;
  }

  render() {
    const { classes, allowActions, isUpdatingResponsibleParty, responsibleParty, availableResponsibleParties } = this.props;
    return (
      <>
        <Typography variant="caption">Responsible Party</Typography>
        {isUpdatingResponsibleParty && <CircularProgress variant="indeterminate" disableShrink className={classes.progressSpinner} size={24} thickness={4} />}
        {!isUpdatingResponsibleParty && (
          <Select native fullWidth value={responsibleParty} disabled={!allowActions} className={classes.responsiblePartyTextField} onChange={this.handleResponsiblePartyChange}>
            {availableResponsibleParties && this.renderResponsibleParties()}
          </Select>
        )}
      </>
    );
  }
}

interface ResponsiblePartyAreaProps {
  classes: {
    progressSpinner: string;
    responsiblePartyTextField?: string;
  };
  allowActions: boolean;
  isUpdatingResponsibleParty: boolean;
  responsibleParty: string;
  availableResponsibleParties: { value: string; text: string }[];
  onResponsiblePartyChange?: (newResponsibleParty: string) => void;
}
