import 'draft-js/dist/Draft.css';
import React from 'react';
import { Editor, EditorState, RichUtils, getDefaultKeyBinding, ContentBlock, DraftHandleValue, CompositeDecorator, Entity, ContentState, CharacterMetadata } from 'draft-js';
import { stateFromHTML } from 'draft-js-import-html';
import autobind from 'autobind-decorator';
import { stateToHTML } from 'draft-js-export-html';
import { Button, Paper, Grid, Theme, createStyles, withStyles, WithStyles, TextField } from '@material-ui/core';
import Popover from '@material-ui/core/Popover';
import debounce from 'lodash/debounce';

const styles = (theme: Theme) =>
  createStyles({
    root: {
      flexGrow: 1
    },
    hyperlinkPopover: {
      padding: theme.spacing(),
      backgroundColor: theme.palette.grey[50],
      flexGrow: 1,
      width: theme.spacing() * 90
    }
  });

class RichEditorEditor extends React.PureComponent<RichEditorEditorProps, RichEditorEditorState> {
  private _editor: Editor | undefined;
  private _isMounted = false;

  debouncedEditorChange: (editorState: EditorState) => Promise<void>;

  constructor(props: RichEditorEditorProps) {
    super(props);
    const decorator = new CompositeDecorator([
      {
        strategy: this.findLinkEntities,
        component: this.Link
      }
    ]);
    this.state = {
      editorState: EditorState.createWithContent(stateFromHTML(props.rawText), decorator),
      rawText: props.rawText,
      showURLInput: false,
      urlValue: '',
      anchorEl: null
    };
    this.debouncedEditorChange = debounce(this.performEditorChange, this.props.waitForDebounce);
  }

  public async componentDidMount() {
    this._isMounted = true;
  }

  public async componentWillUnmount() {
    this._isMounted = false;
  }

  @autobind
  async performEditorChange(editorState: EditorState) {
    const contentState = editorState.getCurrentContent();
    if (this._isMounted && this.props.rawText !== stateToHTML(contentState)) {
      this.props.onChange(stateToHTML(contentState));
    }
  }

  @autobind
  setSetState(html: string) {
    const decorator = new CompositeDecorator([
      {
        strategy: this.findLinkEntities,
        component: this.Link
      }
    ]);
    let editorState = EditorState.createWithContent(stateFromHTML(html), decorator);
    this.setState({ editorState: editorState }, () => {
      if (this._editor) {
        this._editor.focus();
      }
    });
  }

  @autobind
  handleBlur() {
    this.props.onChanging && this.props.onChanging(true);
  }

  @autobind
  handleFocus() {
    this.props.onChanging && this.props.onChanging(false);
  }

  @autobind
  onChange(editorState: EditorState) {
    this.props.onChanging && this.props.onChanging(false);
    this.setState({
      editorState
    });
    this.debouncedEditorChange(editorState);
  }

  @autobind
  focus() {
    if (this._editor) {
      this._editor.focus();
    }
  }

  @autobind
  handleKeyCommand(command: string, editorState: EditorState): DraftHandleValue {
    const newState = RichUtils.handleKeyCommand(editorState, command);
    if (newState) {
      console.log('COMMENTS COMMENTS ');
      this.onChange(newState);
      return 'handled';
    }
    return 'not-handled';
  }

  @autobind
  mapKeyToEditorCommand(e: React.KeyboardEvent): string | null {
    if (e.keyCode === 9 /* TAB */) {
      const newEditorState = RichUtils.onTab(e, this.state.editorState, 4 /* maxDepth */);
      if (newEditorState !== this.state.editorState) {
        this.onChange(newEditorState);
      }
      return null;
    }
    return getDefaultKeyBinding(e);
  }

  @autobind
  onURLChange(e: React.ChangeEvent<HTMLInputElement>) {
    this.setState({ urlValue: e.target.value });
  }

  @autobind
  toggleBlockType(e: React.MouseEvent<HTMLSpanElement>, blockType: string) {
    this.onChange(RichUtils.toggleBlockType(this.state.editorState, blockType));
  }

  @autobind
  toggleInlineStyle(e: React.MouseEvent<HTMLSpanElement>, inlineStyle: string) {
    if (inlineStyle === 'ANCHOR') {
      this.setState(
        {
          anchorEl: e.currentTarget
        },
        () => {
          this.handleAddLink();
        }
      );
      return;
    }
    this.onChange(RichUtils.toggleInlineStyle(this.state.editorState, inlineStyle));
  }

  @autobind
  getBlockStyle(block: ContentBlock): string {
    switch (block.getType()) {
      case 'blockquote':
        return 'RichEditor-blockquote';
      default:
        return '';
    }
  }

  @autobind
  handleAddLink() {
    const { editorState } = this.state;
    const selection = editorState.getSelection();
    if (!selection.isCollapsed()) {
      this.setState({
        showURLInput: true,
        urlValue: ''
      });
    }
  }

  @autobind
  closeLink() {
    this.setState({ showURLInput: false, urlValue: '' }, () => {
      if (this._editor) {
        this._editor.focus();
      }
    });
  }

  @autobind
  confirmLink(e: React.MouseEvent | React.KeyboardEvent) {
    e.preventDefault();
    const { editorState, urlValue } = this.state;
    const entityKey = Entity.create('LINK', 'MUTABLE', { url: urlValue });
    this.setState(
      {
        editorState: RichUtils.toggleLink(editorState, editorState.getSelection(), entityKey),
        showURLInput: false,
        urlValue: ''
      },
      () => {
        this.props.onChange(stateToHTML(this.state.editorState.getCurrentContent()));
        if (this._editor) {
          this._editor.focus();
        }
      }
    );
  }

  @autobind
  Link(props: LinkProps) {
    const { url } = props.contentState.getEntity(props.entityKey).getData();
    return (
      <a href={url} target="_blank">
        {props.children}
      </a>
    );
  }

  @autobind
  findLinkEntities(contentBlock: ContentBlock, callback: any, contentState: ContentState) {
    contentBlock.findEntityRanges((character: CharacterMetadata) => {
      const entityKey = character.getEntity();
      return entityKey !== null && contentState.getEntity(entityKey).getType() === 'LINK';
    }, callback);
  }

  @autobind
  onLinkInputKeyDown(e: React.KeyboardEvent<HTMLInputElement>) {
    if (e.which === 13) {
      this.confirmLink(e);
    }
  }

  render() {
    const { editorState } = this.state;
    const { classes } = this.props;
    // If the user changes block type before entering any text, we can
    // either style the placeholder or hide it. Let's just hide it now.
    let className = 'RichEditor-editor';
    var contentState = editorState.getCurrentContent();
    if (!contentState.hasText()) {
      if (
        contentState
          .getBlockMap()
          .first()
          .getType() !== 'unstyled'
      ) {
        className += ' RichEditor-hidePlaceholder';
      }
    }

    return (
      <div className="RichEditor-root">
        <Popover
          id="simple-popper"
          open={this.state.showURLInput}
          anchorEl={this.state.anchorEl}
          anchorOrigin={{
            vertical: 'top',
            horizontal: 'center'
          }}
          transformOrigin={{
            vertical: 'bottom',
            horizontal: 'center'
          }}
        >
          <Paper className={classes.hyperlinkPopover}>
            <Grid className={classes.root} container spacing={0} justify="flex-end" alignContent="flex-end">
              <Grid item xs={12}>
                <TextField autoFocus={true} label="Enter URL" fullWidth={true} onChange={this.onURLChange} placeholder="Enter Url" value={this.state.urlValue} onKeyDown={this.onLinkInputKeyDown} />
              </Grid>
              <Grid item>
                <Button onClick={this.confirmLink}>Confirm</Button>
                <Button onClick={this.closeLink}>Close</Button>
              </Grid>
            </Grid>
          </Paper>
        </Popover>

        <BlockStyleControls editorState={editorState} onToggle={this.toggleBlockType} />
        <InlineStyleControls editorState={editorState} onToggle={this.toggleInlineStyle} onAddLink={this.handleAddLink} />
        <div className={className + ' rte'}>
          <Editor
            readOnly={this.props.readOnly}
            ref={(editor: Editor) => (this._editor = editor)}
            blockStyleFn={this.getBlockStyle}
            customStyleMap={styleMap}
            editorState={editorState}
            handleKeyCommand={this.handleKeyCommand}
            onBlur={this.handleBlur}
            onFocus={this.handleFocus}
            keyBindingFn={this.mapKeyToEditorCommand}
            onChange={this.onChange}
            placeholder={this.props.placeholder}
            spellCheck={true}
          />
        </div>
      </div>
    );
  }
}
// Custom overrides for "code" style.
const styleMap = {
  CODE: {
    backgroundColor: 'rgba(0, 0, 0, 0.05)',
    fontFamily: '"Inconsolata", "Menlo", "Consolas", monospace',
    fontSize: 16,
    padding: 2
  }
};

class StyleButton extends React.Component<StyleButtonProps> {
  constructor(props: StyleButtonProps) {
    super(props);
  }

  @autobind
  onToggle(e: React.MouseEvent<HTMLSpanElement>) {
    e.preventDefault();
    this.props.onToggle(e, this.props.style);
  }

  render() {
    let className = 'RichEditor-styleButton';
    if (this.props.active) {
      className += ' RichEditor-activeButton';
    }
    return (
      <span className={className} onMouseDown={this.onToggle}>
        {this.props.label}
      </span>
    );
  }
}

const BLOCK_TYPES = [
  { label: 'H1', style: 'header-one' },
  { label: 'H2', style: 'header-two' },
  { label: 'H3', style: 'header-three' },
  { label: 'H4', style: 'header-four' },
  { label: 'H5', style: 'header-five' },
  { label: 'H6', style: 'header-six' },
  { label: 'Blockquote', style: 'blockquote' },
  { label: 'UL', style: 'unordered-list-item' },
  { label: 'OL', style: 'ordered-list-item' },
  { label: 'Code Block', style: 'code-block' }
];

const BlockStyleControls = (props: BlockStyleControlsProps) => {
  const { editorState } = props;
  const selection = editorState.getSelection();
  const blockType = editorState
    .getCurrentContent()
    .getBlockForKey(selection.getStartKey())
    .getType();
  return (
    <div className="RichEditor-controls">
      {BLOCK_TYPES.map(type => (
        <StyleButton key={type.label} active={type.style === blockType} label={type.label} onToggle={props.onToggle} style={type.style} />
      ))}
    </div>
  );
};
const INLINE_STYLES = [
  { label: 'Bold', style: 'BOLD' },
  { label: 'Italic', style: 'ITALIC' },
  { label: 'Underline', style: 'UNDERLINE' },
  { label: 'Monospace', style: 'CODE' },
  { label: 'Create Hyperlink', style: 'ANCHOR' }
];

const InlineStyleControls = (props: InlineStyleControlsProps) => {
  const currentStyle = props.editorState.getCurrentInlineStyle();

  return (
    <div className="RichEditor-controls">
      {INLINE_STYLES.map(type => {
        return <StyleButton key={type.label} active={currentStyle.has(type.style)} label={type.label} onToggle={props.onToggle} style={type.style} />;
      })}
    </div>
  );
};

interface RichEditorEditorProps extends WithStyles {
  rawText: string;
  onChange(rawText: string): void;
  waitForDebounce?: number;
  placeholder?: string;
  onChanging?(completed: boolean): void;
  readOnly?: boolean;
}

interface RichEditorEditorState {
  editorState: EditorState;
  showURLInput: boolean;
  urlValue: string;
  rawText: string;
  anchorEl: any;
}

interface InlineStyleControlsProps {
  editorState: EditorState;
  onToggle(e: React.MouseEvent<HTMLSpanElement>, toggleBlockType: string): void;
  onAddLink(): void;
}

interface StyleButtonProps {
  onToggle(e: React.MouseEvent<HTMLSpanElement>, style: string): void;
  style: string;
  active: boolean;
  label: string;
}

interface LinkProps {
  contentState: ContentState;
  entityKey: string;
  children: any;
  onToggle(e: React.MouseEvent<HTMLSpanElement>, toggleBlockType: string): void;
}

interface BlockStyleControlsProps {
  editorState: EditorState;
  onToggle(e: React.MouseEvent<HTMLSpanElement>, toggleBlockType: string): void;
}

export default withStyles(styles)(RichEditorEditor);
