  @autobind
  async handleCompleteRejectProcess(
    reason: string,
    info?: {
      templateId?: string;
      procInstanceId: string;
      procInstanceName: string;
    }
  ) {
    const { process } = this.state;
    if (!process) {
      return;
    }

    this.setState({
      isRejecting: true
    });

    const updatedProcess = await this.context.processStore.rejectProcess(process.id, reason);

    this.setState({
      isRejecting: false,
      showRejectDialog: false,
      process: updatedProcess
    });
    if (info === undefined) {
      this.props.history.push('/processes');
    } else {
      if (info && info.templateId) {
        this.props.history.push(`/templates?pt=${info.templateId}&start=${info.procInstanceId}&name=${info.procInstanceName}`);
      }
    }
  }

