  @autobind
  getIsActivityExpanded(activity: Activity) {
    const { activeActivityId } = this.state;
    let expanded = false;

    if (Array.isArray(activeActivityId)) {
      expanded = !!activeActivityId.find(aaid => aaid === activity.id);
    } else if (activeActivityId) {
      expanded = activeActivityId === activity.id;
    }

    return expanded;
  }

  @autobind
  async handleActivityExpanded(activity: Activity, activated: boolean) {
    let { activeActivityId } = this.state;

    // Turn it into an array.
    if (Array.isArray(activeActivityId)) {
      // Do Nothing
    } else if (typeof activeActivityId === 'string') {
      activeActivityId = [activeActivityId];
    } else {
      activeActivityId = [];
    }

    const ix = activeActivityId.indexOf(activity.id);
    if (ix >= 0) {
      activeActivityId.splice(ix, 1);
    }

    if (activated) {
      activeActivityId.unshift(activity.id);
    }

    // Set the query string to contain the first five active activities.
    const values = queryString.parse(this.props.location.search);
    const aaids = activeActivityId.slice(0, 5);
    values.aaid = aaids;

    if (this.props.location.search !== queryString.stringify(values)) {
      this.props.history.replace(`${this.props.location.pathname}?${queryString.stringify(values)}`);
    }

    this.setState({
      activeActivityId
    });
  }

