import React from 'react';
import Button from '@material-ui/core/Button';
import Dialog from '@material-ui/core/Dialog';
import DialogActions from '@material-ui/core/DialogActions';
import DialogContent from '@material-ui/core/DialogContent';
import DialogContentText from '@material-ui/core/DialogContentText';
import DialogTitle from '@material-ui/core/DialogTitle';
import Checkbox from '@material-ui/core/Checkbox';
import { Fab, FormControl, InputLabel, Input, FormHelperText, FormControlLabel } from '@material-ui/core';
import AddIcon from '@material-ui/icons/Add';
import autobind from 'autobind-decorator';
import { AppContext } from '../../AppContext';

export default class FormDialog extends React.Component<FilterViewDialogProps, FilterViewDialogState> {
  static contextType = AppContext;
  context!: React.ContextType<typeof AppContext>;

  constructor(props: FilterViewDialogProps) {
    super(props);
    this.state = {
      open: false,
      name: '',
      requiredName: false,
      global: false,
      isDuplicate: false,
      overwriteViewName: false
    };
  }

  private isNewView(): boolean {
    const vf = this.context.personalizationStore.selectedViewFilter;
    return !(vf && vf.id !== '' && vf.id !== '0' && vf.id.length > 10);
  }

  @autobind
  handleClickOpen() {
    let name = '';
    if (this.isNewView() === false) {
      name = this.context.personalizationStore.selectedViewFilter.name;
    }
    this.setState({ open: true, overwriteViewName: false, name, requiredName: false });
  }

  @autobind
  handleClose() {
    this.setState({ open: false });
  }

  @autobind
  async handleSave() {
    let duplicateId: string = '';

    if (this.state.global) {
      this.context.personalizationStore.filterViews
        .filter(i => i.global === true)
        .forEach(f => {
          if (this.state.name.toLowerCase() === f.name.toLowerCase()) {
            duplicateId = f.id;
            return;
          }
        });
    } else {
      this.context.personalizationStore.filterViews
        .filter(i => i.global === false)
        .forEach(f => {
          if (this.state.name.toLowerCase() === f.name.toLowerCase()) {
            duplicateId = f.id;
            return;
          }
        });
    }

    if (duplicateId.length === 0 || this.state.overwriteViewName === true) {
      await this.props.onSaveView(this.state.name, this.state.global, duplicateId);
      this.setState({ open: false, isDuplicate: false });
      return;
    }
    this.setState({ isDuplicate: duplicateId.length >= 0 });
  }

  @autobind
  handleGlobalChkBox(event: React.ChangeEvent<HTMLInputElement>, checked: boolean) {
    this.setState({ global: checked });
  }

  @autobind
  handleoverwriteViewNameChkBox(event: React.ChangeEvent<HTMLInputElement>, checked: boolean) {
    this.setState({ overwriteViewName: checked });
  }

  @autobind
  handleViewNameChange(event: React.ChangeEvent<HTMLTextAreaElement | HTMLInputElement | HTMLSelectElement>): void {
    if (event && event.target) {
      this.setState({ name: event.target.value }, () => {
        this.setState({ requiredName: this.state.name === '' });
      });
    }
  }

  render() {
    const { open, name, requiredName, isDuplicate } = this.state;
    const isNewView = this.isNewView();
    return (
      <>
        <Fab
          size="small"
          variant="extended"
          color="primary"
          aria-label="Save Filtered View"
          onClick={this.handleClickOpen}
          style={{ margin: 3, paddingTop: 1, paddingBottom: 1, fontSize: '.8em', height: '25px' }}
        >
          <AddIcon style={{ fontSize: '.9em' }} /> Save Filtered View
        </Fab>
        <Dialog open={open} onClose={this.handleClose} aria-labelledby="form-dialog-title" maxWidth={'sm'} fullWidth={true}>
          <DialogTitle id="form-dialog-title">Save New Filter View</DialogTitle>
          <DialogContent>
            {isNewView && <DialogContentText>Please enter a name for the new filter view. This view will then appear under your profile name.</DialogContentText>}
            {!isNewView && <DialogContentText>Update view " {name} " or use a different view name to create a new one.</DialogContentText>}
            <FormControl error={requiredName || isDuplicate} fullWidth>
              <InputLabel htmlFor="component-error">View Name</InputLabel>
              <Input fullWidth id="viewname" value={name} onChange={this.handleViewNameChange} aria-describedby="viewname-text" />
              {requiredName && <FormHelperText id="viewname-text">View Name is required.</FormHelperText>}
              {isDuplicate && <FormHelperText id="viewname-text">View name already exists.</FormHelperText>}
              {isDuplicate && (
                <FormControlLabel
                  style={{ marginLeft: 50 }}
                  control={<Checkbox value="overwriteViewName" checked={this.state.overwriteViewName} onChange={this.handleoverwriteViewNameChkBox} />}
                  label={<span style={{ fontWeight: 600 }}>Overwrite?</span>}
                />
              )}
            </FormControl>
            <FormControlLabel control={<Checkbox value="global" checked={this.state.global} onChange={this.handleGlobalChkBox} />} label="Global" />
          </DialogContent>
          <DialogActions>
            <Button onClick={this.handleClose} color="primary">
              Cancel
            </Button>
            <Button onClick={this.handleSave} color="primary" disabled={requiredName}>
              Save
            </Button>
          </DialogActions>
        </Dialog>
      </>
    );
  }
}

interface FilterViewDialogProps {
  onSaveView(name: string, global: boolean, overwriteViewId?: string): void;
}

interface FilterViewDialogState {
  open: boolean;
  name: string;
  requiredName: boolean;
  global: boolean;
  isDuplicate: boolean;
  overwriteViewName: boolean;
}
