import React from 'react';
import autobind from 'autobind-decorator';
import { WithStyles, withStyles } from '@material-ui/core/styles';
import Button from '@material-ui/core/Button';
import Dialog from '@material-ui/core/Dialog';
import DialogActions from '@material-ui/core/DialogActions';
import DialogContent from '@material-ui/core/DialogContent';
import DialogContentText from '@material-ui/core/DialogContentText';
import DialogTitle from '@material-ui/core/DialogTitle';
import { styles } from './styles';

class ConfirmationDialog extends React.Component<ConfirmationDialogProps, {}> {
  @autobind
  handleClose(event: React.MouseEvent) {
    event.preventDefault();
    event.stopPropagation();
    this.props.onClose();
  }

  render() {
    const { title, message, children } = this.props;

    return (
      <div>
        <Dialog fullScreen={false} open={this.props.open} onClose={this.handleClose} aria-labelledby="responsive-dialog-title">
          <DialogTitle id="responsive-dialog-title">{title}</DialogTitle>
          <DialogContent>
            {message && <DialogContentText>{message}</DialogContentText>}
            {children}
          </DialogContent>
          <DialogActions>
            <Button onClick={this.handleClose} color="primary" autoFocus>
              Close
            </Button>
          </DialogActions>
        </Dialog>
      </div>
    );
  }
}

interface ConfirmationDialogProps extends WithStyles<typeof styles> {
  open: boolean;
  onClose: () => void;
  title: string;
  message?: string;
  children?: any;
}

export default withStyles(styles)(ConfirmationDialog);
