import React, { FunctionComponent, useContext } from 'react';
import { makeStyles, Theme, createStyles, useTheme } from '@material-ui/core/styles';
import Badge from '@material-ui/core/Badge';
import NotificationsIcon from '@material-ui/icons/NotificationsNone';
import { IconButton, ListItemIcon, Grid, Tooltip, FormControlLabel, Switch, Typography, CircularProgress } from '@material-ui/core';
import ChevronLeftIcon from '@material-ui/icons/ChevronLeft';
import ChevronRightIcon from '@material-ui/icons/ChevronRight';
import LayersClearIcon from '@material-ui/icons/LayersClear';
import RefreshRoundedIcon from '@material-ui/icons/RefreshRounded';
import VerifiedUserOutlinedIcon from '@material-ui/icons/VerifiedUserOutlined';
import Drawer from '@material-ui/core/Drawer';
import ArrowForwardIcon from '@material-ui/icons/ArrowForward';
import List from '@material-ui/core/List';
import ListItem from '@material-ui/core/ListItem';
import ListItemText from '@material-ui/core/ListItemText';
import Divider from '@material-ui/core/Divider';
import { AppContext } from '../../AppContext';
import { NotificationsProcessLogEntry, NotificationLogEntry } from '../../ppp/ProcessLogEntry';
import moment from 'moment';
import { useHistory } from 'react-router-dom';

const drawerWidth = 300;
const refreshInterval = 3200 * 1000; /// about 53 minutes

const useStyles = makeStyles((theme: Theme) =>
  createStyles({
    root: {
      width: 60
    },
    title: {
      flexGrow: 1
    },
    hide: {
      display: 'none'
    },
    drawer: {
      width: drawerWidth,
      flexShrink: 0
    },
    drawerPaper: {
      width: drawerWidth,
      backgroundColor: theme.palette.grey[50]
    },
    drawerHeader: {
      display: 'flex',
      alignItems: 'center',
      padding: theme.spacing(0, 1),
      ...theme.mixins.toolbar,
      justifyContent: 'flex-start',
      backgroundColor: theme.palette.grey[300]
    },
    listItem: {
      marginBottom: 1,
      '&.Mui-selected': {
        backgroundColor: theme.palette.grey[100]
      }
    },
    listItemNewItem: {
      marginBottom: 1,
      '&.Mui-selected': {},
      '&:hover': {}
    },
    content: {
      flexGrow: 1,
      padding: theme.spacing(3),
      transition: theme.transitions.create('margin', {
        easing: theme.transitions.easing.sharp,
        duration: theme.transitions.duration.leavingScreen
      }),
      marginRight: -drawerWidth
    },
    contentShift: {
      transition: theme.transitions.create('margin', {
        easing: theme.transitions.easing.easeOut,
        duration: theme.transitions.duration.enteringScreen
      }),
      marginRight: 0
    },
    Icon: {
      padding: 0,
      margin: 0,
      minWidth: 30
    },
    fabProgress: {
      position: 'absolute',
      zIndex: 1
    },
    spin: {
      animationName: 'spin',
      animationDuration: '4000ms',
      animationIterationCount: 'infinite',
      animationTimingFunction: 'linear'
    }
  })
);

type NotificationsProps = {
  disabled: boolean;
  currentUser: any;
};

const getTitle = (currentUser: any): string => {
  if (currentUser && currentUser.Title) {
    return currentUser.Title;
  }
  return '';
};

export const Notifications: FunctionComponent<NotificationsProps> = ({ disabled, currentUser }) => {
  const classes = useStyles();
  const theme = useTheme();
  const appContext = useContext(AppContext);
  const history = useHistory();
  const [open, setOpen] = React.useState(false);
  const [includeOwns, setIncludeOwns] = React.useState(false);
  const [visibleControl, setVisibleControl] = React.useState(false);
  const [unread, setUnread] = React.useState(0);
  const [logs, setLogs] = React.useState<NotificationsProcessLogEntry[]>();
  const [isRefreshing, setIsRefreshing] = React.useState<boolean>(false);
  let isAutoRefreshingData = false;
  let currentCacheId: string | undefined = undefined;
  const currentUserText = getTitle(currentUser);

  const fetchData = async (noCache?: boolean) => {
    setIsRefreshing(true);
    const userConfig = await appContext.personalizationStore.getUserConfig();
    if (userConfig && userConfig.notificationsSection) {
      // setIncludeOwns(userConfig.notificationsSection.includeOwns);
      if (noCache === true) {
        currentCacheId = undefined;
      }
      const results = await appContext.processLogStore.getProcessLogs(userConfig.notificationsSection.lastToken, currentCacheId);
      if (results && results.cacheId !== currentCacheId) {
        let filteredProcessLog: NotificationLogEntry[] = results.logEntries;
        if (userConfig.notificationsSection.includeOwns !== true) {
          filteredProcessLog = results.logEntries.filter(el => {
            if (el && el.user) {
              const a = el.user.displayName.toLowerCase();
              const b = currentUserText.toLowerCase();
              if (a === b) {
                return true; // TODO: not filtering yet
              }
            }
            return true;
          });
        }
        const numOfNew = filteredProcessLog.reduce((total, el) => {
          if (el.timeStamp) {
            const last = new Date(el.timeStamp);
            const token = new Date(userConfig.notificationsSection.lastToken);
            return last > token ? total + 1 : total;
          }
          return total;
        }, 0);
        setUnread(numOfNew);
        setLogs(filteredProcessLog || []);
        currentCacheId = results.cacheId;
      }
    }
    setIsRefreshing(false);
  };

  React.useEffect(
    () => {
      setVisibleControl(false);
      let interval: any;
      if (currentUser && currentUser.Title && currentUser.Title.length > 1) {
        fetchData().then(() => {
          interval = setInterval(async () => {
            if (currentUser && currentUser.Title && currentUser.Title.length > 0) {
              isAutoRefreshingData = true;
              await fetchData();
              isAutoRefreshingData = false;
            }
          }, refreshInterval);
        });
      }
      return () => {
        if (interval) {
          clearInterval(interval);
        }
      };
    },
    [currentUser]
  );

  const handleDrawerOpen = async () => {
    setOpen(true);
  };

  const handleDrawerClose = () => {
    setOpen(false);
  };

  const handleClearBadge = async () => {
    setUnread(0);
    await appContext.personalizationStore.saveNotificationsToken();
    if (!isAutoRefreshingData) {
      await fetchData();
    }
  };

  const handleGetLogs = async () => {
    await fetchData(true);
  };

  const handleListItemClick = (event: React.MouseEvent<HTMLDivElement, MouseEvent>, index: number, log: any) => {
    if (log && log.processId && log.processId.length > 10) {
      history.push(`/process/${log.processId}`);
    }
  };

  const handleIncludeMine = async (event: React.ChangeEvent<HTMLInputElement>) => {
    const val = event.target.checked;
    await appContext.personalizationStore.saveIncludeOwn(val);
    setIncludeOwns(val);
  };

  const setIcons = (kind: string) => {
    switch (kind) {
      case 'ProcessCompleted':
      case 'ProcessStarted':
      case 'ProcessDeleted':
      case 'ProcessRejected':
      case 'ProcessCompleted':
      case 'ProcessUnsigned':
        return <ArrowForwardIcon />;
      case 'MonitoringAndReviewProcessCompleted':
      case 'MonitoringAndReviewProcessOwnerChanged':
        return <VerifiedUserOutlinedIcon />;
      default:
        return <ArrowForwardIcon />;
    }
  };

  return (
    <div className={classes.root} style={{ margin: 0, padding: 0 }}>
      <IconButton onClick={handleDrawerOpen} style={{ marginTop: 5 }} aria-label="show 17 new notifications" color="inherit">
        <Badge badgeContent={unread} color="secondary">
          <NotificationsIcon />
        </Badge>
      </IconButton>
      <Drawer
        className={classes.drawer}
        variant="persistent"
        anchor="right"
        open={open}
        classes={{
          paper: classes.drawerPaper
        }}
      >
        <div className={classes.drawerHeader}>
          <Grid container direction="row" justify="space-between">
            <Grid item>
              <IconButton onClick={handleDrawerClose}>{theme.direction === 'rtl' ? <ChevronLeftIcon /> : <ChevronRightIcon />}</IconButton>
            </Grid>
            {visibleControl && (
              <Grid item>
                <Tooltip title="Include or show my changes in the logs.">
                  <span>
                    <FormControlLabel control={<Switch onChange={handleIncludeMine} checked={includeOwns} color="secondary" />} label="Include mine" labelPlacement="bottom" />
                  </span>
                </Tooltip>
              </Grid>
            )}
            <Grid item>
              <Tooltip title="Get or refresh logs">
                <span>
                  <IconButton onClick={handleGetLogs} disabled={isRefreshing}>
                    <RefreshRoundedIcon /> {isRefreshing && <CircularProgress size={32} className={classes.fabProgress} />}
                  </IconButton>
                </span>
              </Tooltip>
            </Grid>
            <Grid item>
              <Tooltip title="Mark Notifications as Read">
                <span>
                  <IconButton onClick={handleClearBadge}>
                    <LayersClearIcon />{' '}
                  </IconButton>
                </span>
              </Tooltip>
            </Grid>
          </Grid>
        </div>
        <Divider />
        <List>
          {logs &&
            logs.map((text, index) => (
              <Tooltip title={text.processName} key={index} onClick={event => handleListItemClick(event, index, text)}>
                <ListItem button key={index} className={index < unread ? classes.listItemNewItem : classes.listItem} selected={index < unread ? true : false}>
                  <ListItemIcon className={classes.Icon}>{setIcons(text.kind)}</ListItemIcon>
                  <ListItemText
                    primary={
                      <Typography style={{ fontWeight: index < unread ? 500 : undefined, fontSize: index < unread ? '1.3em' : undefined }}>{text.kind.replace(/([a-z])([A-Z])/g, '$1 $2')}</Typography>
                    }
                    secondary={`${moment(text.timeStamp || '').format('lll')} by ${(text.user && text.user.displayName) || ''}`}
                  />
                </ListItem>
              </Tooltip>
            ))}
        </List>
      </Drawer>
    </div>
  );
};
