import React from 'react';
import autobind from 'autobind-decorator';

import CircularProgress from '@material-ui/core/CircularProgress';
import Dialog from '@material-ui/core/Dialog';
import DialogTitle from '@material-ui/core/DialogTitle';
import DialogContent from '@material-ui/core/DialogContent';
import DialogActions from '@material-ui/core/DialogActions';
import Button from '@material-ui/core/Button';

import { Process, Activity, Attachment } from '../../ppp';
import Grid from '@material-ui/core/Grid';

export default class DeleteProcessAttachmentDialog extends React.Component<DeleteProcessAttachmentDialogProps, ProcessAttachmentDialogState> {
  constructor(props: DeleteProcessAttachmentDialogProps) {
    super(props);

    this.state = {
      isDeleting: false
    };
  }

  @autobind
  handleOnClose() {
    if (this.props.onClose) {
      this.props.onClose();
    }
  }

  @autobind
  async handleDeleteProcessActivityAttachment() {
    this.setState({
      isDeleting: true
    });

    const { activity, attachment } = this.props;

    if (!activity || !attachment) {
      return;
    }

    await this.props.onDeleteProcessTemplate(activity, attachment);

    this.setState({
      isDeleting: false
    });
  }

  render() {
    const { classes, attachment } = this.props;
    const { isDeleting } = this.state;
    return (
      <Dialog open={this.props.open} onClose={this.handleOnClose} aria-labelledby="confirm-delete-process-activity-attachment-dialog-title" fullWidth={true} maxWidth="sm">
        <DialogTitle id="confirm-delete-process-activity-attachment-dialog-title">Delete Process Activity Attachment?</DialogTitle>
        <DialogContent>
          <div>
            Are you sure you wish to delete the attachment named{' '}
            <strong>
              <i>{attachment && (attachment.originalFileName ? attachment.originalFileName : attachment.fileName)}</i>
            </strong>
          </div>
        </DialogContent>
        <DialogActions>
          {isDeleting && (
            <Grid container style={{ margin: '15px' }}>
              <CircularProgress variant="indeterminate" disableShrink className={classes.progressSpinner} size={24} thickness={4} />
            </Grid>
          )}
          {!isDeleting && (
            <>
              <Button onClick={this.handleOnClose} color="primary">
                Cancel
              </Button>
              <Button onClick={this.handleDeleteProcessActivityAttachment} color="primary">
                Delete
              </Button>
            </>
          )}
        </DialogActions>
      </Dialog>
    );
  }
}

interface DeleteProcessAttachmentDialogProps {
  classes: {
    progressSpinner: string;
  };
  open: boolean;
  activity?: Activity;
  attachment?: Attachment;
  onClose?: (updatedProcess?: Process) => void;
  onDeleteProcessTemplate: (activity: Activity, attachment: Attachment) => Promise<void>;
}

interface ProcessAttachmentDialogState {
  isDeleting: boolean;
}
