import { sp, ContextInfo, List } from '@pnp/sp';
export class SPRestContext implements SPContext {
  private ctx: ContextInfo | undefined;
  private currentUser: any;
  private reportingList: List | undefined = undefined;
  private reportingListName = 'OFS PPP Reporting List';
  private reportingListViewName = 'BPM Reporting List Excel View';

  public async getRootWebTitle() {
    return 'OFS Business Process Management Portal (BPMP)';
  }

  public async getCurrentUser(): Promise<any> {
    if (this.currentUser) {
      return this.currentUser;
    } else {
      this.currentUser = await sp.web.currentUser.get();
      return this.currentUser;
    }
  }

  public async getContextInfo(): Promise<ContextInfo> {
    if (this.ctx) {
      return this.ctx;
    }

    const ctx = await sp.site.getContextInfo();
    this.ctx = ctx;
    return ctx;
  }

  public async getSharePointOrigin(): Promise<string> {
    /* istanbul ignore next */
    if (process.env.NODE_ENV !== 'production') {
      return 'http://localhost:3001';
    }

    const ctx = (await this.getContextInfo()) as any;
    const origin = ctx.WebFullUrl; // `${protocol}//${hostname}${port.length ? `:${port}` : ''}`;
    return origin;
  }

  private getTitle(currentUser: any): string {
    if (currentUser && currentUser.Title) {
      return currentUser.Title;
    }
    return '';
  }

  public isAdministrator(currentUser: any): boolean {
    if (!currentUser) {
      return false;
    }
    const administrators = [
      'Administrator',
      'SLOWBURN\\Administrator', // Jaime's SP dev environment is peculiar
      'Loesley, Barton (Contractor)',
      'Guerrero, Jaime (Contractor)',
      'Patil, Ravindra (Contractor)',
      'Agrinya, Emmanuel (Contractor)',
      'Weiner, Joseph (Contractor)',
      'Nicoletos, Anthony (Contractor)'
    ];
    let found = false;
    administrators.forEach(a => {
      if (this.getTitle(currentUser).toUpperCase() === a.toUpperCase()) {
        found = true;
      }
    });
    return found;
  }

  public isSystemAdministrator(currentUser: any): boolean {
    const administrators = [
      'Administrator',
      'SLOWBURN\\Administrator', // Jaime's SP dev environment is peculiar
      'Loesley, Barton (Contractor)',
      'Guerrero, Jaime (Contractor)',
      'Patil, Ravi (Contractor)',
      'Agrinya, Emmanuel (Contractor)',
      'Weiner, Joseph (Contractor)'
    ];

    let found = false;
    administrators.forEach(a => {
      if (this.getTitle(currentUser).toUpperCase() === a.toUpperCase()) {
        found = true;
      }
    });
    return found;
  }

  public async buildReportingItemList(): Promise<boolean> {
    this.reportingList = await this.getReportingList();
    if (!this.reportingList) {
      await this.createReportingSPList();
    }
    return true;
  }

  private async getReportingList(): Promise<List | undefined> {
    let returnValue: List | undefined = undefined;
    try {
      returnValue = await sp.web.lists.getByTitle(this.reportingListName).get();
    } catch (ex) {
      return undefined;
    }
    return returnValue;
  }

  private async createReportingSPList(): Promise<object> {
    // https://docs.microsoft.com/en-us/previous-versions/office/sharepoint-visio/jj245640(v%3Doffice.15)
    const columns = [
      { name: 'ProcessId', type: 'SP.FieldText', kind: 2 },
      { name: 'ProcessType', type: 'SP.FieldText', kind: 2 },
      { name: 'TransactionDate', type: 'SP.FieldText', kind: 2 },
      { name: 'FiscalYear', type: 'SP.FieldText', kind: 2 },
      { name: 'ProcessName', type: 'SP.FieldText', kind: 2 },
      { name: 'FunctionArea', type: 'SP.FieldText', kind: 2 },
      { name: 'Status', type: 'SP.FieldText', kind: 2 },
      { name: 'SignOffDate', type: 'SP.FieldText', kind: 2 },
      { name: 'SignOffUser', type: 'SP.FieldText', kind: 2 },
      { name: 'RejectionReason', type: 'SP.FieldText', kind: 2 },
      { name: 'RejectionDate', type: 'SP.FieldText', kind: 2 },
      { name: 'RejectionUser', type: 'SP.FieldText', kind: 2 },
      { name: 'Notes', type: 'SP.FieldText', kind: 3 },
      { name: 'InitiatedOn', type: 'SP.FieldText', kind: 2 },
      { name: 'initiatedBy', type: 'SP.FieldText', kind: 2 },
      { name: 'RelatedRejectedProcessInstanceId', type: 'SP.FieldText', kind: 2 },
      { name: 'RelatedRejectedName', type: 'SP.FieldText', kind: 2 },
      { name: 'ModifiedBy', type: 'SP.FieldText', kind: 2 },
      { name: 'UnSigned', type: 'SP.FieldText', kind: 2 },
      // ACTIVITIES
      { name: 'ActivityId', type: 'SP.FieldText', kind: 2 },
      { name: 'ActivitySequence', type: 'SP.FieldText', kind: 1 },
      { name: 'ActivityDescription', type: 'SP.FieldText', kind: 3 },
      { name: 'ActivityTooltip', type: 'SP.FieldText', kind: 3 },
      { name: 'ActivityDependsOnPrevActivity', type: 'SP.FieldText', kind: 2 },
      { name: 'ActivityStatus', type: 'SP.FieldText', kind: 2 },
      { name: 'ActivityResponsibleParty', type: 'SP.FieldText', kind: 2 },
      { name: 'ActivityReviewedBy', type: 'SP.FieldText', kind: 2 },
      { name: 'ActivityReviewedOn', type: 'SP.FieldText', kind: 2 },
      { name: 'ActivityCompletedBy', type: 'SP.FieldText', kind: 2 },
      { name: 'ActivityCompletedOn', type: 'SP.FieldText', kind: 2 },
      { name: 'ActivityNotRequiredSetUser', type: 'SP.FieldText', kind: 2 },
      { name: 'ActivityNotRequiredSetDate', type: 'SP.FieldText', kind: 2 },
      { name: 'ActivityComments', type: 'SP.FieldText', kind: 3 },
      // M and R
      { name: 'MandR Status', type: 'SP.FieldText', kind: 2 },
      { name: 'MandR Signoff Date', type: 'SP.FieldText', kind: 2 },
      { name: 'MandR Signoff By', type: 'SP.FieldText', kind: 2 },
      { name: 'MandR Exception Exists', type: 'SP.FieldText', kind: 2 }
    ];
    const ls = await sp.web.lists.ensure(this.reportingListName);
    if (ls.created) {
      for (let i = 0; i < columns.length; i++) {
        await ls.list.fields.add(columns[i].name, columns[i].type, {
          // see: https://docs.microsoft.com/en-us/previous-versions/office/sharepoint-server/ee540543(v=office.15)?redirectedfrom=MSDN
          FieldTypeKind: columns[i].kind,
          Filterable: true,
          Hidden: false,
          EnforceUniqueValues: false
        });
      }

      // create a new view with specific properties
      const v = await ls.list.views.add(this.reportingListViewName, false, {
        ViewQuery: `<OrderBy><FieldRef Name='ProcessId' /><FieldRef Name='ActivitySequence' Ascending='True' /></OrderBy>`
      });
      // manipulate the view's fields

      await v.view.fields.add('ProcessId');
      await v.view.fields.add('ProcessType');
      await v.view.fields.add('FunctionArea');
      await v.view.fields.add('ProcessName');
      await v.view.fields.add('ActivitySequence');
      await v.view.fields.add('TransactionDate');
      await v.view.fields.add('FiscalYear');
      await v.view.fields.add('Status');
      await v.view.fields.add('SignOffDate');
      await v.view.fields.add('SignOffUser');
      await v.view.fields.add('RejectionReason');
      await v.view.fields.add('RejectionDate');
      await v.view.fields.add('RejectionUser');
      await v.view.fields.add('Notes');
      await v.view.fields.add('InitiatedOn');
      await v.view.fields.add('initiatedBy');
      await v.view.fields.add('RelatedRejectedProcessInstanceId');
      await v.view.fields.add('RelatedRejectedName');
      await v.view.fields.add('ModifiedBy');
      await v.view.fields.add('UnSigned');
      await v.view.fields.add('ActivityDescription');
      await v.view.fields.add('ActivityTooltip');
      await v.view.fields.add('ActivityDependsOnPrevActivity');
      await v.view.fields.add('ActivityStatus');
      await v.view.fields.add('ActivityResponsibleParty');
      await v.view.fields.add('ActivityReviewedBy');
      await v.view.fields.add('ActivityReviewedOn');
      await v.view.fields.add('ActivityCompletedBy');
      await v.view.fields.add('ActivityCompletedOn');
      await v.view.fields.add('ActivityNotRequiredSetUser');
      await v.view.fields.add('ActivityNotRequiredSetDate');
      await v.view.fields.add('ActivityComments');
      await v.view.fields.add('MandR Status');
      await v.view.fields.add('MandR Signoff Date');
      await v.view.fields.add('MandR Signoff By');
    }
    return { response: `${this.reportingListName} was created.` };
  }

  public getReportingListName() {
    return this.reportingListName;
  }

  public getReportingListViewName() {
    return `${this.reportingListName}/${this.reportingListViewName}`;
  }
}

export interface SPContext {
  getRootWebTitle(): Promise<string>;
  getCurrentUser(): Promise<any>;
  isAdministrator(currentUser: any): boolean;
  isSystemAdministrator(currentUser: any): boolean;
  getContextInfo(): Promise<object>;
  buildReportingItemList(): Promise<boolean>;
  getSharePointOrigin(): Promise<string>;
  getReportingListName(): string;
  getReportingListViewName(): string;
}
