import React from 'react';
import Tab from '@material-ui/core/Tab';
import Tabs from '@material-ui/core/Tabs';
import { Switch, Route } from 'react-router-dom';
import autobind from 'autobind-decorator';
import { withStyles, WithStyles, Theme, createStyles } from '@material-ui/core/styles';
import CssBaseline from '@material-ui/core/CssBaseline';
import AppBar from '@material-ui/core/AppBar';
import Toolbar from '@material-ui/core/Toolbar';
import Tooltip from '@material-ui/core/Tooltip';
import { AppContext, AppStore } from './AppContext';
import Footer from './components/controls/Footer';
import ProcessDetail from './components/ProcessDetail/';
import ProcessTemplateEditor from './components/ProcessTemplates/process-template-editor';
import { withRouter, RouteComponentProps } from 'react-router-dom';
import './App.css';
import bmp_1 from './bpm_1.png';
import AppBarProfile from './components/controls/AppBarProfile';
import { ThemeProvider } from '@material-ui/styles';
import { theme as appTheme } from './theme';
import { Notifications } from './components/controls/Notifications';
import Main from './components/Main';

const styles = (theme: Theme) =>
  createStyles({
    '@global': {
      body: {
        backgroundColor: theme.palette.common.white
      }
    },
    appBar: {
      position: 'relative'
    },
    appBody: {
      marginTop: theme.spacing(),
      minHeight: '55vh'
    },
    titleImage: {
      paddingRight: '100px'
    },
    toolbarTitle: {
      flex: 1
    },
    titleLink: {
      textDecoration: 'none',
      color: theme.palette.common.black
    },
    layout: {
      width: 'auto',
      marginLeft: theme.spacing(),
      marginRight: theme.spacing(),
      [theme.breakpoints.up(1270 + theme.spacing(3))]: {
        width: 1270,
        marginLeft: 'auto',
        marginRight: 'auto'
      }
    },
    appIcon: {
      fontSize: '30px',
      position: 'relative',
      bottom: '-5px',
      paddingRight: '5px'
    }
  });

class App extends React.Component<AppProps, AppState> {
  private readonly _childRef: any;

  constructor(props: AppProps) {
    super(props);
    this._childRef = React.createRef();
    this.state = {
      title: props.title,
      currentUser: undefined,
      appStore: props.appStore,
      isAdmin: false
    };
  }

  @autobind
  public async handleViewChange(id: string) {
    const filterView = this.props.appStore.personalizationStore.filterViews.find(fv => fv.id === id);
    if (filterView) {
      this.props.appStore.personalizationStore.selectedViewFilter = {
        id: filterView.id,
        name: filterView.name,
        global: filterView.global,
        filterSet: [...filterView.filterSet]
      };
      if (this._childRef && this._childRef.current) {
        this._childRef.current.refreshData && this._childRef.current.refreshData();
      }
    }
    if (this.props.appStore.personalizationStore.selectedViewFilter && this.props.appStore.personalizationStore.selectedViewFilter.id.length > 0) {
      if (this.props.location.pathname.indexOf('/processes') >= 0) {
        this.props.history.replace(`/processes/${id}`);
      } else {
        this.props.history.push(`/processes/${id}`);
      }
    }
  }

  public async componentDidMount() {
    const { appStore } = this.props;
    const currentUser = await appStore.spContext.getCurrentUser();
    try {
      const [fiscalYears, parties, title] = await Promise.all([
        appStore.processStore.getAvailableFiscalYears(),
        appStore.processTemplateStore.getPartiesList(),
        appStore.spContext.getRootWebTitle(),
        appStore.personalizationStore.loadViews(currentUser.Title)
      ]);

      this.setState({
        currentUser: currentUser,
        title: `${this.props.title} (${title})`,
        isAdmin: appStore.spContext.isAdministrator(currentUser)
      });
      appStore.personalizationStore.availableResponsibleParties = parties as { value: string; text: string }[];
      appStore.personalizationStore.configureFilters(fiscalYears);
    } catch (err) {
      console.log(err);
    }
  }

  @autobind
  handleTabChange(event: any, value: string) {
    if (value === '/processes') {
      if (this.props.appStore.personalizationStore.selectedViewFilter && this.props.appStore.personalizationStore.selectedViewFilter.id.length > 0) {
        value = `${value}/${this.props.appStore.personalizationStore.selectedViewFilter.id}`;
      }
    }
    this.props.history.push(value);
  }

  public render() {
    const { currentUser } = this.state;
    const { classes, appStore, ...other } = this.props;
    return (
      <AppContext.Provider value={appStore}>
        <ThemeProvider theme={appTheme}>
          <CssBaseline />
          <AppBar position="static" color="default" className={classes.appBar}>
            <Toolbar>
              <Tooltip title="OFS Business Process Management (BPM)">
                <a className={classes.titleLink} href="/">
                  <img src={bmp_1} className={classes.titleImage} alt={'OFS Business Process Management'} />
                </a>
              </Tooltip>
              <Tabs
                value={this.props.history.location.pathname === '/templates' ? '/templates' : '/processes'}
                onChange={this.handleTabChange}
                indicatorColor="primary"
                textColor="primary"
                variant="scrollable"
                scrollButtons="off"
                className={classes.toolbarTitle}
              >
                <Tab label="Process Instances" value="/processes" />
                <Tab label="Process Templates" value="/templates" />
              </Tabs>
              {this.state.isAdmin === true && <Notifications disabled={false} currentUser={currentUser} />}
              <AppBarProfile currentUser={currentUser} onViewChange={this.handleViewChange} />
            </Toolbar>
          </AppBar>
          <div className={classes.layout + ' ' + classes.appBody} role="main">
            <Switch>
              <Route
                path="/(|processes|templates)/:viewId?"
                render={() => <Main tab={this.props.history.location.pathname.startsWith('/templates') ? 'templates' : 'processes'} currentUser={currentUser} innerRef={this._childRef} />}
              />
              <Route path="/process/:id/:mr?" render={props => <ProcessDetail {...other} currentUser={currentUser} />} />
              <Route path="/template/:id/:ver?" render={props => <ProcessTemplateEditor {...other} />} />
            </Switch>
          </div>
          <Footer />
        </ThemeProvider>
      </AppContext.Provider>
    );
  }
}

export default withRouter(withStyles(styles)(App));

export interface AppProps extends WithStyles<typeof styles>, RouteComponentProps {
  title: string;
  appStore: AppStore;
}

export interface AppState {
  title: string;
  currentUser: any;
  appStore: AppStore;
  isAdmin: boolean;
}
