import React from 'react';
import autobind from 'autobind-decorator';

import { withStyles, WithStyles } from '@material-ui/core/styles';
import CircularProgress from '@material-ui/core/CircularProgress';
import Dialog from '@material-ui/core/Dialog';
import DialogTitle from '@material-ui/core/DialogTitle';
import DialogContent from '@material-ui/core/DialogContent';
import DialogActions from '@material-ui/core/DialogActions';
import Button from '@material-ui/core/Button';
import TextField from '@material-ui/core/TextField';
import { styles } from './styles';
import { AppContext } from '../../AppContext';
import { Process } from '../../ppp';
import Grid from '@material-ui/core/Grid';

class UnsignProcessDialog extends React.Component<UnsignProcessDialogProps, UnsignProcessDialogState> {
  static contextType = AppContext;
  context!: React.ContextType<typeof AppContext>;

  constructor(props: UnsignProcessDialogProps) {
    super(props);

    this.state = {
      reason: ''
    };
  }

  @autobind
  handleClick() {
    this.props.handleUnsign(this.state.reason);
  }

  @autobind
  handleOnClose() {
    if (this.props.onClose) {
      this.props.onClose(false);
    }
  }

  @autobind
  handleChange(event: React.ChangeEvent<HTMLInputElement>) {
    this.setState({
      reason: event.target.value
    });
  }

  render() {
    const { classes, process, isUnsigning } = this.props;
    const { reason } = this.state;
    return (
      <Dialog open={this.props.open} onClose={this.handleOnClose} aria-labelledby="confirm-unsign-process-dialog-title" fullWidth={true} maxWidth="xs">
        <DialogTitle id="confirm-unsign-process-dialog-title">Unsign Process</DialogTitle>
        <DialogContent>
          <div>
            Please provide a reason for unsigning the process,{' '}
            <strong>
              <i>{process.name}</i>
            </strong>
          </div>
          <TextField required label="Unsign Reason" onChange={this.handleChange} value={this.state.reason} fullWidth />
        </DialogContent>
        <DialogActions>
          {isUnsigning && (
            <Grid container style={{ margin: '15px' }}>
              <CircularProgress variant="indeterminate" disableShrink className={classes.facebook2} size={24} thickness={4} />
            </Grid>
          )}
          {!isUnsigning && (
            <>
              <Button onClick={this.handleOnClose} color="primary">
                Cancel
              </Button>
              <Button onClick={this.handleClick} color="primary" disabled={reason.length < 3}>
                Unsign
              </Button>
            </>
          )}
        </DialogActions>
      </Dialog>
    );
  }
}

export default withStyles(styles)(UnsignProcessDialog);

interface UnsignProcessDialogProps extends WithStyles<typeof styles> {
  open: boolean;
  onClose?: (unsigned: boolean) => void;
  process: Process;
  handleUnsign: (reason: string) => any;
  isUnsigning: boolean;
}

interface UnsignProcessDialogState {
  reason: string;
}
