import React from 'react';
import autobind from 'autobind-decorator';
import { withStyles, WithStyles } from '@material-ui/core/styles';
import CircularProgress from '@material-ui/core/CircularProgress';
import Dialog from '@material-ui/core/Dialog';
import DialogTitle from '@material-ui/core/DialogTitle';
import DialogContent from '@material-ui/core/DialogContent';
import DialogActions from '@material-ui/core/DialogActions';
import Button from '@material-ui/core/Button';

import { styles } from './styles';
import { AppContext } from '../../AppContext';
import { ProcessTemplate } from '../../ppp';
import { ProcessTemplateContainer } from '../../ppp/ProcessTemplateContainer';
import Grid from '@material-ui/core/Grid';

class MoveProcessTemplateDialog extends React.Component<MoveProcessTemplateDialogProps, MoveProcessTemplateDialogState> {
  static contextType = AppContext;
  context!: React.ContextType<typeof AppContext>;

  constructor(props: MoveProcessTemplateDialogProps) {
    super(props);

    this.state = {
      isMoving: false,
      processTemplate: props.processTemplate
    };
  }

  @autobind
  handleOnClose() {
    if (this.props.onClose) {
      this.props.onClose(undefined);
    }
  }

  @autobind
  async moveProcessTemplate() {
    const { onClose, processTemplateContainer } = this.props;
    const { processTemplate } = this.state;
    const { processTemplateStore } = this.context;

    if (!processTemplate.id) {
      if (onClose) {
        onClose(undefined);
      }
      return;
    }

    this.setState({
      isMoving: true
    });

    const result = await processTemplateStore.moveProcessTemplate(processTemplate.id, processTemplateContainer.path);

    this.setState({
      isMoving: false
    });

    if (onClose) {
      onClose(result);
    }
  }

  render() {
    const { classes, processTemplateContainer } = this.props;
    const { processTemplate, isMoving } = this.state;

    return (
      <Dialog open={this.props.open} onClose={this.handleOnClose} aria-labelledby="confirm-move-process-template-dialog-title" fullWidth={true} maxWidth="sm">
        <DialogTitle id="confirm-move-process-template-dialog-title">Move Process Template?</DialogTitle>
        <DialogContent>
          <div>
            Are you sure you wish to move the process template titled{' '}
            <strong>
              <i>{processTemplate.title}</i>
            </strong>{' '}
            to{' '}
            <strong>
              <i>{processTemplateContainer.path}</i>
            </strong>
          </div>
        </DialogContent>
        <DialogActions>
          {isMoving && (
            <Grid container style={{ margin: '15px' }}>
              <CircularProgress variant="indeterminate" disableShrink className={classes.facebook2} size={24} thickness={4} />
            </Grid>
          )}
          {!isMoving && (
            <>
              <Button onClick={this.handleOnClose} color="primary">
                Cancel
              </Button>
              <Button onClick={this.moveProcessTemplate} color="primary">
                Move
              </Button>
            </>
          )}
        </DialogActions>
      </Dialog>
    );
  }
}

export default withStyles(styles)(MoveProcessTemplateDialog);

interface MoveProcessTemplateDialogProps extends WithStyles<typeof styles> {
  open: boolean;
  onClose?: (processTemplate?: ProcessTemplate) => void;
  processTemplateContainer: ProcessTemplateContainer;
  processTemplate: ProcessTemplate;
}

interface MoveProcessTemplateDialogState {
  isMoving: boolean;
  processTemplate: ProcessTemplate;
}
