import React from 'react';
import { Theme, createStyles, withStyles, WithStyles } from '@material-ui/core/styles';
import { ActivityTemplate } from '../../ppp';
import { DragDropContext, Droppable, Draggable, DragUpdate, DroppableProvided, DroppableStateSnapshot, DraggableProvided, DraggableStateSnapshot } from 'react-beautiful-dnd';
import ActivityTemplateListItem from './ActivityTemplateListItem';
import autobind from 'autobind-decorator';
import findIndex from 'lodash/findIndex';

const styles = (theme: Theme) =>
  createStyles({
    list: {
      padding: 0,
      margin: 0,
      listStyle: 'none'
    },
    listitem: {
      margin: `0px`,
      padding: '2px 2px'
    }
  });

const getItemStyle = (isDragging: boolean, draggableStyle: any) => ({
  background: isDragging ? 'lightgrey' : '',
  ...draggableStyle
});

const reorder = (list: Array<ActivityTemplate>, startIndex: number, endIndex: number): Array<ActivityTemplate> => {
  const [removed] = list.splice(startIndex, 1);
  list.splice(endIndex, 0, removed);
  return list;
};

class ActivitiesList extends React.Component<ActivitiesListProps, ActivitiesListState> {
  constructor(props: ActivitiesListProps) {
    super(props);

    this.state = { activities: [] };
  }

  static getDerivedStateFromProps(nextProps: ActivitiesListProps, state: ActivitiesListState) {
    return { activities: nextProps.activities };
  }

  @autobind
  onDragEnd(result: DragUpdate) {
    if (!result.destination) {
      return;
    }
    if (this.state.activities) {
      const activities = reorder(this.state.activities, result.source.index, result.destination.index);
      this.props.onActivitiesOrderChange(activities);
    }
  }

  @autobind
  handleClickEdit(item: ActivityTemplate, index: number) {
    this.props.onClickEdit(item, index);
  }

  @autobind
  handleClickDelete(item: ActivityTemplate, index: number) {
    this.props.onClickDelete(item, index);
  }

  @autobind
  handleActivityTemplateChange(item: ActivityTemplate, index: number) {
    this.props.onActivityTemplateChange(item, index);
  }

  @autobind
  activityDepth(activityId: string, currentDepth?: number): number {
    if (currentDepth && currentDepth >= 5) {
      return 5;
    }

    if (!activityId) {
      return currentDepth || 0;
    }

    const { activities } = this.state;
    if (!activities || activities.length <= 0) {
      return currentDepth || 0;
    }

    const activityTemplateIndex = findIndex(activities, { id: activityId });
    if (activityTemplateIndex <= 0) {
      return currentDepth || 0;
    }

    const activityTemplate = activities[activityTemplateIndex];
    if (activityTemplate.dependsOnPreviousActivity) {
      return this.activityDepth(activities[activityTemplateIndex - 1].id, (currentDepth || 0) + 1);
    }

    return currentDepth || 0;
  }

  render() {
    const { classes, readOnly } = this.props;
    const { activities } = this.state;

    return (
      <DragDropContext onDragEnd={this.onDragEnd}>
        <Droppable droppableId="droppable" isDropDisabled={readOnly}>
          {(provided: DroppableProvided, snapshot: DroppableStateSnapshot) => (
            <div ref={provided.innerRef} className={classes.list}>
              {activities.map((activity: ActivityTemplate, index: number) => (
                <Draggable key={activity.id} draggableId={activity.id} index={index} isDragDisabled={readOnly}>
                  {(draggableProvided: DraggableProvided, draggableSnapshot: DraggableStateSnapshot) => (
                    <div
                      key={activity.id}
                      className={classes.listitem}
                      ref={draggableProvided.innerRef}
                      {...draggableProvided.draggableProps}
                      {...draggableProvided.dragHandleProps}
                      style={getItemStyle(draggableSnapshot.isDragging, draggableProvided.draggableProps.style)}
                    >
                      <ActivityTemplateListItem
                        key={index}
                        index={index}
                        readOnly={readOnly}
                        activityTemplate={activity}
                        activityDepth={this.activityDepth(activity.id)}
                        onClickEdit={this.handleClickEdit}
                        onClickDelete={this.handleClickDelete}
                        onActivityTemplateChange={this.handleActivityTemplateChange}
                      />
                    </div>
                  )}
                </Draggable>
              ))}
            </div>
          )}
        </Droppable>
      </DragDropContext>
    );
  }
}

interface ActivitiesListProps extends WithStyles {
  activities: Array<ActivityTemplate> | undefined;
  readOnly: boolean;
  onClickEdit(item: ActivityTemplate, index: number): any;
  onClickDelete(item: ActivityTemplate, index: number): any;
  onActivityTemplateChange(item: ActivityTemplate, index: number): any;
  onActivitiesOrderChange(activities: Array<ActivityTemplate>): void;
}

interface ActivitiesListState {
  activities: Array<ActivityTemplate>;
}

export default withStyles(styles)(ActivitiesList);
