import React, { FunctionComponent } from 'react';
import { makeStyles, Theme, createStyles } from '@material-ui/core/styles';
import { Box, IconButton, Paper, Grid, Typography } from '@material-ui/core';
import { NotedException } from '../../../ppp/NotedException';
import AttachmentsDisplay from '../../controls/AttachmentsDisplay';
import DisplayEditableField from '../../controls/DisplayEditableField';
import EditIcon from '@material-ui/icons/EditOutlined';
import DeleteIcon from '@material-ui/icons/Delete';
import moment from 'moment';
import DOMPurify from 'dompurify';

const useStyles = makeStyles((theme: Theme) =>
  createStyles({
    root: {
      margin: theme.spacing(2),
      backgroundColor: theme.palette.grey[50]
    },
    textField: {},
    infoArea: {
      padding: theme.spacing(1),
      marginTop: theme.spacing(2)
    },
    otherArea: {
      margin: theme.spacing(1)
    },
    lableArea: {
      marginLeft: theme.spacing(1)
    }
  })
);

type NotedExceptionProps = {
  notedException: NotedException;
  disabled: boolean;
  origin: string | undefined;
  onEditNotedExceptionClickOpen(id: string): void;
  onDeleteNotedException(id: string): void;
  onEditProcessOwner(id: string, value: string): void;
};

export const NotedExceptionDisplay: FunctionComponent<NotedExceptionProps> = ({ notedException, disabled, onEditNotedExceptionClickOpen, onDeleteNotedException, onEditProcessOwner, origin }) => {
  const classes = useStyles();
  const [values, setValues] = React.useState<NotedException>(notedException);

  const handleClickEdit = (id: string) => {
    onEditNotedExceptionClickOpen(id);
  };

  const handleClickDelete = (id: string) => {
    onDeleteNotedException(id);
  };

  const createMarkup = (value?: string) => {
    return { __html: DOMPurify.sanitize(value || '<p></p>') };
  };

  return (
    <>
      <Paper className={classes.root} style={{}}>
        <Grid container spacing={0} direction="row" wrap="nowrap" justify="center">
          <Grid item xs zeroMinWidth>
            <Box className={classes.otherArea}>
              <Typography variant={'body1'} component="span" color="inherit" noWrap className={classes.textField}>
                Exception Noted:&nbsp;
              </Typography>
              <div className={'rte'} style={{ fontSize: '1.2em' }} dangerouslySetInnerHTML={createMarkup(notedException.exceptionNoted)} />
            </Box>
            <Box className={classes.otherArea}>
              <Typography variant={'body1'} component="span" color="inherit" className={classes.textField}>
                Suggested Corrective Action:&nbsp;
              </Typography>
              <Typography variant={'body1'} component="span" color="inherit" className={classes.textField}>
                {notedException.suggestedCorrectiveAction}
              </Typography>
            </Box>
            <Box className={classes.otherArea}>
              Noted on&nbsp;
              <Box component="span" fontWeight="fontWeightMedium" fontStyle="italic">
                {moment(notedException.exceptionNotedTimeStamp).format('lll')}
              </Box>
              &nbsp;by&nbsp;
              <Box component="span" fontWeight="fontWeightMedium" fontStyle="italic">
                {notedException.exceptionNotedBy && notedException.exceptionNotedBy.displayName}
              </Box>
            </Box>
            <Box className={classes.otherArea}>
              <AttachmentsDisplay origin={origin || ''} labelText={'Attachments'} attachments={notedException && notedException.attachments} />
            </Box>
            <Box className={classes.otherArea}>
              <DisplayEditableField
                readOnly={disabled === true}
                isLoading={false}
                label="Process Owner Response"
                title="Process Owner Response: "
                field={(values && values.processOwnerResponse) || ''}
                size="small"
                onSave={async value => {
                  setValues({ ...values, processOwnerResponse: value });
                  onEditProcessOwner(notedException.id, value);
                }}
              />
            </Box>
            {notedException.processOwner && (
              <Box className={classes.lableArea}>
                Responded&nbsp;on&nbsp;
                <Box component="span" fontWeight="fontWeightMedium" fontStyle="italic">
                  {notedException.processOwnerResponseTimeStamp && moment(notedException.processOwnerResponseTimeStamp).format('lll')}
                </Box>
                &nbsp;by&nbsp;
                <Box component="span" fontWeight="fontWeightMedium" fontStyle="italic">
                  {notedException.processOwner && notedException.processOwner.displayName}
                </Box>
              </Box>
            )}
          </Grid>
          {disabled === false && (
            <Grid item>
              <IconButton disabled={disabled} edge="end" aria-label="Delete View" data-id={notedException.id} onClick={() => handleClickEdit(notedException.id)}>
                <EditIcon />
              </IconButton>
              <IconButton disabled={disabled} edge="end" aria-label="Delete View" data-id={notedException.id} onClick={() => handleClickDelete(notedException.id)}>
                <DeleteIcon />
              </IconButton>
            </Grid>
          )}
        </Grid>
      </Paper>
    </>
  );
};
