import React from 'react';
import { Theme, withStyles, WithStyles, createStyles } from '@material-ui/core/styles';
import { RouteComponentProps, withRouter } from 'react-router-dom';
import ProcessTemplateTree from '../ProcessTemplateTree';
import { AppContext } from '../../AppContext';

export const styles = (theme: Theme) => createStyles({});

class ProcessTemplateList extends React.PureComponent<ProcessTemplateListProps> {
  static contextType = AppContext;
  context!: React.ContextType<typeof AppContext>;

  constructor(props: ProcessTemplateListProps, context?: any) {
    super(props, context);

    this.state = {};
  }

  public render() {
    // Maybe 20 is a bit too large. But it'll satisfy the BR of expanding all folders by default.
    return <ProcessTemplateTree initialVisibleDepth={20} />;
  }
}

export default withRouter(withStyles(styles)(ProcessTemplateList));

interface ProcessTemplateListProps extends WithStyles<typeof styles>, RouteComponentProps<ProcessTemplateListRouterProps> {}

interface ProcessTemplateListRouterProps {
  path?: string;
}
