import React from 'react';
import autobind from 'autobind-decorator';

import { withStyles, WithStyles } from '@material-ui/core/styles';
import CircularProgress from '@material-ui/core/CircularProgress';
import Dialog from '@material-ui/core/Dialog';
import DialogTitle from '@material-ui/core/DialogTitle';
import DialogContent from '@material-ui/core/DialogContent';
import DialogActions from '@material-ui/core/DialogActions';
import Button from '@material-ui/core/Button';
import TextField from '@material-ui/core/TextField';
import { styles } from './styles';
import { AppContext } from '../../AppContext';
import { Process } from '../../ppp';
import Grid from '@material-ui/core/Grid';

class RejectProcessDialog extends React.Component<RejectProcessDialogProps, RejectProcessDialogState> {
  static contextType = AppContext;
  context!: React.ContextType<typeof AppContext>;

  constructor(props: RejectProcessDialogProps) {
    super(props);

    this.state = {
      reason: ''
    };
  }

  @autobind
  handleClick() {
    this.props.handleReject(this.state.reason, undefined);
  }

  @autobind
  handlePTClick() {
    const templateId = this.props.process.template.id;
    const procInstanceId = this.props.process.id;
    const procInstanceName = this.props.process.name;
    this.props.handleReject(this.state.reason, { templateId, procInstanceId, procInstanceName });
  }

  @autobind
  handleOnClose() {
    if (this.props.onClose) {
      this.props.onClose(false);
    }
  }

  @autobind
  handleChange(event: React.ChangeEvent<HTMLInputElement>) {
    this.setState({
      reason: event.target.value
    });
  }

  render() {
    const { classes, process, isRejectinging } = this.props;
    const { reason } = this.state;
    return (
      <Dialog open={this.props.open} onClose={this.handleOnClose} aria-labelledby="confirm-reject-process-dialog-title" fullWidth={true} maxWidth="sm">
        <DialogTitle id="confirm-reject-process-dialog-title">Reject Process</DialogTitle>
        <DialogContent>
          <div>
            Please provide a reason for rejecting the process,{' '}
            <strong>
              <i>{process.name}</i>
            </strong>
          </div>
          <TextField required label="Reject Reason" onChange={this.handleChange} value={this.state.reason} fullWidth />
        </DialogContent>
        <DialogActions>
          {isRejectinging && (
            <Grid container style={{ margin: '15px' }}>
              <CircularProgress variant="indeterminate" disableShrink className={classes.facebook2} size={24} thickness={4} />
            </Grid>
          )}
          {!isRejectinging && (
            <>
              <Button onClick={this.handleOnClose} color="primary">
                Cancel
              </Button>
              <Button onClick={this.handleClick} color="primary" disabled={reason.length < 3}>
                Reject and Close
              </Button>
              <Button onClick={this.handlePTClick} color="primary" disabled={reason.length < 3}>
                Reject and Instantiate New Process Instance
              </Button>
            </>
          )}
        </DialogActions>
      </Dialog>
    );
  }
}

export default withStyles(styles)(RejectProcessDialog);

interface RejectProcessDialogProps extends WithStyles<typeof styles> {
  open: boolean;
  onClose?: (rejected: boolean) => void;
  process: Process;
  handleReject: (
    reason: string,
    info?: {
      templateId?: string;
      procInstanceId: string;
      procInstanceName: string;
    }
  ) => any;
  isRejectinging: boolean;
}

interface RejectProcessDialogState {
  reason: string;
}
