import React from 'react';
import autobind from 'autobind-decorator';

import { withStyles, WithStyles } from '@material-ui/core/styles';
import CircularProgress from '@material-ui/core/CircularProgress';
import Dialog from '@material-ui/core/Dialog';
import DialogTitle from '@material-ui/core/DialogTitle';
import DialogContent from '@material-ui/core/DialogContent';
import DialogActions from '@material-ui/core/DialogActions';
import Button from '@material-ui/core/Button';

import { styles } from './styles';
import { AppContext } from '../../AppContext';
import { ProcessTemplate } from '../../ppp';
import { ProcessTemplateContainer } from '../../ppp/ProcessTemplateContainer';
import Grid from '@material-ui/core/Grid';

class DeleteProcessTemplateDialog extends React.Component<DeleteProcessTemplateDialogProps, DeleteProcessTemplateDialogState> {
  static contextType = AppContext;
  context!: React.ContextType<typeof AppContext>;

  constructor(props: DeleteProcessTemplateDialogProps) {
    super(props);

    this.state = {
      isDeleting: false,
      processTemplate: props.processTemplate
    };
  }

  @autobind
  handleOnClose() {
    if (this.props.onClose) {
      this.props.onClose(false);
    }
  }

  @autobind
  async deleteProcessTemplate() {
    const { onClose } = this.props;
    const { processTemplate } = this.state;
    const { processTemplateStore } = this.context;

    if (!processTemplate.id) {
      if (onClose) {
        onClose(false);
      }
      return;
    }

    this.setState({
      isDeleting: true
    });

    const result = await processTemplateStore.deleteProcessTemplateById(processTemplate.id);

    this.setState({
      isDeleting: false
    });

    if (onClose) {
      onClose(result);
    }
  }

  render() {
    const { classes } = this.props;
    const { processTemplate, isDeleting } = this.state;

    return (
      <Dialog open={this.props.open} onClose={this.handleOnClose} aria-labelledby="confirm-delete-process-template-dialog-title" fullWidth={true} maxWidth="xs">
        <DialogTitle id="confirm-delete-process-template-dialog-title">Delete Process Template?</DialogTitle>
        <DialogContent>
          <div>
            Are you sure you wish to delete the process template titled{' '}
            <strong>
              <i>{processTemplate.title}</i>
            </strong>
          </div>
        </DialogContent>
        <DialogActions>
          {isDeleting && (
            <Grid container style={{ margin: '15px' }}>
              <CircularProgress variant="indeterminate" disableShrink className={classes.facebook2} size={24} thickness={4} />
            </Grid>
          )}
          {!isDeleting && (
            <>
              <Button onClick={this.handleOnClose} color="primary">
                Cancel
              </Button>
              <Button onClick={this.deleteProcessTemplate} color="primary">
                Delete
              </Button>
            </>
          )}
        </DialogActions>
      </Dialog>
    );
  }
}

export default withStyles(styles)(DeleteProcessTemplateDialog);

interface DeleteProcessTemplateDialogProps extends WithStyles<typeof styles> {
  open: boolean;
  onClose?: (deleted: boolean) => void;
  processTemplateContainer: ProcessTemplateContainer;
  processTemplate: ProcessTemplate;
}

interface DeleteProcessTemplateDialogState {
  isDeleting: boolean;
  processTemplate: ProcessTemplate;
}
