import React, { FunctionComponent } from 'react';
import { makeStyles, Theme, createStyles } from '@material-ui/core/styles';
import { Box, TextField, Button } from '@material-ui/core';
import { NotedException } from '../../../ppp/NotedException';
import AttachmentsArea from '../../controls/AttachmentArea';
import { Attachment } from '../../../ppp';
import Dialog from '@material-ui/core/Dialog';
import DialogActions from '@material-ui/core/DialogActions';
import DialogContent from '@material-ui/core/DialogContent';
import DialogContentText from '@material-ui/core/DialogContentText';
import DialogTitle from '@material-ui/core/DialogTitle';
import RichEditorEditor from '../../controls/RichEditorEditor';

const useStyles = makeStyles((theme: Theme) =>
  createStyles({
    root: {},
    textField: {
      padding: theme.spacing(1),
      marginTop: theme.spacing(2)
    },
    infoArea: {
      padding: theme.spacing(1),
      marginTop: theme.spacing(2),
      backgroundColor: theme.palette.grey[100]
    },
    parentOverlay: {
      position: 'relative',
      flexBasis: '70%'
    },
    overlay: {
      position: 'absolute',
      top: 0,
      left: 5,
      'z-index': 10
    }
  })
);

type NotedExceptionProps = {
  notedException: NotedException;
  onSubmitNotedException(notedException: NotedException): void;
  onAttachmentUpload(notedException: NotedException, attachmentFile: File): Promise<Attachment | undefined>;
  onClose(): void;
  open: boolean;
  disabled: boolean;
  origin?: string;
};

export const NotedExceptionDialog: FunctionComponent<NotedExceptionProps> = props => {
  const classes = useStyles();
  const [values, setValues] = React.useState<NotedException>(props.notedException);

  React.useEffect(
    () => {
      setValues({ ...props.notedException });
    },
    [props.notedException.id]
  );

  const handleChange = (prop: keyof NotedException) => (event: React.ChangeEvent<HTMLInputElement>) => {
    setValues({ ...values, [prop]: event.target.value });
  };

  const handleClose = () => {
    props.onClose();
  };

  const handleSubmitNotedExeption = () => {
    props.onSubmitNotedException({ ...values });
  };

  const handleAttachmentClicked = (attachment: Attachment) => {
    window.open(props.origin + attachment.serverRelativeUrl, '_blank');
  };

  const handleAttachmentDeleteClicked = (attachment: Attachment) => {
    // NOTE: This wont remove the file from SP by design.
    // Just in case it is using that same  file in  another Noted Exception.
    if (attachment && values && values.attachments) {
      const index = values.attachments.findIndex(x => x.fileName === attachment.fileName);
      if (index >= 0) {
        values.attachments.splice(index, 1);
        setValues({ ...values, attachments: [...values.attachments] });
      }
    }
  };

  const handleProcessAttachmentUpload = async (attachmentFile: File) => {
    if (values.attachments) {
      const found = values.attachments.find(el => el.fileName.toLowerCase() === attachmentFile.name.toLowerCase());
      if (found) {
        return;
      }
    }

    const attachment = await props.onAttachmentUpload(values, attachmentFile);
    if (attachment) {
      setValues({ ...values, attachments: [...(values.attachments || []), attachment] });
    }
  };

  const handleNotesChange = async (rawText: string) => {
    setValues({ ...values, exceptionNoted: rawText });
  };

  return (
    <Dialog open={props.open} onClose={handleClose} fullWidth={true} maxWidth={'md'} aria-labelledby="new-noted-exception">
      <DialogTitle id="form-dialog-title">Noted Exception </DialogTitle>
      <DialogContent>
        <DialogContentText>Enter Exception Noted </DialogContentText>
        <Box>
          <div className={classes.parentOverlay}>
            <RichEditorEditor placeholder="Exception Noted ..." rawText={(values && values.exceptionNoted) || ''} onChange={handleNotesChange} waitForDebounce={200} />
          </div>
          <Box m={1}>
            <TextField
              id="SuggestedCorrectiveActionInput"
              label="Suggested corrective action"
              placeholder="Suggested corrective action"
              className={classes.textField}
              style={{}}
              fullWidth
              value={(values && values.suggestedCorrectiveAction) || ''}
              onChange={handleChange('suggestedCorrectiveAction')}
              InputLabelProps={{
                shrink: true
              }}
            />
          </Box>
          <Box m={1}>
            <AttachmentsArea
              allowActions={true}
              isUploadingAttachment={false}
              attachments={values && values.attachments}
              origin={props.origin || ''}
              onAttachmentClicked={handleAttachmentClicked}
              onAttachmentUpload={handleProcessAttachmentUpload}
              onAttachmentDelete={handleAttachmentDeleteClicked}
              placeholderText="Add Attachment"
            />
          </Box>
        </Box>
      </DialogContent>
      <DialogActions>
        <Button onClick={handleClose} color="primary" variant="outlined" disabled={props.disabled}>
          Cancel
        </Button>
        <Button onClick={handleSubmitNotedExeption} color="primary" variant="outlined" disabled={props.disabled}>
          Ok
        </Button>
      </DialogActions>
    </Dialog>
  );
};
