import React from 'react';
import autobind from 'autobind-decorator';

import { withStyles, WithStyles, Theme, createStyles } from '@material-ui/core/styles';
import TableHead from '@material-ui/core/TableHead';
import TableSortLabel from '@material-ui/core/TableSortLabel';
import TableRow from '@material-ui/core/TableRow';
import TableCell from '@material-ui/core/TableCell';
import Tooltip from '@material-ui/core/Tooltip';

import headerColumns from './processTableHeaderColumns';
import { styles } from './styles';
import { grey } from '@material-ui/core/colors';

export default class ProcessTableHead extends React.PureComponent<ProcessTableHeadProps> {
  @autobind
  createSortHandler(property: any) {
    return (event: any) => {
      this.props.onRequestSort(event, property);
    };
  }

  render() {
    const { classes, showSelect, orderBy, order } = this.props;
    const CustomTableCell = withStyles((theme: Theme) =>
      createStyles({
        head: {
          backgroundColor: theme.palette.primary.dark,
          color: theme.palette.common.white,
          '&:hover': {
            backgroundColor: theme.palette.primary.light
          }
        }
      })
    )(TableCell);

    return (
      <TableHead>
        <TableRow>
          {!showSelect && (
            <CustomTableCell className={classes.tableCell} align="center">
              Select
            </CustomTableCell>
          )}
          {headerColumns.map(col => (
            <CustomTableCell key={col.id} className={classes.tableCell} align="center" padding={col.disablePadding ? 'none' : 'default'} sortDirection={orderBy === col.id ? order : false}>
              <Tooltip title={col.label} placement={col.numeric ? 'bottom-end' : 'bottom-start'} enterDelay={300}>
                <TableSortLabel active={orderBy === col.id} direction={order} onClick={this.createSortHandler(col.id)} style={orderBy === col.id ? { color: grey[300] } : {}}>
                  {col.label}
                </TableSortLabel>
              </Tooltip>
            </CustomTableCell>
          ))}
        </TableRow>
      </TableHead>
    );
  }
}

interface ProcessTableHeadProps extends WithStyles<typeof styles> {
  showSelect: boolean;
  numSelected: number;
  onRequestSort(event: any, property: any): void;
  order: any;
  orderBy: string;
  rowCount: number;
}
