import React from 'react';
import autobind from 'autobind-decorator';

import { withStyles, WithStyles } from '@material-ui/core/styles';
import LinearProgress from '@material-ui/core/LinearProgress';
import Dialog from '@material-ui/core/Dialog';
import DialogTitle from '@material-ui/core/DialogTitle';
import DialogContent from '@material-ui/core/DialogContent';
import DialogActions from '@material-ui/core/DialogActions';

import JSZip from 'jszip';

import { styles } from './styles';
import { AppContext } from '../../AppContext';
import { Process } from '../../ppp';
import { ProcessExporter } from '../../services/ProcessExporter';

class ExportProcessDialog extends React.Component<ExportProcessDialogProps, ExportProcessDialogState> {
  static contextType = AppContext;
  context!: React.ContextType<typeof AppContext>;

  constructor(props: ExportProcessDialogProps) {
    super(props);

    this.state = {
      currentStatus: 'Initializing...'
    };
  }

  @autobind
  async handleOnClose(zipFile?: JSZip) {
    if (this.props.onClose) {
      if (zipFile) {
        const result = await zipFile.generateAsync({
          type: 'blob'
        });

        this.props.onClose(result);
        return;
      } else {
        this.props.onClose();
      }
    }
  }

  @autobind
  async exportProcess() {
    const { origin, process } = this.props;
    let processUrl = await this.context.processStore.getProcessPathByProcessId(process.id);
    const zipFile = await ProcessExporter.exportProcess(process, processUrl, origin, newStatus => {
      this.setState({
        currentStatus: newStatus
      });
    });

    this.handleOnClose(zipFile);
  }

  async componentWillMount() {
    if (this.props.open) {
      await this.exportProcess();
    }
  }

  render() {
    const { currentStatus } = this.state;

    return (
      <Dialog open={this.props.open} onClose={() => this.handleOnClose()} aria-labelledby="export-process-dialog-title" fullWidth={true} maxWidth="xs" disableBackdropClick={true}>
        <DialogTitle id="export-process-dialog-title">Exporting Process...</DialogTitle>
        <DialogContent>
          <div>{currentStatus}</div>
          <LinearProgress />
        </DialogContent>
        <DialogActions />
      </Dialog>
    );
  }
}

export default withStyles(styles)(ExportProcessDialog);

interface ExportProcessDialogProps extends WithStyles<typeof styles> {
  open: boolean;
  origin: string;
  onClose?: (result?: Blob) => void;
  process: Process;
}

interface ExportProcessDialogState {
  currentStatus: string;
}
