import { BaristaService } from '../util/BaristaService';
import { ProcessLog, ProcessLogResults } from './ProcessLog';
import { ProcessLogEntry } from './ProcessLogEntry';

export class ProcessLogStore {
  private _baristaService: BaristaService;

  /**
   * Creates a new ProcessLogStore
   */
  constructor(baristaService: BaristaService) {
    this._baristaService = baristaService;
  }

  /**
   * Gets the log for the process with the corresponding id
   * @param processId The process id to retrieve the log.
   */
  public async getProcessLogByProcessId(processId: string): Promise<ProcessLog> {
    const response = await this._baristaService.get('ProcessLog/getProcessLogByProcessId.js', { params: { processId } });
    return response.data;
  }

  /**
   * Gets the log for the process with the corresponding id
   * @param processId The process id to retrieve the log.
   */
  public async getProcessLogs(date: Date, currentCacheId?: string): Promise<ProcessLogResults> {
    const response = await this._baristaService.get('ProcessLog/getProcessLogs.js', { params: { date, currentCacheId } });
    return response.data;
  }

  /**
   * Adds a new log entry to the process log with the corresponding id
   * @param processId id of the process to add the log entry
   * @param logEntry the log entry to add.
   */
  public async addLogEntryForProcess(processId: string, logEntry: ProcessLogEntry): Promise<ProcessLog> {
    const response = await this._baristaService.post('ProcessLog/addLogEntryForProcess.js', { data: { processId, logEntry } });
    return response.data;
  }
}
