import React from 'react';
import autobind from 'autobind-decorator';
import orderBy from 'lodash/orderBy';

import Dropzone from 'react-dropzone';

import Typography from '@material-ui/core/Typography';
import CircularProgress from '@material-ui/core/CircularProgress';
import Avatar from '@material-ui/core/Avatar';
import Chip from '@material-ui/core/Chip';
import UploadIcon from '@material-ui/icons/CloudUpload';

import { Attachment } from '../../ppp';
import { Process } from '../../ppp';
//import { Activity } from '../../ppp';

export default class ActivityAttachmentsArea extends React.PureComponent<AttachmentsAreaProps, AttachmentsAreaState> {
  @autobind
  handleAttachmentDrop(acceptedFiles: File[]) {
    if (!acceptedFiles || acceptedFiles.length !== 1) {
      if (acceptedFiles.length == 0) {
        alert('Please select one file at a time.');
      }
      return;
    }

    if (this.props.onAttachmentUpload) {
      let isInvalidFileName = this.isFileNameInvalid(acceptedFiles[0]);
      if (isInvalidFileName) {
        return;
      }
      this.props.onAttachmentUpload(acceptedFiles[0]);
    }
  }

  isFileNameInvalid(file: File) {
    if (this.props.attachments)
      for (var i = 0; i < this.props.attachments.length; i++) {
        let fileName = this.props.attachments[i].originalFileName ? this.props.attachments[i].originalFileName : this.props.attachments[i].fileName;
        if (fileName === file.name) {
          return true;
        }
      }

    /*let length100 = 100;
     let length53 = 53;
     let processTempName = 'Accounting and Financial Reporting';
     if (this.props.process) {
       if (this.props.process.functionArea === processTempName) {
         if (file.name.length > length100) {
           alert('Filename is too long'); // , max limit is ${length100} chars`);
           return true;
         }
       } else {
         if (file.name.length > length53) {
           alert(`Filename is too long, max limit is ${length53} chars`);
           return true;
         }
       }
     }*/

    let invalidFileExt = /\.(ade|adp|asa|exe|ashx|asp|cmd|db|tmp|config|dll|ini|aspx|svc|lnk)$/i.test(file.name);
    if (invalidFileExt) {
      alert('Invalid File');
      return invalidFileExt;
    }

    let regEx = /^(?!\.)(?!com[0-9]$)(?!con$)(?!lpt[0-9]$)(?!nul$)(?!prn$)[^\|\*\?\\:%&{}~#<>/"]*[^\.\|\*\?\\:<>/$"]+$/;
    let inValidChars = regEx.test(file.name);
    if (!inValidChars) {
      alert('Invalid Filename');
    }
    return !inValidChars;
  }

  @autobind
  handleAttachmentClicked(e: React.MouseEvent, process: Process, attachment: Attachment) {
    if (this.props.onAttachmentClicked) {
      e.stopPropagation();
      this.props.onAttachmentClicked(process, attachment);
    }
  }

  handleAnchorClick(e: any) {
    e.stopPropagation();
  }

  @autobind
  handleAttachmentDelete(attachment: Attachment) {
    if (this.props.onAttachmentDelete) {
      this.props.onAttachmentDelete(attachment);
    }
  }

  getAttachmentAreaStyle(isDragActive: boolean) {
    if (!isDragActive) {
      return {};
    }
    return {
      backgroundColor: '#eee'
    };
  }

  render() {
    const { classes, allowActions, isUploadingAttachment, attachments, origin, process } = this.props;
    return (
      <>
        {process && isUploadingAttachment && <CircularProgress variant="indeterminate" disableShrink className={classes.progressSpinner} size={24} thickness={4} />}
        {!isUploadingAttachment && (
          <>
            <Dropzone onDrop={this.handleAttachmentDrop} multiple={false} disabled={!allowActions}>
              {({ getRootProps, getInputProps, isDragActive }) => (
                <>
                  <Typography variant="caption" style={this.getAttachmentAreaStyle(isDragActive)}>
                    <div {...getRootProps()}>
                      <input {...getInputProps()} />
                      <UploadIcon style={{ cursor: 'pointer' }} className={classes.uploadIcon} />
                      <span style={{ cursor: 'pointer' }}>&nbsp;Attachments:</span>
                      <div>
                        {(!attachments || attachments.length <= 0) && (
                          <Typography variant="caption">
                            <i>No Attachments</i>
                          </Typography>
                        )}
                        {attachments &&
                          attachments.length > 0 &&
                          orderBy(attachments, ['fileName'], ['asc']).map(attachment => (
                            <div key={attachment.fileName}>
                              <Chip
                                classes={{ label: classes.attachmentChipLabel, deleteIcon: classes.attachmentChipDeleteIcon }}
                                className={classes.attachmentChip}
                                key={attachment.fileName}
                                avatar={<Avatar className={classes.attachmentAvatar} src={`${origin}${attachment.fileIconUrl || '/_layouts/images/ICGEN.GIF'}`} />}
                                label={attachment.originalFileName ? attachment.originalFileName : attachment.fileName}
                                color="secondary"
                                onClick={process ? e => this.handleAttachmentClicked(e, process, attachment) : undefined}
                                onDelete={allowActions ? () => this.handleAttachmentDelete(attachment) : undefined}
                              />
                            </div>
                          ))}
                      </div>
                    </div>
                  </Typography>
                </>
              )}
            </Dropzone>
          </>
        )}
      </>
    );
  }
}

interface AttachmentsAreaState {
  //process?: Process;
}
interface AttachmentsAreaProps {
  classes: {
    attachmentAvatar: string;
    attachmentChipLabel: string;
    attachmentChipDeleteIcon: string;
    attachmentChip: string;
    progressSpinner: string;
    uploadIcon: string;
  };
  allowActions: boolean;
  isUploadingAttachment: boolean;
  attachments?: Attachment[];
  origin: string;
  process?: Process;
  //activity: Activity,
  onAttachmentUpload?: (attachmentFile: File) => void;
  //onAttachmentClicked?: (attachment: Attachment) => void;
  onAttachmentClicked: (process: Process, attachment: Attachment) => void;
  onAttachmentDelete?: (attachment: Attachment) => void;
}
