import React from 'react';
import autobind from 'autobind-decorator';
import find from 'lodash/find';
import debounce from 'lodash/debounce';
import truncate from 'lodash/truncate';

import moment from 'moment';
import DOMPurify from 'dompurify';

import { withStyles, WithStyles } from '@material-ui/core/styles';
import ExpansionPanel from '@material-ui/core/ExpansionPanel';
import ExpansionPanelSummary from '@material-ui/core/ExpansionPanelSummary';
import ExpansionPanelDetails from '@material-ui/core/ExpansionPanelDetails';
import ExpansionPanelActions from '@material-ui/core/ExpansionPanelActions';
import Tooltip from '@material-ui/core/Tooltip';
import Typography from '@material-ui/core/Typography';

import ExpandMoreIcon from '@material-ui/icons/ExpandMore';
import HelpIcon from '@material-ui/icons/Help';
import ActionIcon from '@material-ui/icons/Error';
import DoneIcon from '@material-ui/icons/Done';
import DoneAllIcon from '@material-ui/icons/DoneAll';
//import NotRequiredIcon from '@material-ui/icons/NotInterested';

import { Activity, ActivityStatus, ProcessTemplate, Attachment, ActivityTemplate, Process } from '../../ppp';
import ResponsiblePartyArea from './ResponsiblePartyArea';
import ActivityActionsArea from './ActivityActionsArea';
import ActivityAttachmentsArea from './ActivityAttachmentsArea';
import ActivityCommentsArea from './ActivityCommentsArea';
import { styles } from './styles';
import ConfirmationDialog from '../dialogs/ConfirmationDialog';
import { Grid } from '@material-ui/core';
import AttachmentsDisplay from '../controls/AttachmentsDisplay';
import { AppContext } from '../../AppContext';
import ActivityNotRequiredComment from './ActivityNotRequiredComment';

class ActivityCard extends React.Component<ActivityCardProps, ActivityCardState> {
  static contextType = AppContext;
  context!: React.ContextType<typeof AppContext>;

  debouncedPerformCommentsChange: (comments: string) => Promise<void>;

  constructor(props: ActivityCardProps) {
    super(props);

    let activityTemplate = find(props.template.activities, { id: props.activity.id });

    if (!activityTemplate) {
      activityTemplate = {
        allowAttachments: false,
        dependsOnPreviousActivity: false,
        description: '',
        id: '',
        requireAttachments: false,
        requiresReview: false,
        responsibleParty: '',
        showTooltip: false,
        jobAidAttachments: []
      };
    }

    this.state = {
      isPerformingAction: false,
      isUpdatingResponsibleParty: false,
      isUpdatingComments: false,
      isUploadingAttachment: false,
      showInactivateWarningDialog: false,
      comments: props.activity.comments,
      activityTemplate,
      notRequiredComments: '',
      notRequiredDialogOpen: false
    };

    this.debouncedPerformCommentsChange = debounce(this.performCommentsChange, 1500);
  }

  @autobind
  async handleMarkAsCompleteClicked() {
    this.setState({
      isPerformingAction: true
    });

    await this.props.onMarkAsComplete(this.props.activity);

    this.setState({
      isPerformingAction: false
    });
  }

  @autobind
  async handleMarkAsIncompleteClicked() {
    const { shouldShowWarningIfStatusChanges } = this.props;
    if (shouldShowWarningIfStatusChanges) {
      this.setState({
        showInactivateWarningDialog: true
      });
    }

    this.setState({
      isPerformingAction: true
    });

    await this.props.onMarkAsIncomplete(this.props.activity);

    this.setState({
      isPerformingAction: false
    });
  }

  @autobind
  async handleMarkAsReviewedClicked() {
    this.setState({
      isPerformingAction: true
    });

    await this.props.onMarkAsReviewed(this.props.activity);

    this.setState({
      isPerformingAction: false
    });
  }

  @autobind
  async handleMarkAsNotRequiredClicked() {
    this.setState({
      notRequiredDialogOpen: true
    });
  }

  @autobind
  handleNRDClose() {
    this.setState({
      notRequiredDialogOpen: false
    });
  }

  @autobind
  async handleNRDSave() {
    this.setState({
      isPerformingAction: true
    });
    await this.setState((prevState, props) => {
      //return { comments: prevState.comments + '\n\n Activity Not Required Note: ' + prevState.notRequiredComments };
      return { notRequiredComments: prevState.notRequiredComments };
    });

    //await this.props.onActivityNotReqCommentsUpdated(this.props.activity, this.state.comments);
    await this.props.onMarkAsNotRequired(this.props.activity, this.state.notRequiredComments);

    this.setState({
      notRequiredDialogOpen: false,
      isPerformingAction: false
    });
  }

  @autobind
  handleAttachmentClicked(process: Process, attachment: Attachment) {
    let siteName = '/' + attachment.serverRelativeUrl.split('/')[1];
    var code = encodeURIComponent('/ofsppp/api/Process/downloadProcessActivityAttachment.js');
    var fileName = encodeURIComponent(attachment.fileName);
    let originalFileName = attachment.originalFileName ? attachment.originalFileName : attachment.fileName;
    originalFileName = encodeURIComponent(originalFileName);
    window.open(
      `${this.props.origin + siteName}/_vti_bin/barista/v1/barista.svc/eval?c=${code}&processId=${process.id}&activityId=${
        this.props.activity.id
      }&fileName=${fileName}&originalFileName=${originalFileName}`
    );
  }

  @autobind
  async handleAttachmentUpload(attachmentFile: File) {
    this.setState({
      isPerformingAction: true,
      isUploadingAttachment: true
    });

    await this.props.onAttachmentUpload(this.props.activity, attachmentFile);

    this.setState({
      isPerformingAction: false,
      isUploadingAttachment: false
    });
  }

  @autobind
  async handleAttachmentDelete(attachment: Attachment) {
    this.setState({
      isPerformingAction: true,
      isUploadingAttachment: true
    });

    await this.props.onAttachmentDelete(this.props.activity, attachment);

    this.setState({
      isPerformingAction: false,
      isUploadingAttachment: false
    });
  }

  @autobind
  async handleResponsiblePartyChange(newResponsibleParty: string) {
    this.setState({
      isUpdatingResponsibleParty: true,
      isPerformingAction: true
    });

    await this.props.onResponsiblePartyUpdated(this.props.activity, newResponsibleParty);

    this.setState({
      isUpdatingResponsibleParty: false,
      isPerformingAction: false
    });
  }

  @autobind
  async handleCommentsChange(newComments: string, now?: boolean) {
    this.setState({
      comments: newComments
    });

    if (now === true) {
      this.performCommentsChange(newComments);
    } else {
      /* istanbul ignore next */
      this.debouncedPerformCommentsChange(newComments);
    }
  }

  @autobind
  onNotRequiredCommentChange(newComment: string, now?: boolean) {
    this.setState({
      notRequiredComments: newComment
    });
  }

  @autobind
  async performCommentsChange(comments: string) {
    this.setState({
      isUpdatingComments: true
    });
    await this.props.onCommentsUpdated(this.props.activity, comments);
    this.setState({
      isUpdatingComments: false
    });
  }

  @autobind
  handleCloseInactivateWarningDialog() {
    this.setState({
      showInactivateWarningDialog: false
    });
  }

  getActivityPanelStyle(activity: Activity, activityDepth: number, expanded: boolean): React.CSSProperties {
    const result: React.CSSProperties = {
      marginLeft: `${activityDepth * 15}px`,
      pageBreakAfter: 'always'
    };
    let opacity = expanded ? 0.5 : 1;
    switch (activity.status) {
      case ActivityStatus.Incomplete:
        result.background = `RGB(252,225,229, ${opacity})`; //  '#FCE1E5';
        break;
      case ActivityStatus.NotRequired:
        result.background = `RGB(234,234,234, ${opacity})`; //  '#EAEAEA';'
        break;
      case ActivityStatus.Complete:
        result.background = `RGB(230,245,208, ${opacity})`; // '#e6f5d0';
        break;
      case ActivityStatus.CompleteRequiresReview:
        result.background = `RGB(245,243,208, ${opacity})`; // '#f5f3d0';
        break;
      default:
        result.background = 'white';
    }

    return result;
  }

  get allowActions(): boolean {
    if (!this.props.allowActions) {
      return false;
    }

    switch (this.props.activity.status) {
      case ActivityStatus.Complete:
      case ActivityStatus.CompleteRequiresReview:
      case ActivityStatus.NotRequired:
        return false;
      default:
        return true;
    }
  }

  get canCompleteActivity(): boolean {
    const { activity, isPreviousActivityCompleted } = this.props;
    const { activityTemplate } = this.state;
    if (activityTemplate.requireAttachments && (!activity.attachments || activity.attachments.length <= 0)) {
      return false;
    }

    if (activityTemplate.dependsOnPreviousActivity && !isPreviousActivityCompleted) {
      return false;
    }

    return true;
  }

  get activitySummaryText(): string {
    const { activityTemplate } = this.state;
    const text = new DOMParser().parseFromString(activityTemplate.description, 'text/html');
    if (text.body && text.body.textContent) {
      return truncate((text.body.textContent || '').trim(), { length: 50 });
    } else {
      return '[ Not Available ]';
    }
  }

  get activityActionTooltipText(): string {
    const { activity } = this.props;
    switch (activity.status) {
      case ActivityStatus.Incomplete:
        return 'This activity has yet to be completed.';
      case ActivityStatus.CompleteRequiresReview:
        return 'This activity has been completed, but requires secondary review.';
      default:
        return '';
    }
  }

  render() {
    const { index, classes, expanded, activity, activityDepth, responsibleParties, origin, jobAidAttachments, process } = this.props;
    const {
      notRequiredComments,
      notRequiredDialogOpen,
      activityTemplate,
      isPerformingAction,
      isUpdatingResponsibleParty,
      isUploadingAttachment,
      showInactivateWarningDialog,
      isUpdatingComments
    } = this.state;

    if (!activityTemplate.id) {
      return <div>Error. Activity Template not found.</div>;
    }

    if (activity.responsibleParty == '') {
      activity.responsibleParty = 'No Responsible Party';
    }
    const createMarkup = () => {
      return { __html: DOMPurify.sanitize(activityTemplate.description) };
    };

    return (
      <>
        <ExpansionPanel
          expanded={expanded}
          onChange={(e, ex) => this.props.onExpanded(activity, ex)}
          style={this.getActivityPanelStyle(activity, activityDepth, expanded)}
          classes={{ expanded: classes.cardSummaryExpanded }}
        >
          <ExpansionPanelSummary expandIcon={<ExpandMoreIcon />}>
            <Typography className={classes.activityCardHeading}>
              <span style={{ paddingRight: '15px' }}>
                {activity.status !== ActivityStatus.Complete && activity.status !== ActivityStatus.NotRequired && (
                  <Tooltip title={this.activityActionTooltipText}>
                    <ActionIcon style={{ fontSize: '15px' }} />
                  </Tooltip>
                )}

                {activity.completedBy && !activity.reviewedBy && (
                  <Tooltip
                    title={
                      <>
                        <span>
                          Completed on {moment(activity.completedOn).format('lll')} by {activity.completedBy.displayName}
                        </span>
                      </>
                    }
                  >
                    <DoneIcon style={{ fontSize: '15px' }} />
                  </Tooltip>
                )}
                {activity.completedBy && activity.reviewedBy && (
                  <Tooltip
                    title={
                      <>
                        <span>
                          Completed on {moment(activity.completedOn).format('lll')} by {activity.completedBy.displayName}
                        </span>
                        <br />
                        <span>
                          Reviewed on {moment(activity.reviewedOn).format('lll')} by {activity.reviewedBy.displayName}
                        </span>
                      </>
                    }
                  >
                    <DoneAllIcon style={{ fontSize: '15px' }} />
                  </Tooltip>
                )}
                {activityTemplate.tooltip && activityTemplate.tooltip.length > 0 && (
                  <Tooltip title={activityTemplate.tooltip || ''}>
                    <b>
                      Activity #{index + 1}&nbsp;{activityTemplate.tooltip ? <HelpIcon className={classes.helpIcon} /> : null}
                    </b>
                  </Tooltip>
                )}
                {!activityTemplate.tooltip && <b>Activity #{index + 1}&nbsp; </b>}
              </span>
              {!expanded && <span>{this.activitySummaryText}</span>}
              {!expanded && activity.responsibleParty !== '' && (
                <span style={{ paddingLeft: '40px' }}>
                  <b>Responsible Party :</b>&nbsp;{activity.responsibleParty}
                </span>
              )}
              <span className="printable">
                <br />
                {activity.completedBy && (
                  <span>
                    Completed On&nbsp;
                    <span className={classes.highlight}>{moment(activity.completedOn).format('lll')}</span> by&nbsp;
                    <span className={classes.highlight}>{activity.completedBy.displayName}</span>
                  </span>
                )}
                {activity.reviewedBy && (
                  <span>
                    Reviewed On&nbsp;
                    <span className={classes.highlight}>{moment(activity.reviewedOn).format('lll')}</span> by&nbsp;
                    <span className={classes.highlight}>{activity.reviewedBy.displayName}</span>
                  </span>
                )}
                {activity.status === ActivityStatus.NotRequired && (
                  <span style={{ paddingLeft: '5px' }}>
                    <i>Not Required</i>
                  </span>
                )}
              </span>
            </Typography>
          </ExpansionPanelSummary>
          <ExpansionPanelDetails>
            <Grid container spacing={0} style={{ margin: 0, padding: 0 }}>
              <Grid item xs={12}>
                <div className={classes.activityDescriptionColumn + ' rte'} dangerouslySetInnerHTML={createMarkup()} />
              </Grid>
              {jobAidAttachments && jobAidAttachments.length > 0 && <AttachmentsDisplay origin={origin} attachments={jobAidAttachments} labelText={'Job Aids'} />}
            </Grid>
            <div className={classes.activityAttachmentsColumn}>
              <ResponsiblePartyArea
                classes={classes}
                allowActions={this.allowActions}
                isUpdatingResponsibleParty={isUpdatingResponsibleParty}
                responsibleParty={activity.responsibleParty}
                availableResponsibleParties={responsibleParties}
                onResponsiblePartyChange={this.handleResponsiblePartyChange}
              />
              {activityTemplate.allowAttachments && (
                <ActivityAttachmentsArea
                  classes={classes}
                  allowActions={this.allowActions}
                  isUploadingAttachment={isUploadingAttachment}
                  attachments={activity.attachments}
                  origin={origin}
                  process={process}
                  onAttachmentClicked={this.handleAttachmentClicked}
                  onAttachmentUpload={this.handleAttachmentUpload}
                  onAttachmentDelete={this.handleAttachmentDelete}
                />
              )}
            </div>
          </ExpansionPanelDetails>
          <ExpansionPanelActions>
            <ActivityCommentsArea
              classes={classes}
              allowActions={this.allowActions}
              isUpdatingComments={isUpdatingComments}
              comments={this.state.comments}
              onCommentsChange={this.handleCommentsChange}
              expanded={expanded}
              activity={activity}
            />
            <div style={{ flexBasis: '30%' }}>
              <ActivityActionsArea
                classes={classes}
                isPerformingAction={isPerformingAction}
                activity={activity}
                allowActions={this.props.allowActions}
                canCompleteActivity={this.canCompleteActivity}
                onMarkAsComplete={this.handleMarkAsCompleteClicked}
                onMarkAsIncomplete={this.handleMarkAsIncompleteClicked}
                onMarkAsNotRequired={this.handleMarkAsNotRequiredClicked}
                onMarkAsReviewed={this.handleMarkAsReviewedClicked}
              />
              <ActivityNotRequiredComment
                notRequiredDialogOpen={notRequiredDialogOpen}
                handleNRDClose={this.handleNRDClose}
                handleNRDSave={this.handleNRDSave}
                notRequiredComments={notRequiredComments}
                onNotRequiredCommentChange={this.onNotRequiredCommentChange}
              />
            </div>
          </ExpansionPanelActions>
          <ExpansionPanelActions style={{ padding: '0' }}>
            {activityTemplate.requiresReview && (
              <Typography variant="caption" style={{ paddingRight: '15px' }}>
                Requires Secondary Review
              </Typography>
            )}
            {activityTemplate.dependsOnPreviousActivity && (
              <Typography variant="caption" style={{ paddingRight: '15px' }}>
                Depends on Previous Activity
              </Typography>
            )}
            {activityTemplate.requireAttachments && (
              <Typography variant="caption" style={{ paddingRight: '15px' }}>
                Attachments are required
              </Typography>
            )}
          </ExpansionPanelActions>
        </ExpansionPanel>
        <ConfirmationDialog open={showInactivateWarningDialog} title="Activity has a completed dependent child activity" onClose={this.handleCloseInactivateWarningDialog}>
          <>
            <p>The activity you have just marked as incomplete had a dependent child activity that was already completed.</p>
            <p>This is just a notification that you did this, and there's nothing wrong with doing so, it's just that you did it and here we are.</p>
            <p>Keep Calm and Carry On.</p>
          </>
        </ConfirmationDialog>
      </>
    );
  }
}

export default withStyles(styles)(ActivityCard);

interface ActivityCardProps extends WithStyles<typeof styles> {
  expanded: boolean;
  index: number;
  activity: Activity;
  activityDepth: number;
  template: ProcessTemplate;
  isPreviousActivityCompleted: boolean;
  shouldShowWarningIfStatusChanges: boolean;
  allowActions: boolean;
  origin: string;
  responsibleParties: { value: string; text: string }[];
  jobAidAttachments?: Attachment[];
  process: Process;
  onExpanded: (activity: Activity, expanded: boolean) => Promise<void>;
  onMarkAsComplete: (activity: Activity) => Promise<void>;
  onMarkAsIncomplete: (activity: Activity) => Promise<void>;
  onMarkAsNotRequired: (activity: Activity, comments: string) => Promise<void>;
  onMarkAsReviewed: (activity: Activity) => Promise<void>;
  onAttachmentUpload: (activity: Activity, file: File) => Promise<void>;
  onAttachmentDelete: (activity: Activity, attachment: Attachment) => Promise<void>;
  onResponsiblePartyUpdated: (activity: Activity, responsibleParty: string) => Promise<void>;
  onCommentsUpdated: (activity: Activity, newComment: string) => Promise<void>;
  //onActivityNotReqCommentsUpdated: (activity: Activity, newComment: string) => Promise<void>;
}

interface ActivityCardState {
  isPerformingAction: boolean;
  isUpdatingComments: boolean;
  isUpdatingResponsibleParty: boolean;
  isUploadingAttachment: boolean;
  showInactivateWarningDialog: boolean;
  comments: string;
  activityTemplate: ActivityTemplate;
  notRequiredComments: string;
  notRequiredDialogOpen: boolean;
}
