import React, { FunctionComponent } from 'react';
import { makeStyles, Theme, createStyles } from '@material-ui/core/styles';
import { Radio, RadioGroup, FormControlLabel, FormControl, Typography, Box } from '@material-ui/core';

const useStyles = makeStyles((theme: Theme) =>
  createStyles({
    root: {
      width: '100%',
      padding: theme.spacing(0.5)
    }
  })
);

type QuestionProps = {
  questionId: string;
  questionText: string;
  questionAnswer?: string;
  onAnswerChange(answer: string): void;
  disabled: boolean;
};

export const Question: FunctionComponent<QuestionProps> = ({ questionId, questionText, questionAnswer, onAnswerChange, disabled }) => {
  const classes = useStyles();
  const [answer, setAnswer] = React.useState<string | undefined>(questionAnswer);

  React.useEffect(
    () => {
      setAnswer(questionAnswer);
    },
    [questionAnswer]
  );

  const handleChange = (event: React.ChangeEvent<{}>) => {
    setAnswer((event.target as HTMLInputElement).value);
    onAnswerChange((event.target as HTMLInputElement).value);
  };

  return (
    <Box display="flex" className={classes.root} justifyContent="flex-start">
      <Box p={0} bgcolor="grey.100">
        <FormControl component="fieldset" style={{ minWidth: 150 }}>
          <RadioGroup value={answer} onChange={handleChange} row>
            <FormControlLabel disabled={disabled} value="Yes" control={<Radio color="primary" />} label="Yes" labelPlacement="end" />
            <FormControlLabel disabled={disabled} value="No" control={<Radio color="primary" />} label="No" labelPlacement="end" />
          </RadioGroup>
        </FormControl>
      </Box>
      <Box p={1} bgcolor="grey.100">
        <Typography component="p">{questionText} </Typography>
      </Box>
    </Box>
  );
};
