import React from 'react';
import { Theme, createStyles, withStyles, WithStyles } from '@material-ui/core/styles';
import Typography from '@material-ui/core/Typography';
import Grid from '@material-ui/core/Grid';
import axios from 'axios';

const styles = (theme: Theme) =>
  createStyles({
    footer: {
      marginTop: theme.spacing() * 8,
      borderTop: `1px solid ${theme.palette.divider}`,
      padding: `${theme.spacing() * 6}px 0`
    },
    layout: {
      width: 'auto',
      marginLeft: theme.spacing() * 1,
      marginRight: theme.spacing() * 1,
      [theme.breakpoints.up(1000 + theme.spacing() * 3 * 2)]: {
        width: 1100,
        marginLeft: 'auto',
        marginRight: 'auto'
      }
    }
  });

class Footer extends React.Component<WithStyles, FooterState> {
  private _isMounted: boolean = false;
  constructor(props: WithStyles) {
    super(props);
    this.state = {
      releaseName: ''
    };
  }

  async componentWillMount() {
    this._isMounted = true;
    try {
      const response = await axios.get('./release.txt');
      if (this._isMounted === true) {
        this.setState({
          releaseName: response.data
        });
      }
    } catch (err) {
      if (this._isMounted === true) {
        this.setState({
          releaseName: 'Unknown'
        });
      }
    }
  }

  public componentWillUnmount() {
    this._isMounted = false;
  }

  render() {
    const { classes } = this.props;
    const { releaseName } = this.state;
    return (
      <footer className={classes.footer + ' ' + classes.layout}>
        <Grid container spacing={6} justify="flex-end">
          <Grid item>
            <Typography variant="caption">{releaseName}</Typography>
          </Grid>
        </Grid>
      </footer>
    );
  }
}
export default withStyles(styles)(Footer);

interface FooterState {
  releaseName: string;
}
