import React from 'react';
import { sp } from '@pnp/sp';
import { SPRestContext, SPContext } from './services/SPContext';
import { AppRestService, AppService } from './services/AppService';
import { ProcessTemplateStore } from './ppp/ProcessTemplateStore';
import { BaristaService } from './util/BaristaService';
import { ProcessStore, ProcessLogStore, ProcessTemplateContainerStore, ReportingStore } from './ppp';
import { PersonalizationStore } from './ppp/PersonalizationStore';

let webRelativeUrl = '';
/* istanbul ignore next */
if (process.env.NODE_ENV === 'production') {
  webRelativeUrl = '../';
}

sp.setup({
  sp: {
    baseUrl: '../',
    headers: {
      Accept: 'application/json;odata=verbose'
    }
  }
});

const baristaService = new BaristaService(webRelativeUrl, '/ofsppp/api/');

export interface AppStore {
  appService: AppService;
  baristaService: BaristaService;
  spContext: SPContext;
  personalizationStore: PersonalizationStore;
  processStore: ProcessStore;

  processTemplateStore: ProcessTemplateStore;
  processTemplateContainerStore: ProcessTemplateContainerStore;
  processLogStore: ProcessLogStore;
  reportingStore: ReportingStore;
}

const spContext = new SPRestContext();
const processTemplateStore = new ProcessTemplateStore(baristaService);
const personalizationStore = new PersonalizationStore(baristaService, sessionStorage);

export const defaultAppStore: AppStore = {
  appService: new AppRestService(),
  spContext,
  baristaService,
  personalizationStore: personalizationStore,
  processStore: new ProcessStore(baristaService),
  processTemplateStore,
  processTemplateContainerStore: new ProcessTemplateContainerStore(processTemplateStore),
  processLogStore: new ProcessLogStore(baristaService),
  reportingStore: new ReportingStore(baristaService)
};

export const AppContext = React.createContext(defaultAppStore);
