import React, { FunctionComponent } from 'react';
import { makeStyles, Theme, createStyles } from '@material-ui/core/styles';
import {
  ExpansionPanel,
  ExpansionPanelDetails,
  ExpansionPanelSummary,
  Typography,
  Grid,
  Box,
  Card,
  CardHeader,
  CardContent,
  CircularProgress,
  Button,
  Checkbox,
  FormControlLabel
} from '@material-ui/core';
import Fab from '@material-ui/core/Fab';
import ExpandMoreIcon from '@material-ui/icons/ExpandMore';
import { Link as DomLink, useParams } from 'react-router-dom';
import OpenInNewOutlinedIcon from '@material-ui/icons/OpenInNewOutlined';
import { Attachment } from '../../../ppp';
import { Question } from './Question';
import RichEditorEditor from '../../controls/RichEditorEditor';
import { NotedException } from '../../../ppp/NotedException';
import { NotedExceptionDialog } from './NotedExceptionDialog';
import AddIcon from '@material-ui/icons/Add';
import { NotedExceptionDisplay } from './NotedExceptionDisplay';
import { getGUID } from '@pnp/common';
import moment from 'moment';
import debounce from 'lodash/debounce';
import { MonitoringAndReviewStatus } from '../../../ppp/MonitoringAndReviewStatus';
import DOMPurify from 'dompurify';
import { MonitoringAndReview } from '../../../ppp/MonitoringAndReview';

const useStyles = makeStyles((theme: Theme) =>
  createStyles({
    root: {
      width: '100%'
    },
    heading: {
      flexBasis: '80.33%',
      flexShrink: 0
    },
    panelSummary: {
      backgroundColor: theme.palette.grey[200]
    },
    extendedIcon: {
      marginRight: theme.spacing(1)
    },
    margin: {
      margin: theme.spacing(1)
    },
    OpenInNewStyle: {
      color: theme.palette.primary.dark,
      marginLeft: 'auto'
    },
    parentOverlay: {
      position: 'relative',
      flexBasis: '70%'
    },
    overlay: {
      position: 'absolute',
      top: -30,
      left: 5,
      'z-index': 10
    }
  })
);

type MonitorAndReviewProps = {
  monitoringAndReview: MonitoringAndReview;
  onEditQuestions(item: MonitoringAndReview): void;
  onNotesChange(rawText: string): void;
  onEditNotedExceptions(notedExceptions: NotedException[] | undefined): void;
  onMonitoringAndReviewComplete(): void;
  onUpdateMonitoringAndReviewProcessOwner(notedExceptionId: string, value: string): void;
  onNotedExceptiononAttachmentUpload(notedException: NotedException, attachmentFile: File): Promise<Attachment | undefined>;
  origin?: string;
};

export const MonitoringAndReviewComponent: FunctionComponent<MonitorAndReviewProps> = ({
  monitoringAndReview,
  origin,
  onEditQuestions,
  onEditNotedExceptions,
  onNotesChange,
  onMonitoringAndReviewComplete,
  onUpdateMonitoringAndReviewProcessOwner,
  onNotedExceptiononAttachmentUpload
}) => {
  const classes = useStyles();
  const match = useParams() as { id: string; mr?: string };
  const debounceWait = 1350;
  const expandPanelInitially = match && match.mr === 'true' ? 'panel1' : false;
  const [doAutoScroll, setDoAutoScroll] = React.useState<boolean>(match && match.mr === 'true');
  const [expanded, setExpanded] = React.useState<string | false>(expandPanelInitially);
  const [open, setOpen] = React.useState(false);
  const [acknowledgeComplete, setAcknowledgeComplete] = React.useState(false);
  const [isSaving, setIsSaving] = React.useState<boolean>(false);
  const [currenNotedException, setCurrenNotedException] = React.useState<NotedException>({ id: '' });
  const [isUpdatingNotes, setIsUpdatingNotes] = React.useState<boolean>(false);

  const scrollToElement = (id: string) => {
    const ele = document.getElementById(id);
    if (ele) {
      const clientRects = ele.getClientRects();
      /* istanbul ignore next */
      const top = clientRects[0] ? clientRects[0].top : 0;
      /* istanbul ignore next */
      // @ts-ignore
      const isIE = /*@cc_on!@*/ false || !!document.documentMode;
      if (isIE) {
        document.documentElement.scrollTop = top;
      } else {
        window.scrollTo(0, top);
      }
    }
  };

  React.useEffect(() => {
    if (scrollToElement && expandPanelInitially && doAutoScroll) {
      setDoAutoScroll(false);
      scrollToElement('MonitoringAndReviewComponentView');
    }
  }, []);

  const handleChange = (panel: string) => (event: React.ChangeEvent<{}>, isExpanded: boolean) => {
    setExpanded(isExpanded ? panel : false);
  };

  const handleNotesChange = async (rawText: string) => {
    if (monitoringAndReview) {
      setIsSaving(true);
      setIsUpdatingNotes(true);
      await onNotesChange(rawText);
      setIsUpdatingNotes(false);
      setIsSaving(false);
    }
  };

  const handleAddNotedExceptionClickOpen = () => {
    setOpen(true);
    setCurrenNotedException({
      id: getGUID()
    });
  };

  const handleEditNotedExceptionClickOpen = (id: string) => {
    setOpen(true);
    if (monitoringAndReview && monitoringAndReview.notedExceptions) {
      const list = monitoringAndReview.notedExceptions;
      let item = list.find(i => i.id === id);
      if (item) {
        setCurrenNotedException(item);
      }
    }
  };

  const isMonitoringAndReviewCompleted = () => {
    if (monitoringAndReview && monitoringAndReview.status === MonitoringAndReviewStatus.Completed) {
      return true;
    }
    return false;
  };

  const handleDeleteNotedException = async (id: string) => {
    if (monitoringAndReview && monitoringAndReview.notedExceptions) {
      const foundIndex = monitoringAndReview.notedExceptions.findIndex(x => x.id === id);
      if (foundIndex >= 0) {
        monitoringAndReview.notedExceptions.splice(foundIndex, 1);
        setIsSaving(true);
        await onEditNotedExceptions(monitoringAndReview.notedExceptions);
        setIsSaving(false);
      }
    }
  };

  const handleSubmitNotedException = async (notedException: NotedException) => {
    setIsSaving(true);
    if (monitoringAndReview) {
      if (monitoringAndReview.notedExceptions) {
        const foundIndex = monitoringAndReview.notedExceptions.findIndex(x => x.id === notedException.id);
        if (foundIndex >= 0) {
          monitoringAndReview.notedExceptions[foundIndex] = notedException;
        } else {
          monitoringAndReview.notedExceptions = [...monitoringAndReview.notedExceptions, notedException];
        }
      } else {
        monitoringAndReview.notedExceptions = [notedException];
      }
    } else {
      monitoringAndReview = { status: MonitoringAndReviewStatus.NotStarted, notedExceptions: [notedException] };
    }
    await onEditNotedExceptions(monitoringAndReview.notedExceptions);
    setIsSaving(false);
    setOpen(false);
  };

  const handleNotedExceptionClose = () => {
    setOpen(false);
  };

  const handleQuestionAnswer = async (question: string, answer: string) => {
    switch (question) {
      case 'q1':
        monitoringAndReview.q1 = answer;
        break;
      case 'q2':
        monitoringAndReview.q2 = answer;
        break;
      case 'q3':
        monitoringAndReview.q3 = answer;
        break;
      case 'q4':
        monitoringAndReview.q4 = answer;
        break;
      default:
    }
    doEditQuestion(monitoringAndReview);
  };

  const doEditQuestion = debounce(async (monitorngAndReview: MonitoringAndReview) => {
    setIsSaving(true);
    await onEditQuestions(monitorngAndReview);
    setIsSaving(false);
  }, debounceWait);

  const isQ4ValidationActive = () => {
    if (monitoringAndReview) {
      return monitoringAndReview.q1 === 'No' || monitoringAndReview.q2 === 'No' || monitoringAndReview.q3 === 'No';
    }
    return false;
  };

  const handleAcknowledmentChange = (event: React.ChangeEvent<{}>) => {
    setAcknowledgeComplete((event.target as HTMLInputElement).checked);
  };

  const handleMonitoringAndReviewCompleteClick = async () => {
    setIsSaving(true);
    setTimeout(async () => {
      await onMonitoringAndReviewComplete();
      setIsSaving(false);
    }, 3500);
  };

  const handleEditProcessOwner = async (id: string, value: string) => {
    if (monitoringAndReview) {
      if (monitoringAndReview.notedExceptions) {
        const foundIndex = monitoringAndReview.notedExceptions.findIndex(x => x.id === id);
        if (foundIndex >= 0) {
          monitoringAndReview.notedExceptions[foundIndex].processOwnerResponse = value;
          setIsSaving(true);
          await onUpdateMonitoringAndReviewProcessOwner(id, value);
          setIsSaving(false);
        }
      }
    }
  };

  const handleNotedExceptiononAttachmentUpload = async (notedException: NotedException, attachmentFile: File): Promise<Attachment | undefined> => {
    setIsSaving(true);
    const ret = await onNotedExceptiononAttachmentUpload(notedException, attachmentFile);
    setIsSaving(false);
    return ret;
  };

  const createMarkup = (value?: string) => {
    return { __html: DOMPurify.sanitize(value || '<p></p>') };
  };

  const getCommentArea = () => {
    if (monitoringAndReview && isMonitoringAndReviewCompleted()) {
      return <div className={'rte'} dangerouslySetInnerHTML={createMarkup(monitoringAndReview.generalNotes || '')} />;
    } else {
      return (
        <div className={classes.parentOverlay}>
          {isUpdatingNotes && <CircularProgress variant="indeterminate" disableShrink className={classes.overlay} size={24} thickness={4} />}
          <RichEditorEditor
            readOnly={false}
            placeholder="General Notes ..."
            rawText={(monitoringAndReview && monitoringAndReview.generalNotes) || ''}
            onChange={handleNotesChange}
            waitForDebounce={debounceWait}
          />
        </div>
      );
    }
  };

  return (
    <div className={classes.root}>
      <ExpansionPanel expanded={expanded === 'panel1'} onChange={handleChange('panel1')}>
        <ExpansionPanelSummary className={classes.panelSummary} expandIcon={<ExpandMoreIcon />} aria-controls="panel1bh-content" id="panel1bh-header">
          <Typography className={classes.heading} variant="h5">
            Monitoring and Review
          </Typography>
          <DomLink className={classes.OpenInNewStyle} to={`/process/${match.id}/true`} target="_blank">
            <OpenInNewOutlinedIcon style={{ verticalAlign: 'middle', paddingRight: 5, fontSize: '2em' }} />
          </DomLink>
        </ExpansionPanelSummary>
        <ExpansionPanelDetails>
          <Grid container>
            {monitoringAndReview && (
              <Grid item xs={12}>
                <Question
                  disabled={isSaving || isMonitoringAndReviewCompleted()}
                  questionId="q1"
                  onAnswerChange={answer => handleQuestionAnswer('q1', answer)}
                  questionText="Verify the activities in the checklist were reviewed by the appropriate personnel(s)."
                  questionAnswer={monitoringAndReview.q1}
                />
                <Question
                  disabled={isSaving || isMonitoringAndReviewCompleted()}
                  questionId="q2"
                  onAnswerChange={answer => handleQuestionAnswer('q2', answer)}
                  questionText="Verify the checklist is completed in a sufficiently timely manner. 
                  And verify each activity in the checklist was completed properly by inspecting the attachments/evidences, if available, and as needed."
                  questionAnswer={monitoringAndReview.q2}
                />
                <Question
                  disabled={isSaving || isMonitoringAndReviewCompleted()}
                  questionId="q3"
                  onAnswerChange={answer => handleQuestionAnswer('q3', answer)}
                  questionText="Verify the final review signature of the checklist was signed by the appropriate personnel."
                  questionAnswer={monitoringAndReview.q3}
                />
                <Question
                  disabled={!isQ4ValidationActive() || isSaving || isMonitoringAndReviewCompleted()}
                  questionId="q4"
                  onAnswerChange={answer => handleQuestionAnswer('q4', answer)}
                  questionText={`If the checklist fails any of the first three steps above, see exceptions.`}
                  questionAnswer={monitoringAndReview.q4}
                />
              </Grid>
            )}
            <Grid item xs={12}>
              <Grid container>
                <Grid item xs={6}>
                  <Typography variant="h6">Noted Exceptions (and responses)</Typography>
                </Grid>
                <Grid item xs={6}>
                  {isMonitoringAndReviewCompleted() === false && (
                    <Fab disabled={isSaving} variant="extended" size="small" color="primary" aria-label="add" className={classes.margin} onClick={handleAddNotedExceptionClickOpen}>
                      <AddIcon className={classes.extendedIcon} />
                      <Box fontSize="small">Add Noted Exception</Box>
                    </Fab>
                  )}
                </Grid>
              </Grid>
            </Grid>
            {monitoringAndReview &&
              monitoringAndReview.notedExceptions &&
              monitoringAndReview.notedExceptions.map((n, index) => (
                <Grid item xs={12} key={index}>
                  <NotedExceptionDisplay
                    key={index}
                    disabled={isSaving || isMonitoringAndReviewCompleted()}
                    origin={origin}
                    notedException={n}
                    onEditNotedExceptionClickOpen={handleEditNotedExceptionClickOpen}
                    onDeleteNotedException={handleDeleteNotedException}
                    onEditProcessOwner={handleEditProcessOwner}
                  />
                </Grid>
              ))}
            <Grid item xs={12}>
              <Grid container direction="row" spacing={1} alignItems="stretch" style={{ paddingTop: '10px' }}>
                <Grid item xs={6}>
                  <Card style={{ height: '100%' }}>
                    <CardHeader title="General Notes" />
                    <CardContent>{getCommentArea()}</CardContent>
                  </Card>
                </Grid>
                <Grid item xs={6}>
                  <Card style={{ height: '100%' }}>
                    <CardHeader title="Monitoring and Review Signoff" />
                    <CardContent>
                      <div className={classes.parentOverlay}>
                        {isSaving && <CircularProgress variant="indeterminate" disableShrink className={classes.overlay} size={24} thickness={4} />}
                        {monitoringAndReview && isMonitoringAndReviewCompleted() && (
                          <Box component="div" m={1}>
                            <Typography className={classes.heading} variant="h6">
                              Monitoring and Review Completed
                            </Typography>
                            <Box>
                              Completed&nbsp;
                              <Box component="span" fontWeight="fontWeightMedium" fontStyle="italic">
                                {monitoringAndReview.signOff && moment(monitoringAndReview.signOff.timeStamp).format('lll')}
                              </Box>
                              &nbsp; by&nbsp;
                              <Box component="span" fontWeight="fontWeightMedium" fontStyle="italic">
                                {monitoringAndReview.signOff && monitoringAndReview.signOff.signer && monitoringAndReview.signOff.signer.displayName}
                              </Box>
                            </Box>
                          </Box>
                        )}
                        {monitoringAndReview && isMonitoringAndReviewCompleted() === false && (
                          <>
                            <Box component="div" m={1}>
                              <FormControlLabel
                                disabled={isSaving}
                                control={<Checkbox checked={acknowledgeComplete} onChange={handleAcknowledmentChange} value={acknowledgeComplete} color="primary" />}
                                label="I acknowledge that I have reviewed this process and
                              confirm that it has been completed correctly."
                              />
                            </Box>
                            <Box component="div" m={1}>
                              <Button size="large" color="primary" onClick={handleMonitoringAndReviewCompleteClick} disabled={isSaving || !acknowledgeComplete}>
                                Monitoring and Review Complete
                              </Button>
                            </Box>
                          </>
                        )}
                      </div>
                    </CardContent>
                  </Card>
                </Grid>
              </Grid>
            </Grid>
          </Grid>
        </ExpansionPanelDetails>
      </ExpansionPanel>
      <NotedExceptionDialog
        onClose={handleNotedExceptionClose}
        origin={origin}
        open={open}
        disabled={isSaving}
        notedException={currenNotedException}
        onSubmitNotedException={handleSubmitNotedException}
        onAttachmentUpload={handleNotedExceptiononAttachmentUpload}
      />
    </div>
  );
};
