import React from 'react';
import moment from 'moment';
import { Theme, createStyles, withStyles, WithStyles } from '@material-ui/core/styles';
import Grid from '@material-ui/core/Grid';
import Paper from '@material-ui/core/Paper';
import TextField from '@material-ui/core/TextField';
import AttachmentsArea from '../controls/AttachmentArea';
import { ProcessTemplate, Attachment } from '../../ppp';

const styles = (theme: Theme) =>
  createStyles({
    root: {
      flexGrow: 1
    },
    paper: {
      padding: '5px',
      textAlign: 'center',
      color: theme.palette.text.secondary
    },
    formControl: {
      margin: theme.spacing(),
      width: '100%'
    }
  });

class ProcessHeader extends React.Component<TemplateHeaderProps> {
  constructor(props: TemplateHeaderProps) {
    super(props);
  }

  render() {
    const { classes, data, path, isUploadingAttachment, origin } = this.props;
    return (
      <div className={classes.root}>
        <Paper className={classes.paper}>
          <Grid container className={classes.container} spacing={6}>
            <Grid item xs={9}>
              <TextField fullWidth label="Path" className={classes.textField} value={path} InputProps={{ readOnly: true }} />
            </Grid>
            <Grid item xs={3}>
              <TextField
                fullWidth
                label="Version"
                className={classes.textField}
                value={`${data.status ? data!.status!.toString() : 'N/A'}, ${data.version ? data!.version : '0'}`}
                InputProps={{ readOnly: true }}
              />
            </Grid>
            <Grid item xs={9}>
              <TextField fullWidth label="Created By" className={classes.textField} value={data.createdBy ? data!.createdBy!.displayName : ''} InputProps={{ readOnly: true }} />
            </Grid>
            <Grid item xs={3}>
              <TextField fullWidth label="Created Date" className={classes.textField} value={data.created ? moment(data!.created).format('lll') : ''} InputProps={{ readOnly: true }} />
            </Grid>
            <Grid item xs={12}>
              <Grid container spacing={0} justify={'flex-start'}>
                <AttachmentsArea
                  allowActions={true}
                  isUploadingAttachment={isUploadingAttachment}
                  attachments={this.props.data.jobAidAttachments}
                  origin={origin}
                  onAttachmentClicked={this.props.onAttachmentClicked}
                  onAttachmentUpload={this.props.onProcessAttachmentUpload}
                  onAttachmentDelete={this.props.onProcessAttachmentDelete}
                />
              </Grid>
            </Grid>
          </Grid>
        </Paper>
      </div>
    );
  }
}

export default withStyles(styles)(ProcessHeader);

interface TemplateHeaderProps extends WithStyles {
  data: ProcessTemplate;
  id: string;
  path: string;
  isUploadingAttachment: boolean;
  origin: string;
  onAttachmentClicked(attachment: Attachment): void;
  onProcessAttachmentUpload(attachmentFile: File): Promise<void>;
  onProcessAttachmentDelete(attachment: Attachment): Promise<void>;
}
